// frontend/src/pages/Organisation/TurnierEinstellungen.jsx
// TURNIER-EINSTELLUNGEN - Wertungssysteme konfigurieren (Japanese Style)

import React, { useState, useEffect } from 'react';
import './TurnierEinstellungen.css';

function TurnierEinstellungen() {
  const [einstellungen, setEinstellungen] = useState([]);
  const [turniere, setTurniere] = useState([]);
  const [selectedTurnier, setSelectedTurnier] = useState(null);
  const [turnierEinstellungen, setTurnierEinstellungen] = useState([]);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [activeTab, setActiveTab] = useState('global'); // 'global', 'turnier', 'zeitplan'
  const [editingItem, setEditingItem] = useState(null);

  // Zeitplan-Einstellungen
  const [zeitplanSettings, setZeitplanSettings] = useState({
    rings: 2,
    start_time: '09:00',
    pause_between_categories: 5,
    disciplines: {
      'Formen': { durationPerPerson: 3, breakAfter: 1 },
      'Selbstverteidigung': { durationPerPerson: 4, breakAfter: 1 },
      'Kickboxen': { roundDuration: 2, rounds: 2, breakBetweenRounds: 1, breakAfter: 2 },
      'Freefight': { roundDuration: 2, rounds: 2, breakBetweenRounds: 1, breakAfter: 2 },
      'Pointfighting': { roundDuration: 2, rounds: 2, breakBetweenRounds: 1, breakAfter: 2 },
      'Default': { durationPerMatch: 5, breakAfter: 2 }
    }
  });
  const [zeitplanSource, setZeitplanSource] = useState('hardcoded');

  useEffect(() => {
    fetchData();
    fetchZeitplanSettings();
  }, []);

  useEffect(() => {
    if (selectedTurnier) {
      fetchTurnierEinstellungen(selectedTurnier.turnier_id);
      fetchZeitplanSettings(selectedTurnier.turnier_id);
    } else {
      fetchZeitplanSettings();
    }
  }, [selectedTurnier]);

  const fetchData = async () => {
    try {
      const token = localStorage.getItem('token');

      // Globale Einstellungen laden
      const einstellungenRes = await fetch('/api/wertung/einstellungen', {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const einstellungenData = await einstellungenRes.json();
      if (einstellungenData.success) {
        setEinstellungen(einstellungenData.data);
      }

      // Turniere laden
      const turniereRes = await fetch('/api/turniere', {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const turniereData = await turniereRes.json();
      if (turniereData.data) {
        setTurniere(turniereData.data);
      }

      setLoading(false);
    } catch (err) {
      console.error('Fehler beim Laden:', err);
      setLoading(false);
    }
  };

  const fetchTurnierEinstellungen = async (turnierId) => {
    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/wertung/turnier/${turnierId}`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (data.success) {
        setTurnierEinstellungen(data.data);
      }
    } catch (err) {
      console.error('Fehler beim Laden der Turnier-Einstellungen:', err);
    }
  };

  const fetchZeitplanSettings = async (turnierId = null) => {
    try {
      const token = localStorage.getItem('token');
      const url = turnierId
        ? `/api/schedule/zeitplan-einstellungen/${turnierId}`
        : '/api/schedule/zeitplan-einstellungen/default';

      const res = await fetch(url, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();

      if (data.success && data.data) {
        setZeitplanSettings({
          rings: data.data.rings || 2,
          start_time: data.data.start_time || '09:00',
          pause_between_categories: data.data.pause_between_categories || 5,
          disciplines: data.data.disciplines || {}
        });
        setZeitplanSource(data.source || 'loaded');
      }
    } catch (err) {
      console.error('Fehler beim Laden der Zeitplan-Einstellungen:', err);
    }
  };

  const handleSaveZeitplanSettings = async (isGlobal = false) => {
    setSaving(true);
    try {
      const token = localStorage.getItem('token');
      const url = isGlobal
        ? '/api/schedule/zeitplan-einstellungen/default'
        : `/api/schedule/zeitplan-einstellungen/${selectedTurnier?.turnier_id}`;

      const res = await fetch(url, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(zeitplanSettings)
      });

      const data = await res.json();
      if (data.success) {
        alert(isGlobal ? 'Standard-Einstellungen gespeichert!' : 'Turnier-Einstellungen gespeichert!');
      } else {
        alert('Fehler: ' + data.error);
      }
    } catch (err) {
      alert('Fehler beim Speichern: ' + err.message);
    } finally {
      setSaving(false);
    }
  };

  const handleResetZeitplanToDefault = async () => {
    if (!selectedTurnier) return;
    if (!window.confirm('Zeitplan-Einstellungen auf Standard zurcksetzen?')) return;

    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/schedule/zeitplan-einstellungen/${selectedTurnier.turnier_id}`, {
        method: 'DELETE',
        headers: { 'Authorization': `Bearer ${token}` }
      });

      const data = await res.json();
      if (data.success) {
        await fetchZeitplanSettings();
        alert('Auf Standard zurckgesetzt!');
      }
    } catch (err) {
      alert('Fehler: ' + err.message);
    }
  };

  const updateDiscipline = (name, field, value) => {
    setZeitplanSettings(prev => ({
      ...prev,
      disciplines: {
        ...prev.disciplines,
        [name]: {
          ...prev.disciplines[name],
          [field]: parseInt(value) || 0
        }
      }
    }));
  };

  const handleSaveGlobal = async (item) => {
    setSaving(true);
    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/wertung/einstellungen/${encodeURIComponent(item.division_type)}`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(item)
      });
      const data = await res.json();
      if (data.success) {
        await fetchData();
        setEditingItem(null);
        alert('Einstellung gespeichert!');
      } else {
        alert('Fehler: ' + data.message);
      }
    } catch (err) {
      alert('Fehler beim Speichern: ' + err.message);
    } finally {
      setSaving(false);
    }
  };

  const handleSaveTurnier = async (item) => {
    if (!selectedTurnier) return;

    setSaving(true);
    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/wertung/turnier/${selectedTurnier.turnier_id}/${encodeURIComponent(item.division_type)}`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(item)
      });
      const data = await res.json();
      if (data.success) {
        await fetchTurnierEinstellungen(selectedTurnier.turnier_id);
        setEditingItem(null);
        alert('Turnier-Einstellung gespeichert!');
      } else {
        alert('Fehler: ' + data.message);
      }
    } catch (err) {
      alert('Fehler beim Speichern: ' + err.message);
    } finally {
      setSaving(false);
    }
  };

  const handleResetToDefault = async (divisionType) => {
    if (!selectedTurnier) return;
    if (!window.confirm(`"${divisionType}" auf Standard zurcksetzen?`)) return;

    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/wertung/turnier/${selectedTurnier.turnier_id}/${encodeURIComponent(divisionType)}`, {
        method: 'DELETE',
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (data.success) {
        await fetchTurnierEinstellungen(selectedTurnier.turnier_id);
        alert('Auf Standard zurckgesetzt!');
      }
    } catch (err) {
      alert('Fehler: ' + err.message);
    }
  };

  if (loading) {
    return (
      <div className="turnier-einstellungen-jp loading">
        <div className="loading-kanji-jp"></div>
        <p>Lade Einstellungen...</p>
      </div>
    );
  }

  return (
    <div className="turnier-einstellungen-jp">
      {/* Header - Japanese Style */}
      <div className="page-header-jp">
        <div className="header-title-jp">
          <span className="header-kanji"></span>
          <div className="header-text-jp">
            <h1>Turnier-Einstellungen</h1>
            <p>Wertungssysteme und Punktebereiche konfigurieren</p>
          </div>
        </div>
      </div>

      {/* Tab Navigation */}
      <div className="tab-nav-jp">
        <button
          className={`tab-btn-jp ${activeTab === 'global' ? 'active' : ''}`}
          onClick={() => setActiveTab('global')}
        >
          <span className="tab-icon"></span>
          Standard-Einstellungen
        </button>
        <button
          className={`tab-btn-jp ${activeTab === 'turnier' ? 'active' : ''}`}
          onClick={() => setActiveTab('turnier')}
        >
          <span className="tab-icon"></span>
          Turnier-spezifisch
        </button>
        <button
          className={`tab-btn-jp ${activeTab === 'zeitplan' ? 'active' : ''}`}
          onClick={() => setActiveTab('zeitplan')}
        >
          <span className="tab-icon"></span>
          Zeitplan-Einstellungen
        </button>
      </div>

      {/* Global Settings Tab */}
      {activeTab === 'global' && (
        <div className="settings-container-jp">
          <div className="section-header-jp">
            <h2> Standard-Einstellungen (Dauereinstellungen)</h2>
            <p>Diese Einstellungen gelten fr alle Turniere, sofern nicht berschrieben.</p>
          </div>

          <div className="settings-grid-jp">
            {/* Bracket-Kategorien */}
            <div className="settings-group-jp">
              <h3> Bracket-Kategorien (K.O.-System)</h3>
              {einstellungen
                .filter(e => e.wertungs_system === 'bracket')
                .map(item => (
                  <SettingsCardJP
                    key={item.division_type}
                    item={item}
                    isEditing={editingItem === item.division_type}
                    onEdit={() => setEditingItem(item.division_type)}
                    onSave={handleSaveGlobal}
                    onCancel={() => setEditingItem(null)}
                    saving={saving}
                  />
                ))}
            </div>

            {/* Punkte-Kategorien */}
            <div className="settings-group-jp">
              <h3> Punkte-Kategorien (Bewertung)</h3>
              {einstellungen
                .filter(e => e.wertungs_system === 'punkte')
                .map(item => (
                  <SettingsCardJP
                    key={item.division_type}
                    item={item}
                    isEditing={editingItem === item.division_type}
                    onEdit={() => setEditingItem(item.division_type)}
                    onSave={handleSaveGlobal}
                    onCancel={() => setEditingItem(null)}
                    saving={saving}
                  />
                ))}
            </div>
          </div>
        </div>
      )}

      {/* Tournament-specific Tab */}
      {activeTab === 'turnier' && (
        <div className="settings-container-jp">
          <div className="section-header-jp">
            <h2> Turnier-spezifische Einstellungen</h2>
            <p>berschreiben Sie die Standard-Einstellungen fr ein bestimmtes Turnier.</p>
          </div>

          {/* Turnier Auswahl */}
          <div className="turnier-selector-jp">
            <label>Turnier auswhlen:</label>
            <select
              value={selectedTurnier?.turnier_id || ''}
              onChange={(e) => {
                const turnier = turniere.find(t => t.turnier_id === parseInt(e.target.value));
                setSelectedTurnier(turnier);
              }}
            >
              <option value="">-- Turnier whlen --</option>
              {turniere.map(t => (
                <option key={t.turnier_id} value={t.turnier_id}>
                  {t.name} ({new Date(t.datum).toLocaleDateString('de-DE')})
                </option>
              ))}
            </select>
          </div>

          {selectedTurnier && (
            <div className="turnier-settings-jp">
              <h3> Einstellungen fr: {selectedTurnier.name}</h3>

              <div className="settings-list-jp">
                {turnierEinstellungen.map(item => (
                  <TurnierSettingsRowJP
                    key={item.division_type}
                    item={item}
                    isEditing={editingItem === `turnier-${item.division_type}`}
                    onEdit={() => setEditingItem(`turnier-${item.division_type}`)}
                    onSave={handleSaveTurnier}
                    onCancel={() => setEditingItem(null)}
                    onReset={() => handleResetToDefault(item.division_type)}
                    saving={saving}
                  />
                ))}
              </div>
            </div>
          )}
        </div>
      )}

      {/* Zeitplan Settings Tab */}
      {activeTab === 'zeitplan' && (
        <div className="settings-container-jp">
          <div className="section-header-jp">
            <h2> Zeitplan-Einstellungen</h2>
            <p>Konfigurieren Sie die Zeiten fr die automatische Zeitplan-Generierung.</p>
          </div>

          {/* Turnier Auswahl fr spezifische Einstellungen */}
          <div className="turnier-selector-jp">
            <label>Einstellungen fr:</label>
            <select
              value={selectedTurnier?.turnier_id || 'global'}
              onChange={(e) => {
                if (e.target.value === 'global') {
                  setSelectedTurnier(null);
                  fetchZeitplanSettings();
                } else {
                  const turnier = turniere.find(t => t.turnier_id === parseInt(e.target.value));
                  setSelectedTurnier(turnier);
                }
              }}
            >
              <option value="global">Standard (Global)</option>
              {turniere.map(t => (
                <option key={t.turnier_id} value={t.turnier_id}>
                  {t.name} ({new Date(t.datum).toLocaleDateString('de-DE')})
                </option>
              ))}
            </select>
            {zeitplanSource === 'turnier' && (
              <span className="override-badge-jp">Turnier-spezifisch</span>
            )}
          </div>

          {/* Basis-Einstellungen */}
          <div className="zeitplan-settings-box">
            <h3> Basis-Einstellungen</h3>
            <div className="zeitplan-settings-grid">
              <div className="form-group-jp">
                <label>Anzahl Ringe</label>
                <input
                  type="number"
                  min="1"
                  max="10"
                  value={zeitplanSettings.rings}
                  onChange={(e) => setZeitplanSettings({
                    ...zeitplanSettings,
                    rings: parseInt(e.target.value) || 2
                  })}
                />
              </div>

              <div className="form-group-jp">
                <label>Beginn</label>
                <input
                  type="time"
                  value={zeitplanSettings.start_time}
                  onChange={(e) => setZeitplanSettings({
                    ...zeitplanSettings,
                    start_time: e.target.value
                  })}
                />
              </div>

              <div className="form-group-jp">
                <label>Pause zwischen Kategorien (Min)</label>
                <input
                  type="number"
                  min="0"
                  max="30"
                  value={zeitplanSettings.pause_between_categories}
                  onChange={(e) => setZeitplanSettings({
                    ...zeitplanSettings,
                    pause_between_categories: parseInt(e.target.value) || 5
                  })}
                />
              </div>
            </div>
          </div>

          {/* Disziplin-Zeiten */}
          <div className="zeitplan-settings-box">
            <h3> Disziplin-Zeiten</h3>

            {/* Formen */}
            <div className="discipline-settings-row">
              <div className="discipline-name-box">
                <span className="discipline-icon"></span>
                <span>Formen / Hyong / Tul</span>
              </div>
              <div className="discipline-inputs-box">
                <div className="input-group-mini">
                  <label>Pro Person:</label>
                  <input
                    type="number"
                    min="1"
                    max="10"
                    value={zeitplanSettings.disciplines?.['Formen']?.durationPerPerson || 3}
                    onChange={(e) => updateDiscipline('Formen', 'durationPerPerson', e.target.value)}
                  />
                  <span>Min</span>
                </div>
                <div className="input-group-mini">
                  <label>Pause danach:</label>
                  <input
                    type="number"
                    min="0"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Formen']?.breakAfter || 1}
                    onChange={(e) => updateDiscipline('Formen', 'breakAfter', e.target.value)}
                  />
                  <span>Min</span>
                </div>
              </div>
            </div>

            {/* Selbstverteidigung */}
            <div className="discipline-settings-row">
              <div className="discipline-name-box">
                <span className="discipline-icon"></span>
                <span>Selbstverteidigung</span>
              </div>
              <div className="discipline-inputs-box">
                <div className="input-group-mini">
                  <label>Pro Person:</label>
                  <input
                    type="number"
                    min="1"
                    max="10"
                    value={zeitplanSettings.disciplines?.['Selbstverteidigung']?.durationPerPerson || 4}
                    onChange={(e) => updateDiscipline('Selbstverteidigung', 'durationPerPerson', e.target.value)}
                  />
                  <span>Min</span>
                </div>
                <div className="input-group-mini">
                  <label>Pause danach:</label>
                  <input
                    type="number"
                    min="0"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Selbstverteidigung']?.breakAfter || 1}
                    onChange={(e) => updateDiscipline('Selbstverteidigung', 'breakAfter', e.target.value)}
                  />
                  <span>Min</span>
                </div>
              </div>
            </div>

            {/* Kickboxen */}
            <div className="discipline-settings-row">
              <div className="discipline-name-box">
                <span className="discipline-icon"></span>
                <span>Kickboxen / LK</span>
              </div>
              <div className="discipline-inputs-box">
                <div className="input-group-mini">
                  <label>Runden:</label>
                  <input
                    type="number"
                    min="1"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Kickboxen']?.rounds || 2}
                    onChange={(e) => updateDiscipline('Kickboxen', 'rounds', e.target.value)}
                  />
                </div>
                <div className="input-group-mini">
                  <label>Rundendauer:</label>
                  <input
                    type="number"
                    min="1"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Kickboxen']?.roundDuration || 2}
                    onChange={(e) => updateDiscipline('Kickboxen', 'roundDuration', e.target.value)}
                  />
                  <span>Min</span>
                </div>
                <div className="input-group-mini">
                  <label>Rundenpause:</label>
                  <input
                    type="number"
                    min="0"
                    max="3"
                    value={zeitplanSettings.disciplines?.['Kickboxen']?.breakBetweenRounds || 1}
                    onChange={(e) => updateDiscipline('Kickboxen', 'breakBetweenRounds', e.target.value)}
                  />
                  <span>Min</span>
                </div>
                <div className="input-group-mini">
                  <label>Pause danach:</label>
                  <input
                    type="number"
                    min="0"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Kickboxen']?.breakAfter || 2}
                    onChange={(e) => updateDiscipline('Kickboxen', 'breakAfter', e.target.value)}
                  />
                  <span>Min</span>
                </div>
              </div>
            </div>

            {/* Freefight */}
            <div className="discipline-settings-row">
              <div className="discipline-name-box">
                <span className="discipline-icon"></span>
                <span>Freefight</span>
              </div>
              <div className="discipline-inputs-box">
                <div className="input-group-mini">
                  <label>Runden:</label>
                  <input
                    type="number"
                    min="1"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Freefight']?.rounds || 2}
                    onChange={(e) => updateDiscipline('Freefight', 'rounds', e.target.value)}
                  />
                </div>
                <div className="input-group-mini">
                  <label>Rundendauer:</label>
                  <input
                    type="number"
                    min="1"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Freefight']?.roundDuration || 2}
                    onChange={(e) => updateDiscipline('Freefight', 'roundDuration', e.target.value)}
                  />
                  <span>Min</span>
                </div>
                <div className="input-group-mini">
                  <label>Rundenpause:</label>
                  <input
                    type="number"
                    min="0"
                    max="3"
                    value={zeitplanSettings.disciplines?.['Freefight']?.breakBetweenRounds || 1}
                    onChange={(e) => updateDiscipline('Freefight', 'breakBetweenRounds', e.target.value)}
                  />
                  <span>Min</span>
                </div>
                <div className="input-group-mini">
                  <label>Pause danach:</label>
                  <input
                    type="number"
                    min="0"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Freefight']?.breakAfter || 2}
                    onChange={(e) => updateDiscipline('Freefight', 'breakAfter', e.target.value)}
                  />
                  <span>Min</span>
                </div>
              </div>
            </div>

            {/* Pointfighting */}
            <div className="discipline-settings-row">
              <div className="discipline-name-box">
                <span className="discipline-icon"></span>
                <span>Pointfighting / Semi</span>
              </div>
              <div className="discipline-inputs-box">
                <div className="input-group-mini">
                  <label>Runden:</label>
                  <input
                    type="number"
                    min="1"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Pointfighting']?.rounds || 2}
                    onChange={(e) => updateDiscipline('Pointfighting', 'rounds', e.target.value)}
                  />
                </div>
                <div className="input-group-mini">
                  <label>Rundendauer:</label>
                  <input
                    type="number"
                    min="1"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Pointfighting']?.roundDuration || 2}
                    onChange={(e) => updateDiscipline('Pointfighting', 'roundDuration', e.target.value)}
                  />
                  <span>Min</span>
                </div>
                <div className="input-group-mini">
                  <label>Rundenpause:</label>
                  <input
                    type="number"
                    min="0"
                    max="3"
                    value={zeitplanSettings.disciplines?.['Pointfighting']?.breakBetweenRounds || 1}
                    onChange={(e) => updateDiscipline('Pointfighting', 'breakBetweenRounds', e.target.value)}
                  />
                  <span>Min</span>
                </div>
                <div className="input-group-mini">
                  <label>Pause danach:</label>
                  <input
                    type="number"
                    min="0"
                    max="5"
                    value={zeitplanSettings.disciplines?.['Pointfighting']?.breakAfter || 2}
                    onChange={(e) => updateDiscipline('Pointfighting', 'breakAfter', e.target.value)}
                  />
                  <span>Min</span>
                </div>
              </div>
            </div>
          </div>

          {/* Aktionen */}
          <div className="zeitplan-actions-box">
            <button
              className="btn-save-jp"
              onClick={() => handleSaveZeitplanSettings(!selectedTurnier)}
              disabled={saving}
            >
              {saving ? 'Speichern...' : (selectedTurnier ? 'Turnier-Einstellungen speichern' : 'Standard-Einstellungen speichern')}
            </button>

            {selectedTurnier && zeitplanSource === 'turnier' && (
              <button
                className="btn-reset-jp"
                onClick={handleResetZeitplanToDefault}
                disabled={saving}
              >
                Auf Standard zurcksetzen
              </button>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

// Settings Card Component fr globale Einstellungen (Japanese Style)
function SettingsCardJP({ item, isEditing, onEdit, onSave, onCancel, saving }) {
  const [editData, setEditData] = useState({ ...item });

  useEffect(() => {
    setEditData({ ...item });
  }, [item, isEditing]);

  if (!isEditing) {
    return (
      <div className="settings-card-jp">
        <div className="card-header-jp">
          <span className="division-type-jp">{item.division_type}</span>
          <span className={`wertung-badge-jp ${item.wertungs_system}`}>
            {item.wertungs_system === 'bracket' ? 'K.O.' : 'Punkte'}
          </span>
        </div>

        {item.wertungs_system === 'punkte' && (
          <div className="punkte-info-jp">
            <div className="punkte-range-jp">
              <span>Farbgurte:</span>
              <strong>{item.punkte_min_farbgurt} - {item.punkte_max_farbgurt}</strong>
            </div>
            <div className="punkte-range-jp">
              <span>Schwarzgurte:</span>
              <strong>{item.punkte_min_schwarzgurt} - {item.punkte_max_schwarzgurt}</strong>
            </div>
            <div className="kampfrichter-info-jp">
              <span>Kampfrichter:</span>
              <strong>{item.anzahl_kampfrichter}</strong>
            </div>
          </div>
        )}

        <p className="beschreibung-jp">{item.beschreibung}</p>

        <button className="btn-edit-jp" onClick={onEdit}>
          Bearbeiten
        </button>
      </div>
    );
  }

  return (
    <div className="settings-card-jp editing">
      <div className="card-header-jp">
        <span className="division-type-jp">{editData.division_type}</span>
      </div>

      <div className="form-group-jp">
        <label>Wertungssystem:</label>
        <select
          value={editData.wertungs_system}
          onChange={(e) => setEditData({ ...editData, wertungs_system: e.target.value })}
        >
          <option value="bracket">Bracket (K.O.-System)</option>
          <option value="punkte">Punkte (Bewertung)</option>
        </select>
      </div>

      {editData.wertungs_system === 'punkte' && (
        <>
          <div className="form-row-jp">
            <div className="form-group-jp">
              <label>Farbgurte Min:</label>
              <input
                type="number"
                step="0.5"
                value={editData.punkte_min_farbgurt}
                onChange={(e) => setEditData({ ...editData, punkte_min_farbgurt: parseFloat(e.target.value) })}
              />
            </div>
            <div className="form-group-jp">
              <label>Farbgurte Max:</label>
              <input
                type="number"
                step="0.5"
                value={editData.punkte_max_farbgurt}
                onChange={(e) => setEditData({ ...editData, punkte_max_farbgurt: parseFloat(e.target.value) })}
              />
            </div>
          </div>

          <div className="form-row-jp">
            <div className="form-group-jp">
              <label>Schwarzgurte Min:</label>
              <input
                type="number"
                step="0.5"
                value={editData.punkte_min_schwarzgurt}
                onChange={(e) => setEditData({ ...editData, punkte_min_schwarzgurt: parseFloat(e.target.value) })}
              />
            </div>
            <div className="form-group-jp">
              <label>Schwarzgurte Max:</label>
              <input
                type="number"
                step="0.5"
                value={editData.punkte_max_schwarzgurt}
                onChange={(e) => setEditData({ ...editData, punkte_max_schwarzgurt: parseFloat(e.target.value) })}
              />
            </div>
          </div>

          <div className="form-group-jp">
            <label>Anzahl Kampfrichter:</label>
            <input
              type="number"
              min="1"
              max="10"
              value={editData.anzahl_kampfrichter}
              onChange={(e) => setEditData({ ...editData, anzahl_kampfrichter: parseInt(e.target.value) })}
            />
          </div>
        </>
      )}

      <div className="form-group-jp">
        <label>Beschreibung:</label>
        <input
          type="text"
          value={editData.beschreibung || ''}
          onChange={(e) => setEditData({ ...editData, beschreibung: e.target.value })}
        />
      </div>

      <div className="card-actions-jp">
        <button className="btn-cancel-jp" onClick={onCancel} disabled={saving}>
          Abbrechen
        </button>
        <button className="btn-save-jp" onClick={() => onSave(editData)} disabled={saving}>
          {saving ? 'Speichern...' : 'Speichern'}
        </button>
      </div>
    </div>
  );
}

// Turnier Settings Row fr turnierspezifische berschreibungen (Japanese Style)
function TurnierSettingsRowJP({ item, isEditing, onEdit, onSave, onCancel, onReset, saving }) {
  const [editData, setEditData] = useState({ ...item });

  useEffect(() => {
    setEditData({ ...item });
  }, [item, isEditing]);

  if (!isEditing) {
    return (
      <div className={`turnier-row-jp ${item.ist_ueberschrieben ? 'overridden' : ''}`}>
        <div className="row-main-jp">
          <span className="division-type-jp">{item.division_type}</span>
          <span className={`wertung-badge-jp ${item.wertungs_system}`}>
            {item.wertungs_system === 'bracket' ? 'K.O.' : 'Punkte'}
          </span>
          {item.wertungs_system === 'punkte' && (
            <span className="punkte-preview-jp">
              Farbgurt: {item.punkte_min_farbgurt}-{item.punkte_max_farbgurt} |
              Schwarzgurt: {item.punkte_min_schwarzgurt}-{item.punkte_max_schwarzgurt}
            </span>
          )}
          {item.ist_ueberschrieben && (
            <span className="override-badge-jp">Angepasst</span>
          )}
        </div>
        <div className="row-actions-jp">
          <button className="btn-edit-jp" onClick={onEdit}>Anpassen</button>
          {item.ist_ueberschrieben && (
            <button className="btn-reset-jp" onClick={onReset}>Standard</button>
          )}
        </div>
      </div>
    );
  }

  return (
    <div className="turnier-row-jp editing">
      <div className="edit-form-jp">
        <h4>{item.division_type}</h4>

        <div className="form-group-jp">
          <label>Wertungssystem:</label>
          <select
            value={editData.wertungs_system}
            onChange={(e) => setEditData({ ...editData, wertungs_system: e.target.value })}
          >
            <option value="bracket">Bracket (K.O.-System)</option>
            <option value="punkte">Punkte (Bewertung)</option>
          </select>
        </div>

        {editData.wertungs_system === 'punkte' && (
          <div className="punkte-fields-jp">
            <div className="form-row-jp">
              <div className="form-group-jp">
                <label>Farbgurte:</label>
                <div className="range-inputs-jp">
                  <input
                    type="number"
                    step="0.5"
                    value={editData.punkte_min_farbgurt}
                    onChange={(e) => setEditData({ ...editData, punkte_min_farbgurt: parseFloat(e.target.value) })}
                  />
                  <span>bis</span>
                  <input
                    type="number"
                    step="0.5"
                    value={editData.punkte_max_farbgurt}
                    onChange={(e) => setEditData({ ...editData, punkte_max_farbgurt: parseFloat(e.target.value) })}
                  />
                </div>
              </div>
              <div className="form-group-jp">
                <label>Schwarzgurte:</label>
                <div className="range-inputs-jp">
                  <input
                    type="number"
                    step="0.5"
                    value={editData.punkte_min_schwarzgurt}
                    onChange={(e) => setEditData({ ...editData, punkte_min_schwarzgurt: parseFloat(e.target.value) })}
                  />
                  <span>bis</span>
                  <input
                    type="number"
                    step="0.5"
                    value={editData.punkte_max_schwarzgurt}
                    onChange={(e) => setEditData({ ...editData, punkte_max_schwarzgurt: parseFloat(e.target.value) })}
                  />
                </div>
              </div>
            </div>
            <div className="form-group-jp">
              <label>Kampfrichter:</label>
              <input
                type="number"
                min="1"
                max="10"
                value={editData.anzahl_kampfrichter}
                onChange={(e) => setEditData({ ...editData, anzahl_kampfrichter: parseInt(e.target.value) })}
                style={{ width: '80px' }}
              />
            </div>
          </div>
        )}

        <div className="edit-actions-jp">
          <button className="btn-cancel-jp" onClick={onCancel} disabled={saving}>
            Abbrechen
          </button>
          <button className="btn-save-jp" onClick={() => onSave(editData)} disabled={saving}>
            {saving ? 'Speichern...' : 'Speichern'}
          </button>
        </div>
      </div>
    </div>
  );
}

export default TurnierEinstellungen;
