// frontend/src/components/dashboard/KampfflaechenManager.jsx
// KAMPFFLCHEN VERWALTUNG - Japanese Style ()

import React, { useState, useEffect } from 'react';
import './KampfflaechenManager.css';

const FLAECHEN_TYPEN = [
  { value: 'matte', label: 'Matte', icon: '' },
  { value: 'tatami', label: 'Tatami', icon: '' },
  { value: 'ring', label: 'Ring', icon: '' },
  { value: 'arena', label: 'Arena', icon: '' },
  { value: 'octagon', label: 'Octagon', icon: '' }
];

const STATUS_OPTIONEN = [
  { value: 'frei', label: 'Frei', color: '#4CAF50', icon: '' },
  { value: 'besetzt', label: 'Besetzt', color: '#8B0000', icon: '' },
  { value: 'pause', label: 'Pause', color: '#d4af37', icon: '' },
  { value: 'inaktiv', label: 'Inaktiv', color: '#666', icon: '' }
];

const FARBEN = [
  '#8B0000', '#1a5c1a', '#1a3c6e', '#6e1a6e', '#d4af37', '#333333'
];

function KampfflaechenManager({ turnierId }) {
  const [flaechen, setFlaechen] = useState([]);
  const [turniere, setTurniere] = useState([]);
  const [selectedTurnier, setSelectedTurnier] = useState(turnierId || null);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [editingFlaeche, setEditingFlaeche] = useState(null);
  const [saving, setSaving] = useState(false);

  const [formData, setFormData] = useState({
    name: '',
    nummer: 1,
    typ: 'matte',
    groesse: '8x8m',
    standort: '',
    farbe: '#8B0000',
    prioritaet: 1,
    hat_anzeigetafel: false,
    hat_kamera: false,
    hat_timer: true,
    anzahl_kampfrichter: 3,
    hat_schreiber: true,
    anzahl_schreiber: 1
  });

  useEffect(() => {
    fetchTurniere();
  }, []);

  useEffect(() => {
    if (selectedTurnier) {
      fetchFlaechen();
    }
  }, [selectedTurnier]);

  const fetchTurniere = async () => {
    try {
      const token = localStorage.getItem('token');
      const res = await fetch('/api/turniere', {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (data.data) {
        setTurniere(data.data);
        if (!selectedTurnier && data.data.length > 0) {
          setSelectedTurnier(data.data[0].turnier_id);
        }
      }
      setLoading(false);
    } catch (err) {
      console.error('Fehler beim Laden der Turniere:', err);
      setLoading(false);
    }
  };

  const fetchFlaechen = async () => {
    if (!selectedTurnier) return;
    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/kampfflaechen/${selectedTurnier}`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (data.success) {
        setFlaechen(data.data);
      }
    } catch (err) {
      console.error('Fehler beim Laden der Kampfflchen:', err);
    }
  };

  const handleCreate = async () => {
    setSaving(true);
    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/kampfflaechen/${selectedTurnier}`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      });
      const data = await res.json();
      if (data.success) {
        await fetchFlaechen();
        setShowModal(false);
        resetForm();
      } else {
        alert('Fehler: ' + data.message);
      }
    } catch (err) {
      alert('Fehler beim Erstellen: ' + err.message);
    } finally {
      setSaving(false);
    }
  };

  const handleUpdate = async () => {
    if (!editingFlaeche) return;
    setSaving(true);
    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/kampfflaechen/${editingFlaeche.kampfflaeche_id}`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      });
      const data = await res.json();
      if (data.success) {
        await fetchFlaechen();
        setShowModal(false);
        setEditingFlaeche(null);
        resetForm();
      } else {
        alert('Fehler: ' + data.message);
      }
    } catch (err) {
      alert('Fehler beim Aktualisieren: ' + err.message);
    } finally {
      setSaving(false);
    }
  };

  const handleDelete = async (id) => {
    if (!window.confirm('Kampfflche wirklich lschen?')) return;
    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/kampfflaechen/${id}`, {
        method: 'DELETE',
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (data.success) {
        await fetchFlaechen();
      }
    } catch (err) {
      alert('Fehler beim Lschen: ' + err.message);
    }
  };

  const handleStatusChange = async (id, newStatus) => {
    try {
      const token = localStorage.getItem('token');
      await fetch(`/api/kampfflaechen/${id}/status`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ status: newStatus })
      });
      await fetchFlaechen();
    } catch (err) {
      console.error('Fehler beim Status-Update:', err);
    }
  };

  const handleAutoGenerate = async () => {
    const anzahl = prompt('Wie viele Kampfflchen sollen erstellt werden?', '4');
    if (!anzahl) return;

    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/kampfflaechen/${selectedTurnier}/auto-generate`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ anzahl: parseInt(anzahl) })
      });
      const data = await res.json();
      if (data.success) {
        await fetchFlaechen();
        alert(data.message);
      }
    } catch (err) {
      alert('Fehler: ' + err.message);
    }
  };

  const openEditModal = (flaeche) => {
    setEditingFlaeche(flaeche);
    setFormData({
      name: flaeche.name,
      nummer: flaeche.nummer,
      typ: flaeche.typ,
      groesse: flaeche.groesse || '',
      standort: flaeche.standort || '',
      farbe: flaeche.farbe || '#8B0000',
      prioritaet: flaeche.prioritaet || 1,
      hat_anzeigetafel: flaeche.hat_anzeigetafel || false,
      hat_kamera: flaeche.hat_kamera || false,
      hat_timer: flaeche.hat_timer !== false,
      anzahl_kampfrichter: flaeche.anzahl_kampfrichter || 3,
      hat_schreiber: flaeche.hat_schreiber !== false,
      anzahl_schreiber: flaeche.anzahl_schreiber || 1
    });
    setShowModal(true);
  };

  const openNewModal = () => {
    setEditingFlaeche(null);
    const nextNumber = flaechen.length > 0
      ? Math.max(...flaechen.map(f => f.nummer)) + 1
      : 1;
    resetForm();
    setFormData(prev => ({ ...prev, nummer: nextNumber, name: `Matte ${nextNumber}` }));
    setShowModal(true);
  };

  const resetForm = () => {
    setFormData({
      name: '',
      nummer: 1,
      typ: 'matte',
      groesse: '8x8m',
      standort: '',
      farbe: '#8B0000',
      prioritaet: 1,
      hat_anzeigetafel: false,
      hat_kamera: false,
      hat_timer: true,
      anzahl_kampfrichter: 3,
      hat_schreiber: true,
      anzahl_schreiber: 1
    });
  };

  const getTypIcon = (typ) => {
    const found = FLAECHEN_TYPEN.find(t => t.value === typ);
    return found ? found.icon : '';
  };

  const getStatusInfo = (status) => {
    return STATUS_OPTIONEN.find(s => s.value === status) || STATUS_OPTIONEN[0];
  };

  if (loading) {
    return (
      <div className="kampfflaechen-jp loading">
        <div className="loading-kanji-jp"></div>
        <p>Lade Kampfflchen...</p>
      </div>
    );
  }

  return (
    <div className="kampfflaechen-jp">
      {/* Header */}
      <div className="kf-header-jp">
        <div className="header-title-jp">
          <span className="header-kanji"></span>
          <div className="header-text-jp">
            <h1>Kampfflchen</h1>
            <p>Matten, Arenen und Kampfbereiche verwalten</p>
          </div>
        </div>
        <div className="header-actions-jp">
          <button className="btn-auto-jp" onClick={handleAutoGenerate}>
             Auto-Generieren
          </button>
          <button className="btn-add-jp" onClick={openNewModal}>
             Neue Flche
          </button>
        </div>
      </div>

      {/* Turnier-Auswahl */}
      <div className="turnier-selector-jp">
        <label>Turnier:</label>
        <select
          value={selectedTurnier || ''}
          onChange={(e) => setSelectedTurnier(parseInt(e.target.value))}
        >
          {turniere.map(t => (
            <option key={t.turnier_id} value={t.turnier_id}>
              {t.name} ({new Date(t.datum).toLocaleDateString('de-DE')})
            </option>
          ))}
        </select>
      </div>

      {/* Kampfflchen Grid */}
      {flaechen.length === 0 ? (
        <div className="empty-state-jp">
          <div className="empty-icon"></div>
          <h3>Keine Kampfflchen vorhanden</h3>
          <p>Erstellen Sie Kampfflchen fr dieses Turnier</p>
          <button className="btn-add-jp" onClick={openNewModal}>
             Erste Flche erstellen
          </button>
        </div>
      ) : (
        <div className="flaechen-grid-jp">
          {flaechen.map(flaeche => {
            // Automatischer Status basierend auf aktivem Kampf
            const hatAktivenKampf = flaeche.aktueller_kampf_id || flaeche.kaempfer1;
            const effektiverStatus = hatAktivenKampf ? 'besetzt' : flaeche.status;
            const statusInfo = getStatusInfo(effektiverStatus);

            return (
              <div
                key={flaeche.kampfflaeche_id}
                className={`flaeche-card-jp status-${effektiverStatus} ${hatAktivenKampf ? 'hat-kampf' : ''}`}
                style={{ '--flaeche-color': flaeche.farbe }}
              >
                <div className="flaeche-header-jp">
                  <div className="flaeche-nummer-jp" style={{ background: flaeche.farbe }}>
                    {flaeche.nummer}
                  </div>
                  <div className="flaeche-info-jp">
                    <h3>{flaeche.name}</h3>
                    <span className="flaeche-typ-jp">
                      {getTypIcon(flaeche.typ)} {flaeche.typ}
                    </span>
                  </div>
                  <div
                    className={`flaeche-status-jp ${hatAktivenKampf ? 'pulsing' : ''}`}
                    style={{ background: statusInfo.color }}
                  >
                    {statusInfo.icon} {statusInfo.label}
                  </div>
                </div>

                {/* Aktueller Kampf */}
                {flaeche.status === 'besetzt' && flaeche.kaempfer1 && (
                  <div className="flaeche-kampf-jp">
                    <div className="kampf-header-jp">
                      <span> Aktueller Kampf</span>
                      <span className="bracket-name-jp">{flaeche.bracket_name}</span>
                    </div>
                    <div className="kampf-teilnehmer-jp">
                      <div className="teilnehmer-jp rot">
                        <span className="name-jp">{flaeche.kaempfer1}</span>
                        <span className="verein-jp">{flaeche.verein1}</span>
                      </div>
                      <div className="vs-jp">VS</div>
                      <div className="teilnehmer-jp blau">
                        <span className="name-jp">{flaeche.kaempfer2}</span>
                        <span className="verein-jp">{flaeche.verein2}</span>
                      </div>
                    </div>
                  </div>
                )}

                {/* Details */}
                <div className="flaeche-details-jp">
                  <div className="details-row-jp">
                    {flaeche.groesse && (
                      <span className="detail-item-jp"> {flaeche.groesse}</span>
                    )}
                    {flaeche.standort && (
                      <span className="detail-item-jp"> {flaeche.standort}</span>
                    )}
                  </div>
                  <div className="details-row-jp personal-row-jp">
                    <span className="detail-item-jp" title="Kampfrichter">
                       {flaeche.anzahl_kampfrichter || 3} KR
                    </span>
                    {(flaeche.hat_schreiber !== false) && (
                      <span className="detail-item-jp" title="Schreiber">
                         {flaeche.anzahl_schreiber || 1} Schreiber
                      </span>
                    )}
                    <div className="ausstattung-jp">
                      {flaeche.hat_timer && <span title="Timer"></span>}
                      {flaeche.hat_anzeigetafel && <span title="Anzeigetafel"></span>}
                      {flaeche.hat_kamera && <span title="Kamera"></span>}
                    </div>
                  </div>
                </div>

                {/* Status-Buttons */}
                <div className="status-buttons-jp">
                  {STATUS_OPTIONEN.map(s => (
                    <button
                      key={s.value}
                      className={`status-btn-jp ${effektiverStatus === s.value ? 'active' : ''}`}
                      style={{ '--status-color': s.color }}
                      onClick={() => handleStatusChange(flaeche.kampfflaeche_id, s.value)}
                      disabled={hatAktivenKampf && s.value === 'frei'}
                      title={hatAktivenKampf && s.value === 'frei' ? 'Kampf muss erst beendet werden' : ''}
                    >
                      {s.icon} {s.label}
                    </button>
                  ))}
                </div>

                {/* Aktionen */}
                <div className="flaeche-actions-jp">
                  <button
                    className="btn-edit-jp"
                    onClick={() => openEditModal(flaeche)}
                  >
                     Bearbeiten
                  </button>
                  <button
                    className="btn-delete-jp"
                    onClick={() => handleDelete(flaeche.kampfflaeche_id)}
                  >
                    
                  </button>
                </div>
              </div>
            );
          })}
        </div>
      )}

      {/* Modal */}
      {showModal && (
        <div className="modal-overlay-jp" onClick={() => setShowModal(false)}>
          <div className="modal-content-jp" onClick={e => e.stopPropagation()}>
            <div className="modal-header-jp">
              <h2>{editingFlaeche ? ' Kampfflche bearbeiten' : ' Neue Kampfflche'}</h2>
              <button className="modal-close-jp" onClick={() => setShowModal(false)}></button>
            </div>

            <div className="modal-body-jp">
              <div className="form-row-jp">
                <div className="form-group-jp">
                  <label>Name *</label>
                  <input
                    type="text"
                    value={formData.name}
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    placeholder="z.B. Matte 1"
                  />
                </div>
                <div className="form-group-jp">
                  <label>Nummer *</label>
                  <input
                    type="number"
                    min="1"
                    value={formData.nummer}
                    onChange={(e) => {
                      const neueNummer = parseInt(e.target.value) || 1;
                      const typLabel = FLAECHEN_TYPEN.find(t => t.value === formData.typ)?.label || 'Matte';
                      setFormData({
                        ...formData,
                        nummer: neueNummer,
                        name: `${typLabel} ${neueNummer}`
                      });
                    }}
                  />
                </div>
              </div>

              <div className="form-row-jp">
                <div className="form-group-jp">
                  <label>Typ</label>
                  <select
                    value={formData.typ}
                    onChange={(e) => {
                      const neuerTyp = e.target.value;
                      const typLabel = FLAECHEN_TYPEN.find(t => t.value === neuerTyp)?.label || 'Matte';
                      setFormData({
                        ...formData,
                        typ: neuerTyp,
                        name: `${typLabel} ${formData.nummer}`
                      });
                    }}
                  >
                    {FLAECHEN_TYPEN.map(t => (
                      <option key={t.value} value={t.value}>
                        {t.icon} {t.label}
                      </option>
                    ))}
                  </select>
                </div>
                <div className="form-group-jp">
                  <label>Gre</label>
                  <input
                    type="text"
                    value={formData.groesse}
                    onChange={(e) => setFormData({ ...formData, groesse: e.target.value })}
                    placeholder="z.B. 8x8m"
                  />
                </div>
              </div>

              <div className="form-group-jp">
                <label>Standort</label>
                <input
                  type="text"
                  value={formData.standort}
                  onChange={(e) => setFormData({ ...formData, standort: e.target.value })}
                  placeholder="z.B. Halle A, Bereich Nord"
                />
              </div>

              <div className="form-group-jp">
                <label>Farbe</label>
                <div className="color-picker-jp">
                  {FARBEN.map(color => (
                    <button
                      key={color}
                      className={`color-btn-jp ${formData.farbe === color ? 'active' : ''}`}
                      style={{ background: color }}
                      onClick={() => setFormData({ ...formData, farbe: color })}
                    />
                  ))}
                </div>
              </div>

              <div className="form-group-jp">
                <label>Personal-Bedarf</label>
                <div className="personal-config-jp">
                  <div className="personal-item-jp">
                    <label> Kampfrichter:</label>
                    <select
                      value={formData.anzahl_kampfrichter}
                      onChange={(e) => setFormData({ ...formData, anzahl_kampfrichter: parseInt(e.target.value) })}
                    >
                      <option value={1}>1 Kampfrichter</option>
                      <option value={2}>2 Kampfrichter</option>
                      <option value={3}>3 Kampfrichter (Standard)</option>
                      <option value={4}>4 Kampfrichter</option>
                      <option value={5}>5 Kampfrichter</option>
                    </select>
                  </div>
                  <div className="personal-item-jp">
                    <label className="checkbox-inline-jp">
                      <input
                        type="checkbox"
                        checked={formData.hat_schreiber}
                        onChange={(e) => setFormData({ ...formData, hat_schreiber: e.target.checked })}
                      />
                      <span> Schreiber</span>
                    </label>
                    {formData.hat_schreiber && (
                      <select
                        value={formData.anzahl_schreiber}
                        onChange={(e) => setFormData({ ...formData, anzahl_schreiber: parseInt(e.target.value) })}
                      >
                        <option value={1}>1 Schreiber</option>
                        <option value={2}>2 Schreiber</option>
                      </select>
                    )}
                  </div>
                </div>
              </div>

              <div className="form-group-jp">
                <label>Ausstattung (Hardware vor Ort)</label>
                <div className="checkbox-group-jp">
                  <label className="checkbox-item-jp" title="Countdown-Display fr Kampfzeit">
                    <input
                      type="checkbox"
                      checked={formData.hat_timer}
                      onChange={(e) => setFormData({ ...formData, hat_timer: e.target.checked })}
                    />
                    <span> Timer-Display</span>
                  </label>
                  <label className="checkbox-item-jp" title="Punkte-Anzeigetafel">
                    <input
                      type="checkbox"
                      checked={formData.hat_anzeigetafel}
                      onChange={(e) => setFormData({ ...formData, hat_anzeigetafel: e.target.checked })}
                    />
                    <span> Anzeigetafel</span>
                  </label>
                  <label className="checkbox-item-jp" title="Kamera fr Streaming/Aufzeichnung">
                    <input
                      type="checkbox"
                      checked={formData.hat_kamera}
                      onChange={(e) => setFormData({ ...formData, hat_kamera: e.target.checked })}
                    />
                    <span> Kamera</span>
                  </label>
                </div>
              </div>
            </div>

            <div className="modal-footer-jp">
              <button className="btn-cancel-jp" onClick={() => setShowModal(false)}>
                Abbrechen
              </button>
              <button
                className="btn-save-jp"
                onClick={editingFlaeche ? handleUpdate : handleCreate}
                disabled={saving || !formData.name}
              >
                {saving ? 'Speichern...' : (editingFlaeche ? 'Aktualisieren' : 'Erstellen')}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default KampfflaechenManager;
