import React, { useState, useEffect, useCallback } from 'react';
import './AnmeldeDashboard.css';

function AnmeldeDashboard({ selectedTurnier }) {
  const [anmeldungen, setAnmeldungen] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterStatus, setFilterStatus] = useState('');
  const [filterVerein, setFilterVerein] = useState('');
  const [sortBy, setSortBy] = useState('nachname');
  const [sortOrder, setSortOrder] = useState('asc');
  const [selectedRows, setSelectedRows] = useState(new Set());
  const [expandedRows, setExpandedRows] = useState(new Set());

  const fetchAnmeldungen = useCallback(async () => {
    if (!selectedTurnier) return;

    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/anmeldungen/admin/all?turnierId=${selectedTurnier.id}`, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });

      if (!response.ok) {
        throw new Error('Fehler beim Abrufen der Anmeldungen');
      }

      const result = await response.json();
      if (result.success && result.data) {
        setAnmeldungen(Array.isArray(result.data) ? result.data : []);
      } else if (Array.isArray(result)) {
        setAnmeldungen(result);
      } else {
        setAnmeldungen([]);
      }
      setLoading(false);
    } catch (err) {
      console.error('Fehler:', err);
      setError(err.message);
      setLoading(false);
    }
  }, [selectedTurnier]);

  useEffect(() => {
    if (selectedTurnier) {
      fetchAnmeldungen();
    }
  }, [selectedTurnier, fetchAnmeldungen]);

  // Gruppiere Anmeldungen nach Wettkmpfer
  const groupByWettkampfer = (anmeldungenList) => {
    const grouped = {};

    anmeldungenList.forEach(a => {
      const key = a.wettkampfer_id || `${a.vorname}-${a.nachname}`;
      if (!grouped[key]) {
        grouped[key] = {
          wettkampfer_id: a.wettkampfer_id,
          vorname: a.vorname,
          nachname: a.nachname,
          verein_name: a.verein_name,
          gewicht: a.gewicht,
          skill_level: a.skill_level,
          anmeldungen: []
        };
      }
      grouped[key].anmeldungen.push({
        anmeldung_id: a.anmeldung_id,
        division_code: a.division_code,
        status: a.status,
        kampfstil: a.kampfstil
      });
    });

    return Object.values(grouped);
  };

  // Stats berechnen
  const stats = {
    total: anmeldungen.length,
    pending: anmeldungen.filter(a => a.status === 'Angemeldet').length,
    confirmed: anmeldungen.filter(a => a.status === 'Besttigt').length,
    cancelled: anmeldungen.filter(a => a.status === 'Storniert' || a.status === 'Abgemeldet').length
  };

  // Unique Vereine fr Filter
  const uniqueVereine = [...new Set(anmeldungen.map(a => a.verein_name).filter(Boolean))].sort();

  // Unique Personen zhlen
  const uniquePersons = new Set(anmeldungen.map(a => a.wettkampfer_id || `${a.vorname}-${a.nachname}`)).size;

  // Gefilterte und sortierte Anmeldungen
  const getFilteredAnmeldungen = () => {
    let filtered = anmeldungen.filter(a => {
      const name = `${a.vorname || ''} ${a.nachname || ''}`.toLowerCase();
      const matchesSearch = name.includes(searchTerm.toLowerCase()) ||
        a.verein_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        a.division_code?.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesStatus = !filterStatus || a.status === filterStatus;
      const matchesVerein = !filterVerein || a.verein_name === filterVerein;
      return matchesSearch && matchesStatus && matchesVerein;
    });

    return filtered;
  };

  // Gruppierte und sortierte Daten
  const getGroupedData = () => {
    const filtered = getFilteredAnmeldungen();
    const grouped = groupByWettkampfer(filtered);

    // Sortieren
    grouped.sort((a, b) => {
      let aVal, bVal;
      switch (sortBy) {
        case 'nachname':
          aVal = `${a.nachname || ''} ${a.vorname || ''}`.toLowerCase();
          bVal = `${b.nachname || ''} ${b.vorname || ''}`.toLowerCase();
          break;
        case 'verein':
          aVal = (a.verein_name || '').toLowerCase();
          bVal = (b.verein_name || '').toLowerCase();
          break;
        case 'gewicht':
          aVal = parseFloat(a.gewicht) || 0;
          bVal = parseFloat(b.gewicht) || 0;
          break;
        case 'kategorien':
          aVal = a.anmeldungen.length;
          bVal = b.anmeldungen.length;
          break;
        default:
          aVal = `${a.nachname || ''}`.toLowerCase();
          bVal = `${b.nachname || ''}`.toLowerCase();
      }
      if (aVal < bVal) return sortOrder === 'asc' ? -1 : 1;
      if (aVal > bVal) return sortOrder === 'asc' ? 1 : -1;
      return 0;
    });

    return grouped;
  };

  const handleSort = (field) => {
    if (sortBy === field) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortBy(field);
      setSortOrder('asc');
    }
  };

  const handleStatusChange = async (anmeldungId, newStatus) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/anmeldungen/${anmeldungId}`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ status: newStatus })
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Fehler beim Aktualisieren');
      }

      await fetchAnmeldungen();
    } catch (err) {
      console.error('Fehler:', err);
      alert('Fehler: ' + err.message);
    }
  };

  // Alle Anmeldungen einer Person besttigen
  const handleConfirmAll = async (person) => {
    const pendingAnmeldungen = person.anmeldungen.filter(a => a.status === 'Angemeldet');
    for (const anmeldung of pendingAnmeldungen) {
      await handleStatusChange(anmeldung.anmeldung_id, 'Besttigt');
    }
  };

  const handleDelete = async (anmeldungId) => {
    if (!window.confirm('Anmeldung wirklich lschen?')) return;
    try {
      const token = localStorage.getItem('token');
      await fetch(`/api/anmeldungen/${anmeldungId}`, {
        method: 'DELETE',
        headers: { 'Authorization': `Bearer ${token}` }
      });
      await fetchAnmeldungen();
    } catch (err) {
      console.error('Fehler:', err);
    }
  };

  const toggleRowSelection = (wettkampferId) => {
    setSelectedRows(prev => {
      const newSet = new Set(prev);
      if (newSet.has(wettkampferId)) {
        newSet.delete(wettkampferId);
      } else {
        newSet.add(wettkampferId);
      }
      return newSet;
    });
  };

  const toggleRowExpand = (wettkampferId) => {
    setExpandedRows(prev => {
      const newSet = new Set(prev);
      if (newSet.has(wettkampferId)) {
        newSet.delete(wettkampferId);
      } else {
        newSet.add(wettkampferId);
      }
      return newSet;
    });
  };

  const groupedData = getGroupedData();

  const toggleAllRows = () => {
    if (selectedRows.size === groupedData.length) {
      setSelectedRows(new Set());
    } else {
      setSelectedRows(new Set(groupedData.map(p => p.wettkampfer_id)));
    }
  };

  const handleBatchConfirm = async () => {
    if (selectedRows.size === 0) return;

    for (const person of groupedData) {
      if (selectedRows.has(person.wettkampfer_id)) {
        await handleConfirmAll(person);
      }
    }
    setSelectedRows(new Set());
  };

  const getOverallStatus = (anmeldungenList) => {
    const statuses = anmeldungenList.map(a => a.status);
    if (statuses.every(s => s === 'Besttigt')) return 'confirmed';
    if (statuses.every(s => s === 'Storniert' || s === 'Abgemeldet')) return 'cancelled';
    if (statuses.some(s => s === 'Angemeldet')) return 'pending';
    return 'mixed';
  };

  const getStatusBadge = (status) => {
    const config = {
      'confirmed': { class: 'status-confirmed', label: 'Besttigt' },
      'pending': { class: 'status-pending', label: 'Ausstehend' },
      'cancelled': { class: 'status-cancelled', label: 'Storniert' },
      'mixed': { class: 'status-mixed', label: 'Gemischt' }
    };
    const c = config[status] || config.pending;
    return <span className={`status-badge ${c.class}`}>{c.label}</span>;
  };

  if (loading) {
    return (
      <div className="anmelde-dashboard">
        <div className="loading-state">Anmeldungen werden geladen...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="anmelde-dashboard">
        <div className="error-state">
          <p>Fehler: {error}</p>
          <button onClick={fetchAnmeldungen}>Erneut versuchen</button>
        </div>
      </div>
    );
  }

  return (
    <div className="anmelde-dashboard">
      {/* Kompakte Stats */}
      <div className="stats-row">
        <div className="stat-item">
          <span className="stat-value">{uniquePersons}</span>
          <span className="stat-label">Personen</span>
        </div>
        <div className="stat-item">
          <span className="stat-value">{stats.total}</span>
          <span className="stat-label">Starts</span>
        </div>
        <div className="stat-item stat-pending">
          <span className="stat-value">{stats.pending}</span>
          <span className="stat-label">Ausstehend</span>
        </div>
        <div className="stat-item stat-confirmed">
          <span className="stat-value">{stats.confirmed}</span>
          <span className="stat-label">Besttigt</span>
        </div>
      </div>

      {/* Filter-Leiste */}
      <div className="filter-bar">
        <input
          type="text"
          className="search-input"
          placeholder="Suchen..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />

        <select
          className="filter-select"
          value={filterStatus}
          onChange={(e) => setFilterStatus(e.target.value)}
        >
          <option value="">Alle Status</option>
          <option value="Angemeldet">Angemeldet</option>
          <option value="Besttigt">Besttigt</option>
          <option value="Storniert">Storniert</option>
        </select>

        <select
          className="filter-select"
          value={filterVerein}
          onChange={(e) => setFilterVerein(e.target.value)}
        >
          <option value="">Alle Vereine</option>
          {uniqueVereine.map(v => (
            <option key={v} value={v}>{v}</option>
          ))}
        </select>

        {selectedRows.size > 0 && (
          <button className="batch-btn" onClick={handleBatchConfirm}>
            {selectedRows.size} besttigen
          </button>
        )}

        <span className="result-count">{groupedData.length} Personen</span>
      </div>

      {/* Tabelle */}
      <div className="table-container">
        <table className="anmeldungen-table">
          <thead>
            <tr>
              <th className="col-checkbox">
                <input
                  type="checkbox"
                  checked={selectedRows.size === groupedData.length && groupedData.length > 0}
                  onChange={toggleAllRows}
                />
              </th>
              <th className="col-expand"></th>
              <th className="col-name sortable" onClick={() => handleSort('nachname')}>
                Name {sortBy === 'nachname' && (sortOrder === 'asc' ? '' : '')}
              </th>
              <th className="col-verein sortable" onClick={() => handleSort('verein')}>
                Verein {sortBy === 'verein' && (sortOrder === 'asc' ? '' : '')}
              </th>
              <th className="col-kategorien sortable" onClick={() => handleSort('kategorien')}>
                Kategorien {sortBy === 'kategorien' && (sortOrder === 'asc' ? '' : '')}
              </th>
              <th className="col-gewicht sortable" onClick={() => handleSort('gewicht')}>
                Gewicht {sortBy === 'gewicht' && (sortOrder === 'asc' ? '' : '')}
              </th>
              <th className="col-level">Level</th>
              <th className="col-status">Status</th>
              <th className="col-actions">Aktionen</th>
            </tr>
          </thead>
          <tbody>
            {groupedData.length === 0 ? (
              <tr>
                <td colSpan="9" className="empty-row">
                  Keine Anmeldungen gefunden
                </td>
              </tr>
            ) : (
              groupedData.map(person => {
                const isExpanded = expandedRows.has(person.wettkampfer_id);
                const overallStatus = getOverallStatus(person.anmeldungen);
                const hasPending = person.anmeldungen.some(a => a.status === 'Angemeldet');

                return (
                  <React.Fragment key={person.wettkampfer_id}>
                    <tr className={`${selectedRows.has(person.wettkampfer_id) ? 'selected' : ''} ${isExpanded ? 'expanded' : ''}`}>
                      <td className="col-checkbox">
                        <input
                          type="checkbox"
                          checked={selectedRows.has(person.wettkampfer_id)}
                          onChange={() => toggleRowSelection(person.wettkampfer_id)}
                        />
                      </td>
                      <td className="col-expand">
                        {person.anmeldungen.length > 1 && (
                          <button
                            className="expand-btn"
                            onClick={() => toggleRowExpand(person.wettkampfer_id)}
                          >
                            {isExpanded ? '' : ''}
                          </button>
                        )}
                      </td>
                      <td className="col-name">
                        <strong>{person.nachname}</strong>, {person.vorname}
                      </td>
                      <td className="col-verein">{person.verein_name || '-'}</td>
                      <td className="col-kategorien">
                        <span className="kategorie-count">{person.anmeldungen.length}</span>
                        {!isExpanded && person.anmeldungen.length <= 2 && (
                          <span className="kategorie-preview">
                            {person.anmeldungen.map(a => a.division_code).join(', ')}
                          </span>
                        )}
                      </td>
                      <td className="col-gewicht">{person.gewicht ? `${person.gewicht} kg` : '-'}</td>
                      <td className="col-level">{person.skill_level || '-'}</td>
                      <td className="col-status">{getStatusBadge(overallStatus)}</td>
                      <td className="col-actions">
                        <div className="action-buttons">
                          {hasPending && (
                            <button
                              className="action-btn confirm"
                              onClick={() => handleConfirmAll(person)}
                              title="Alle besttigen"
                            >
                              
                            </button>
                          )}
                        </div>
                      </td>
                    </tr>

                    {/* Expandierte Kategorien-Zeilen */}
                    {isExpanded && person.anmeldungen.map(anmeldung => (
                      <tr key={anmeldung.anmeldung_id} className="sub-row">
                        <td></td>
                        <td></td>
                        <td colSpan="2" className="sub-division">
                          <span className="division-badge">{anmeldung.division_code}</span>
                          {anmeldung.kampfstil && <span className="kampfstil">{anmeldung.kampfstil}</span>}
                        </td>
                        <td colSpan="2"></td>
                        <td>
                          <span className={`status-badge status-${anmeldung.status === 'Besttigt' ? 'confirmed' : anmeldung.status === 'Angemeldet' ? 'pending' : 'cancelled'}`}>
                            {anmeldung.status}
                          </span>
                        </td>
                        <td>
                          <div className="action-buttons">
                            {anmeldung.status === 'Angemeldet' && (
                              <button
                                className="action-btn confirm small"
                                onClick={() => handleStatusChange(anmeldung.anmeldung_id, 'Besttigt')}
                                title="Besttigen"
                              >
                                
                              </button>
                            )}
                            <button
                              className="action-btn delete small"
                              onClick={() => handleDelete(anmeldung.anmeldung_id)}
                              title="Lschen"
                            >
                              
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </React.Fragment>
                );
              })
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default AnmeldeDashboard;
