import React, { useState, useEffect, useRef, useCallback } from 'react';
import ReactDOM from 'react-dom';
import TDACard from '../common/TDACard';
import './BracketDashboard.css';

function BracketDashboard() {
  const [brackets, setBrackets] = useState([]);
  const [selectedBracket, setSelectedBracket] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [bracketDetails, setBracketDetails] = useState(null);
  const [availableFighters, setAvailableFighters] = useState([]);
  const [turniere, setTurniere] = useState([]);
  const [selectedTurnier, setSelectedTurnier] = useState(null);
  const [generating, setGenerating] = useState(false);

  useEffect(() => {
    fetchTurniere();
    fetchBrackets();
    fetchAvailableFighters();
  }, []);

  const fetchTurniere = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch('/api/turniere', {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (response.ok) {
        const data = await response.json();
        const turniereList = data.data || data || [];
        setTurniere(Array.isArray(turniereList) ? turniereList : []);
        if (turniereList.length > 0) {
          setSelectedTurnier(turniereList[0]);
        }
      }
    } catch (err) {
      console.error('Fehler beim Laden der Turniere:', err);
    }
  };

  const handleAutoGenerateBrackets = async () => {
    if (!selectedTurnier) {
      alert('Bitte whlen Sie zuerst ein Turnier aus.');
      return;
    }

    const force = brackets.length > 0;
    if (force) {
      const confirmed = window.confirm(
        `Es existieren bereits Brackets. Mchten Sie alle lschen und neu generieren?`
      );
      if (!confirmed) return;
    }

    setGenerating(true);
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/brackets/turnier/${selectedTurnier.turnier_id}/auto-generate`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ force })
      });

      const data = await response.json();

      if (response.ok && data.success) {
        alert(`${data.data.brackets_erstellt} Brackets erfolgreich generiert!`);
        await fetchBrackets();
      } else {
        throw new Error(data.message || 'Fehler bei der Generierung');
      }
    } catch (err) {
      console.error('Fehler:', err);
      alert('Fehler: ' + err.message);
    } finally {
      setGenerating(false);
    }
  };

  const fetchBrackets = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch('/api/brackets', {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });

      if (!response.ok) {
        throw new Error('Fehler beim Abrufen der Brackets');
      }

      const result = await response.json();
      // API gibt { success: true, data: [...] } zurck
      const bracketsData = result.data || result || [];
      setBrackets(Array.isArray(bracketsData) ? bracketsData : []);
      setLoading(false);
    } catch (err) {
      console.error('Fehler:', err);
      setError(err.message);
      setLoading(false);
    }
  };

  const fetchAvailableFighters = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch('/api/wettkaempfer', {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (response.ok) {
        const fighters = await response.json();
        setAvailableFighters(Array.isArray(fighters) ? fighters : []);
      }
    } catch (err) {
      console.error('Fehler beim Laden der Wettkmpfer:', err);
    }
  };

  const fetchBracketDetails = async (bracketId) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/brackets/${bracketId}`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (response.ok) {
        const result = await response.json();
        // API gibt { success, data: { bracket, teilnehmer, kaempfe } } zurck
        const data = result.data || result;
        setBracketDetails({
          bracket: data.bracket,
          teilnehmer: data.teilnehmer || [],
          kaempfe: data.kaempfe || []
        });
      }
    } catch (err) {
      console.error('Fehler beim Laden der Bracket-Details:', err);
    }
  };

  const handleCreateBracket = async (bracketData) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch('/api/brackets', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(bracketData)
      });

      if (!response.ok) {
        throw new Error('Fehler beim Erstellen des Brackets');
      }

      await fetchBrackets();
      setShowCreateModal(false);
    } catch (err) {
      console.error('Fehler:', err);
      setError(err.message);
    }
  };

  const handleAddParticipants = async (bracketId, participants) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/brackets/${bracketId}/teilnehmer`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          wettkaempfer_ids: participants.map(p => p.wettkaempfer_id),
          seed_positions: participants.map((_, index) => index + 1)
        })
      });

      if (!response.ok) {
        throw new Error('Fehler beim Hinzufgen der Teilnehmer');
      }

      await fetchBracketDetails(bracketId);
    } catch (err) {
      console.error('Fehler:', err);
      setError(err.message);
    }
  };

  const handleGenerateBracket = async (bracketId) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/brackets/${bracketId}/generate`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (!response.ok) {
        throw new Error('Fehler beim Generieren des Brackets');
      }

      await fetchBracketDetails(bracketId);
    } catch (err) {
      console.error('Fehler:', err);
      setError(err.message);
    }
  };

  const handleUpdateFightResult = async (fightId, result) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/brackets/kampf/${fightId}/result`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(result)
      });

      if (!response.ok) {
        throw new Error('Fehler beim Aktualisieren des Kampfergebnisses');
      }

      await fetchBracketDetails(selectedBracket.bracket_id);
    } catch (err) {
      console.error('Fehler:', err);
      setError(err.message);
    }
  };

  const handleSplitBracket = async (bracketId, splitType) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/brackets/${bracketId}/split`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ splitType })
      });

      const data = await response.json();

      if (response.ok && data.success) {
        alert(`Division erfolgreich geteilt!\nGruppe A: ${data.data.originalBracket.participants} Teilnehmer\nGruppe B: ${data.data.newBracket.participants} Teilnehmer`);
        setSelectedBracket(null);
        setBracketDetails(null);
        await fetchBrackets();
      } else {
        throw new Error(data.message || 'Fehler beim Splitten');
      }
    } catch (err) {
      console.error('Fehler:', err);
      alert('Fehler: ' + err.message);
    }
  };

  const handleMergeBrackets = async (sourceBracketId, targetBracketId, newName = null) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch('/api/brackets/merge', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ sourceBracketId, targetBracketId, newName })
      });

      const data = await response.json();

      if (response.ok && data.success) {
        alert(`Brackets erfolgreich zusammengefhrt!\n${data.data.totalParticipants} Teilnehmer im neuen Bracket`);
        setSelectedBracket(null);
        setBracketDetails(null);
        await fetchBrackets();
      } else {
        throw new Error(data.message || 'Fehler beim Zusammenfhren');
      }
    } catch (err) {
      console.error('Fehler:', err);
      alert('Fehler: ' + err.message);
    }
  };

  const handleUpdateTournamentMode = async (bracketId, tournament_mode) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/brackets/${bracketId}/mode`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ tournament_mode })
      });

      const data = await response.json();

      if (response.ok && data.success) {
        // Kmpfe wurden neu generiert - alle Daten aktualisieren
        await fetchBracketDetails(bracketId);
        await fetchBrackets();

        // WICHTIG: selectedBracket mit neuem tournament_mode aktualisieren
        setSelectedBracket(prev => prev ? { ...prev, tournament_mode } : prev);
      } else {
        throw new Error(data.message || 'Fehler beim Aktualisieren');
      }
    } catch (err) {
      console.error('Fehler:', err);
      alert('Fehler: ' + err.message);
    }
  };

  // Bracket zurcksetzen (alle Kmpfe lschen, Teilnehmer behalten)
  const handleResetBracket = async (bracketId, bracketName) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/brackets/${bracketId}/reset`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ confirm: 'RESET' })
      });

      const data = await response.json();

      if (response.ok && data.success) {
        alert(`Bracket "${bracketName}" wurde zurckgesetzt!\n${data.data.deleted_kaempfe} Kmpfe gelscht.`);
        await fetchBracketDetails(bracketId);
        await fetchBrackets();
      } else {
        throw new Error(data.message || 'Fehler beim Zurcksetzen');
      }
    } catch (err) {
      console.error('Fehler:', err);
      alert('Fehler: ' + err.message);
    }
  };

  const handleMoveParticipant = async (wettkaempferId, sourceBracketId, targetBracketId) => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch('/api/brackets/move-participant', {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ wettkaempferId, sourceBracketId, targetBracketId })
      });

      const data = await response.json();

      if (response.ok && data.success) {
        alert('Teilnehmer erfolgreich verschoben!');
        await fetchBracketDetails(selectedBracket.bracket_id);
        await fetchBrackets();
      } else {
        throw new Error(data.message || 'Fehler beim Verschieben');
      }
    } catch (err) {
      console.error('Fehler:', err);
      alert('Fehler: ' + err.message);
    }
  };

  if (loading) {
    return (
      <div className="bracket-loading">
        <div className="loading-spinner"></div>
        <p>Brackets werden geladen...</p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bracket-error">
        <h3> Fehler</h3>
        <p>{error}</p>
        <button onClick={fetchBrackets}>Erneut versuchen</button>
      </div>
    );
  }

  return (
    <div className="bracket-dashboard">
      {!selectedBracket ? (
        <BracketOverview
          brackets={brackets}
          turniere={turniere}
          selectedTurnier={selectedTurnier}
          onSelectTurnier={setSelectedTurnier}
          onSelectBracket={(bracket) => {
            setSelectedBracket(bracket);
            fetchBracketDetails(bracket.bracket_id);
          }}
          onCreateBracket={() => setShowCreateModal(true)}
          onAutoGenerate={handleAutoGenerateBrackets}
          generating={generating}
        />
      ) : (
        <BracketDetailsView
          bracket={selectedBracket}
          details={bracketDetails}
          availableFighters={availableFighters}
          allBrackets={brackets}
          onBack={() => {
            setSelectedBracket(null);
            setBracketDetails(null);
          }}
          onAddParticipants={handleAddParticipants}
          onGenerateBracket={handleGenerateBracket}
          onUpdateFightResult={handleUpdateFightResult}
          onSplitBracket={handleSplitBracket}
          onMergeBrackets={handleMergeBrackets}
          onMoveParticipant={handleMoveParticipant}
          onUpdateTournamentMode={handleUpdateTournamentMode}
          onResetBracket={handleResetBracket}
        />
      )}

      {showCreateModal && (
        <CreateBracketModal
          onSave={handleCreateBracket}
          onClose={() => setShowCreateModal(false)}
        />
      )}
    </div>
  );
}

// Bracket Card Component - Ausklappbar mit Teilnehmer-Details
function BracketCard({ bracket, onSelect }) {
  const [expanded, setExpanded] = useState(false);
  const [teilnehmer, setTeilnehmer] = useState([]);
  const [loadingTeilnehmer, setLoadingTeilnehmer] = useState(false);

  const getStatusText = (status) => {
    switch (status) {
      case 'draft': case 'open': return 'Offen';
      case 'active': case 'ready': return 'Bereit';
      case 'running': return 'Luft';
      case 'completed': case 'finished': return 'Abgeschlossen';
      case 'cancelled': return 'Abgebrochen';
      default: return status || 'Unbekannt';
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'draft': case 'open': return '';
      case 'active': case 'ready': return '';
      case 'running': return '';
      case 'completed': case 'finished': return '';
      case 'cancelled': return '';
      default: return '';
    }
  };

  // Alter aus Geburtsdatum berechnen
  const calculateAge = (geburtsdatum) => {
    if (!geburtsdatum) return '-';
    const birthDate = new Date(geburtsdatum);
    const today = new Date();
    let age = today.getFullYear() - birthDate.getFullYear();
    const monthDiff = today.getMonth() - birthDate.getMonth();
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birthDate.getDate())) {
      age--;
    }
    return age;
  };

  // Geburtsdatum formatieren
  const formatDate = (dateString) => {
    if (!dateString) return '-';
    const date = new Date(dateString);
    return date.toLocaleDateString('de-DE', { day: '2-digit', month: '2-digit', year: 'numeric' });
  };

  // Teilnehmer laden beim Ausklappen
  const handleToggleExpand = async () => {
    if (!expanded && teilnehmer.length === 0) {
      setLoadingTeilnehmer(true);
      try {
        const token = localStorage.getItem('token');
        const response = await fetch(`/api/brackets/${bracket.bracket_id}`, {
          headers: { 'Authorization': `Bearer ${token}` }
        });
        if (response.ok) {
          const data = await response.json();
          const details = data.data || data;
          setTeilnehmer(details.teilnehmer || []);
        }
      } catch (err) {
        console.error('Fehler beim Laden der Teilnehmer:', err);
      } finally {
        setLoadingTeilnehmer(false);
      }
    }
    setExpanded(!expanded);
  };

  // Status kann bracket_status oder status heien
  const status = bracket.bracket_status || bracket.status;

  return (
    <div className={`bracket-card-expandable ${expanded ? 'expanded' : ''}`}>
      <div className="bracket-card-header" onClick={handleToggleExpand}>
        <div className="bracket-card-main">
          <div className="bracket-card-title">
            <span className="expand-icon">{expanded ? '' : ''}</span>
            <h4>{bracket.bracket_name}</h4>
          </div>
          <div className="bracket-card-meta">
            <span className="meta-item">
              {getStatusIcon(status)} {getStatusText(status)}
            </span>
            <span className="meta-item">
               {bracket.division_code}
            </span>
            <span className="meta-item">
               {bracket.current_participants || 0}/{bracket.max_participants}
            </span>
          </div>
        </div>
        <div className="bracket-card-actions">
          <button
            className="btn-view"
            onClick={(e) => {
              e.stopPropagation();
              onSelect();
            }}
          >
             Details
          </button>
        </div>
      </div>

      {expanded && (
        <div className="bracket-card-body">
          {loadingTeilnehmer ? (
            <div className="loading-teilnehmer"> Lade Teilnehmer...</div>
          ) : teilnehmer.length > 0 ? (
            <table className="teilnehmer-table">
              <thead>
                <tr>
                  <th>#</th>
                  <th>Name</th>
                  <th>Alter</th>
                  <th>Geburtsdatum</th>
                  <th>Gewicht</th>
                  <th>Verein</th>
                </tr>
              </thead>
              <tbody>
                {teilnehmer.map((t, index) => (
                  <tr key={t.wettkaempfer_id}>
                    <td className="seed-col">{t.seed_position || index + 1}</td>
                    <td className="name-col">{t.vorname} {t.nachname}</td>
                    <td className="age-col">{calculateAge(t.geburtsdatum)} Jahre</td>
                    <td className="date-col">{formatDate(t.geburtsdatum)}</td>
                    <td className="weight-col">{t.gewicht ? `${t.gewicht} kg` : '-'}</td>
                    <td className="verein-col">{t.verein_name || '-'}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          ) : (
            <div className="no-teilnehmer">Keine Teilnehmer vorhanden</div>
          )}
        </div>
      )}
    </div>
  );
}

// Bracket Overview Component
function BracketOverview({
  brackets,
  turniere,
  selectedTurnier,
  onSelectTurnier,
  onSelectBracket,
  onCreateBracket,
  onAutoGenerate,
  generating
}) {
  const getStatusColor = (status) => {
    switch (status) {
      case 'created': return '#fbbf24';
      case 'in_progress': return '#3b82f6';
      case 'completed': return '#10b981';
      default: return '#6b7280';
    }
  };

  const getStatusText = (status) => {
    switch (status) {
      case 'created': return 'Erstellt';
      case 'in_progress': return 'Luft';
      case 'completed': return 'Abgeschlossen';
      default: return 'Unbekannt';
    }
  };

  return (
    <div className="bracket-overview">
      <div className="overview-header">
        <h2> Bracket-Verwaltung</h2>
        <div className="header-controls">
          {turniere && turniere.length > 0 && (
            <select
              className="turnier-select"
              value={selectedTurnier?.turnier_id || ''}
              onChange={(e) => {
                const turnier = turniere.find(t => t.turnier_id === parseInt(e.target.value));
                onSelectTurnier(turnier);
              }}
              style={{ padding: '8px 12px', marginRight: '10px', borderRadius: '4px' }}
            >
              {turniere.map(t => (
                <option key={t.turnier_id} value={t.turnier_id}>
                  {t.name}
                </option>
              ))}
            </select>
          )}
          <button
            className="auto-generate-btn"
            onClick={onAutoGenerate}
            disabled={generating || !selectedTurnier}
            style={{
              padding: '8px 16px',
              marginRight: '10px',
              backgroundColor: '#10b981',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: generating ? 'wait' : 'pointer'
            }}
          >
            {generating ? ' Generiere...' : ' Brackets automatisch erstellen'}
          </button>
          <button className="create-bracket-btn" onClick={onCreateBracket}>
             Neues Bracket
          </button>
        </div>
      </div>

      <div className="tda-card-grid tda-card-grid-3">
        <TDACard
          title={brackets.length}
          subtitle="Brackets gesamt"
          meta={[{ icon: '', text: 'Gesamt' }]}
          size="small"
        />
        
        <TDACard
          title={brackets.filter(b => b.status === 'in_progress' || b.status === 'active').length}
          subtitle="Aktive Brackets"
          meta={[{ icon: '', text: 'Laufend' }]}
          size="small"
          variant="success"
        />
        
        <TDACard
          title={brackets.filter(b => b.status === 'completed').length}
          subtitle="Abgeschlossen"
          meta={[{ icon: '', text: 'Fertig' }]}
          size="small"
        />
      </div>

      <div className="brackets-list">
        {brackets.map(bracket => (
          <BracketCard
            key={bracket.bracket_id}
            bracket={bracket}
            onSelect={() => onSelectBracket(bracket)}
          />
        ))}
      </div>

      {brackets.length === 0 && (
        <div className="empty-state">
          <div className="empty-icon"></div>
          <h3>Noch keine Brackets vorhanden</h3>
          <p>Erstellen Sie Ihr erstes Turnier-Bracket</p>
          <button className="create-first-bracket" onClick={onCreateBracket}>
             Erstes Bracket erstellen
          </button>
        </div>
      )}
    </div>
  );
}

// Bracket Details View Component
function BracketDetailsView({
  bracket,
  details,
  availableFighters,
  allBrackets = [],
  onBack,
  onAddParticipants,
  onGenerateBracket,
  onUpdateFightResult,
  onSplitBracket,
  onMergeBrackets,
  onMoveParticipant,
  onUpdateTournamentMode,
  onResetBracket
}) {
  const [showAddParticipants, setShowAddParticipants] = useState(false);
  const [showFightResult, setShowFightResult] = useState(null);
  const [showSplitModal, setShowSplitModal] = useState(false);
  const [showMergeModal, setShowMergeModal] = useState(false);
  const [showMoveModal, setShowMoveModal] = useState(null); // Contains participant to move
  const [showResetModal, setShowResetModal] = useState(false); // Reset confirmation modal
  const [resetting, setResetting] = useState(false);
  const [splitting, setSplitting] = useState(false);
  const [merging, setMerging] = useState(false);
  const [mergeSelectedBracket, setMergeSelectedBracket] = useState(null);
  const [mergeNewName, setMergeNewName] = useState('');
  // Bevorzuge details.bracket.tournament_mode ber bracket.tournament_mode
  const [tournamentMode, setTournamentMode] = useState(
    details?.bracket?.tournament_mode || bracket?.tournament_mode || 'single_elimination_bronze'
  );
  const [showModeHelpModal, setShowModeHelpModal] = useState(false);

  // Update tournament mode when bracket data loads/changes
  React.useEffect(() => {
    if (details?.bracket?.tournament_mode) {
      setTournamentMode(details.bracket.tournament_mode);
    }
  }, [details?.bracket?.tournament_mode]);

  // Auch aktualisieren wenn bracket prop sich ndert
  React.useEffect(() => {
    if (bracket?.tournament_mode) {
      setTournamentMode(bracket.tournament_mode);
    }
  }, [bracket?.tournament_mode]);

  // Turniermodi mit ausfhrlichen Beschreibungen
  const tournamentModes = [
    {
      value: 'single_elimination',
      label: 'Single Elimination (KO)',
      description: 'Klassisches K.O.-System: Wer verliert, scheidet sofort aus.',
      details: 'Schnellster Modus. 1 Sieger, alle anderen ohne Platzierung. Ideal fr groe Teilnehmerfelder mit wenig Zeit.'
    },
    {
      value: 'single_elimination_bronze',
      label: 'KO + Bronze-Kampf',
      description: 'K.O.-System mit zustzlichem Kampf um Platz 3 zwischen den Halbfinal-Verlierern.',
      details: 'Standard bei vielen Verbnden (WAKO, WKF). Vergibt Platz 1, 2, 3 und 4.'
    },
    {
      value: 'single_elimination_double_bronze',
      label: 'KO + Doppel-Bronze',
      description: 'K.O.-System ohne kleines Finale - beide Halbfinal-Verlierer erhalten Bronze.',
      details: 'Zeitsparend, da kein Bronze-Kampf ntig. Zwei 3. Pltze werden vergeben.'
    },
    {
      value: 'consolation_bracket',
      label: 'Trostrunden-Bracket',
      description: 'Alle Verlierer kmpfen in einem separaten Bracket weiter um Platz 3.',
      details: 'Verlierer aus jeder Runde kommen in ein eigenes Trostrunden-Bracket. Der Sieger dort erhlt Bronze.'
    },
    {
      value: 'double_elimination',
      label: 'Double Elimination',
      description: 'Erst nach zwei Niederlagen ausgeschieden. Verlierer kmpfen im Losers-Bracket weiter.',
      details: 'Fairster Modus - eine schlechte Tagesform kostet nicht sofort das Turnier. Zwei 3. Pltze.'
    },
    {
      value: 'repechage',
      label: 'Repechage (Olympisch)',
      description: 'Verlierer gegen Finalisten bekommen eine zweite Chance auf Bronze.',
      details: 'Olympischer Standard (Judo, Taekwondo, Ringen). Wer gegen einen Finalisten verlor, kmpft im Trostrunden-Bracket.'
    },
    {
      value: 'round_robin',
      label: 'Round Robin',
      description: 'Jeder kmpft gegen jeden. Platzierung nach Punkten.',
      details: 'Ideal fr kleine Gruppen (3-6 Teilnehmer). Fairste Bewertung, aber zeitintensiv.'
    }
  ];

  // Filter other brackets for merge/move (same tournament, not this bracket)
  const otherBrackets = allBrackets.filter(b =>
    b.bracket_id !== bracket.bracket_id &&
    b.turnier_id === bracket.turnier_id
  );

  if (!details) {
    return (
      <div className="loading-details">
        <div className="loading-spinner"></div>
        <p>Bracket-Details werden geladen...</p>
      </div>
    );
  }

  const teilnehmer = details.teilnehmer || [];
  const kaempfe = details.kaempfe || [];

  return (
    <div className="bracket-details" style={{ padding: 0 }}>
      {/* Kompakter Header im Turnierbaum-Stil */}
      <div style={{
        background: 'linear-gradient(180deg, #1a1510 0%, #0d0a07 100%)',
        border: '2px solid #3d2d1f',
        borderRadius: '4px',
        padding: '12px 16px',
        marginBottom: '8px'
      }}>
        <div style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          flexWrap: 'wrap',
          gap: '10px'
        }}>
          {/* Links: Zurck + Titel */}
          <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
            <button
              onClick={onBack}
              style={{
                background: 'transparent',
                border: '1px solid #3d2d1f',
                color: '#c4b89a',
                padding: '6px 12px',
                borderRadius: '4px',
                cursor: 'pointer',
                fontSize: '0.85rem'
              }}
            >
               Zurck
            </button>
            <div>
              <h2 style={{
                margin: 0,
                fontSize: '1.1rem',
                color: '#d4af37',
                fontWeight: 600
              }}>{bracket.bracket_name}</h2>
              <span style={{
                fontSize: '0.75rem',
                color: '#ff6b35',
                background: 'rgba(255,107,53,0.15)',
                padding: '2px 8px',
                borderRadius: '3px',
                textTransform: 'uppercase',
                fontWeight: 600
              }}>
                {teilnehmer.length} Teilnehmer
              </span>
            </div>

            {/* Turniermodus Dropdown mit Hilfe */}
            <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
              <label style={{ color: '#888', fontSize: '0.8rem' }}>Modus:</label>
              <select
                value={tournamentMode}
                onChange={(e) => {
                  const newMode = e.target.value;
                  setTournamentMode(newMode);
                  if (onUpdateTournamentMode) {
                    onUpdateTournamentMode(bracket.bracket_id, newMode);
                  }
                }}
                style={{
                  background: '#1a1510',
                  color: '#d4af37',
                  border: '1px solid #3d2d1f',
                  padding: '6px 10px',
                  borderRadius: '4px',
                  fontSize: '0.8rem',
                  cursor: 'pointer'
                }}
              >
                {tournamentModes.map(mode => (
                  <option key={mode.value} value={mode.value}>
                    {mode.label}
                  </option>
                ))}
              </select>
              <button
                onClick={() => setShowModeHelpModal(true)}
                title="Hilfe zu Turniermodi"
                style={{
                  background: 'transparent',
                  border: '1px solid #3d2d1f',
                  color: '#d4af37',
                  width: '24px',
                  height: '24px',
                  borderRadius: '50%',
                  cursor: 'pointer',
                  fontSize: '0.8rem',
                  fontWeight: 'bold',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center'
                }}
              >
                ?
              </button>
            </div>
          </div>

          {/* Rechts: Action Buttons */}
          <div style={{ display: 'flex', alignItems: 'center', gap: '6px', flexWrap: 'wrap' }}>
            <button
              onClick={() => setShowAddParticipants(true)}
              style={{
                background: 'rgba(42,90,42,0.3)',
                color: '#6adb6a',
                border: '1px solid #3a7a3a',
                padding: '6px 10px',
                borderRadius: '4px',
                cursor: 'pointer',
                fontSize: '0.8rem'
              }}
            >
              + Teilnehmer
            </button>

            {teilnehmer.length >= 4 && (
              <button
                onClick={() => setShowSplitModal(true)}
                style={{
                  background: 'rgba(139,69,19,0.3)',
                  color: '#cd7f32',
                  border: '1px solid #8B4513',
                  padding: '6px 10px',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  fontSize: '0.8rem'
                }}
              >
                Splitten
              </button>
            )}

            {otherBrackets.length > 0 && (
              <button
                onClick={() => setShowMergeModal(true)}
                style={{
                  background: 'rgba(74,85,104,0.3)',
                  color: '#a0aec0',
                  border: '1px solid #4a5568',
                  padding: '6px 10px',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  fontSize: '0.8rem'
                }}
              >
                Zusammenfhren
              </button>
            )}

            {teilnehmer.length > 0 && kaempfe.length === 0 && (
              <button
                onClick={() => onGenerateBracket(bracket.bracket_id)}
                style={{
                  background: 'linear-gradient(180deg, #8B0000 0%, #5c0000 100%)',
                  color: '#fff',
                  border: '1px solid #d4af37',
                  padding: '6px 12px',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  fontSize: '0.8rem',
                  fontWeight: 600
                }}
              >
                Kmpfe generieren
              </button>
            )}

            {/* Reset Button - nur anzeigen wenn Kmpfe existieren */}
            {kaempfe.length > 0 && (
              <button
                onClick={() => setShowResetModal(true)}
                style={{
                  background: 'rgba(139,0,0,0.3)',
                  color: '#ff6b6b',
                  border: '1px solid #8B0000',
                  padding: '6px 10px',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  fontSize: '0.8rem'
                }}
                title="Alle Kmpfe lschen und Bracket zurcksetzen"
              >
                Reset
              </button>
            )}
          </div>
        </div>
      </div>

      {/* Turnierbaum anzeigen wenn Kmpfe existieren */}
      {kaempfe.length > 0 && (
        <TournamentBracketTree
          kaempfe={kaempfe}
          teilnehmer={teilnehmer}
          onFightClick={(fight) => setShowFightResult(fight)}
          tournamentMode={tournamentMode}
        />
      )}

      {/* Teilnehmer-Liste wenn noch keine Kmpfe */}
      {teilnehmer.length > 0 && kaempfe.length === 0 && (
        <div className="participants-section">
          <h3> Teilnehmer ({teilnehmer.length})</h3>
          <div className="participants-list">
            {teilnehmer.map((t, index) => (
              <div key={t.wettkaempfer_id} className="participant-item" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                  <span className="seed-number">#{t.seed_position || index + 1}</span>
                  <span className="participant-name">
                    {t.vorname} {t.nachname}
                  </span>
                  <span className="participant-info">
                    {t.skill_level}  {t.gewicht}kg
                  </span>
                </div>
                {otherBrackets.length > 0 && (
                  <button
                    onClick={() => setShowMoveModal(t)}
                    style={{
                      background: 'transparent',
                      border: '1px solid rgba(255,215,0,0.3)',
                      color: 'rgba(255,255,255,0.7)',
                      padding: '4px 8px',
                      borderRadius: '4px',
                      cursor: 'pointer',
                      fontSize: '0.8rem'
                    }}
                  >
                     Verschieben
                  </button>
                )}
              </div>
            ))}
          </div>
        </div>
      )}

      {showAddParticipants && (
        <AddParticipantsModal
          availableFighters={availableFighters}
          bracket={bracket}
          existingParticipants={teilnehmer}
          maxParticipants={bracket.max_participants}
          onSave={(participants) => {
            onAddParticipants(bracket.bracket_id, participants);
            setShowAddParticipants(false);
          }}
          onClose={() => setShowAddParticipants(false)}
        />
      )}

      {showFightResult && (
        <FightDetailView
          fight={showFightResult}
          participants={teilnehmer}
          bracket={bracket}
          allFights={kaempfe}
          onSave={(result) => {
            onUpdateFightResult(showFightResult.kampf_id, result);
            setShowFightResult(null);
          }}
          onClose={() => setShowFightResult(null)}
          onSwapFighters={async (kampfId, swap) => {
            // API zum Tauschen der Kmpfer
            try {
              const token = localStorage.getItem('token');
              await fetch(`/api/brackets/kampf/${kampfId}/swap`, {
                method: 'PUT',
                headers: {
                  'Authorization': `Bearer ${token}`,
                  'Content-Type': 'application/json'
                }
              });
            } catch (err) {
              console.error('Fehler beim Tauschen:', err);
            }
          }}
          onScheduleTime={async (kampfId, time) => {
            // API zum Planen der Zeit
            try {
              const token = localStorage.getItem('token');
              await fetch(`/api/brackets/kampf/${kampfId}/schedule`, {
                method: 'PUT',
                headers: {
                  'Authorization': `Bearer ${token}`,
                  'Content-Type': 'application/json'
                },
                body: JSON.stringify({ geplante_zeit: time })
              });
            } catch (err) {
              console.error('Fehler beim Planen:', err);
            }
          }}
        />
      )}

      {/* Split Division Modal */}
      {showSplitModal && (
        <div className="modal-overlay">
          <div className="modal-content" style={{ maxWidth: '450px' }}>
            <div className="modal-header">
              <h3>Division splitten</h3>
              <button className="close-btn" onClick={() => setShowSplitModal(false)}></button>
            </div>
            <div style={{ padding: '1.5rem' }}>
              <p style={{ color: 'rgba(255,255,255,0.7)', marginBottom: '20px', textAlign: 'center' }}>
                {teilnehmer.length} Teilnehmer in 2 Gruppen aufteilen:
              </p>

              <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                <button
                  onClick={async () => {
                    setSplitting(true);
                    await onSplitBracket(bracket.bracket_id, 'half');
                    setSplitting(false);
                    setShowSplitModal(false);
                  }}
                  disabled={splitting}
                  className="split-option-btn"
                  style={{
                    padding: '14px', background: 'rgba(139,0,0,0.2)', border: '1px solid rgba(255,215,0,0.3)',
                    color: 'rgba(255,255,255,0.9)', borderRadius: '8px', cursor: 'pointer', textAlign: 'left'
                  }}
                >
                  <strong>Halb / Halb</strong><br/>
                  <small style={{ color: 'rgba(255,255,255,0.6)' }}>50/50 nach Seeding-Position</small>
                </button>

                <button
                  onClick={async () => {
                    setSplitting(true);
                    await onSplitBracket(bracket.bracket_id, 'gender');
                    setSplitting(false);
                    setShowSplitModal(false);
                  }}
                  disabled={splitting}
                  className="split-option-btn"
                  style={{
                    padding: '14px', background: 'rgba(139,0,0,0.2)', border: '1px solid rgba(255,215,0,0.3)',
                    color: 'rgba(255,255,255,0.9)', borderRadius: '8px', cursor: 'pointer', textAlign: 'left'
                  }}
                >
                  <strong>Nach Geschlecht</strong><br/>
                  <small style={{ color: 'rgba(255,255,255,0.6)' }}>Mnnlich vs Weiblich</small>
                </button>

                <button
                  onClick={async () => {
                    setSplitting(true);
                    await onSplitBracket(bracket.bracket_id, 'weight');
                    setSplitting(false);
                    setShowSplitModal(false);
                  }}
                  disabled={splitting}
                  className="split-option-btn"
                  style={{
                    padding: '14px', background: 'rgba(139,0,0,0.2)', border: '1px solid rgba(255,215,0,0.3)',
                    color: 'rgba(255,255,255,0.9)', borderRadius: '8px', cursor: 'pointer', textAlign: 'left'
                  }}
                >
                  <strong>Nach Gewicht</strong><br/>
                  <small style={{ color: 'rgba(255,255,255,0.6)' }}>Leichtere vs Schwerere</small>
                </button>

                <button
                  onClick={async () => {
                    setSplitting(true);
                    await onSplitBracket(bracket.bracket_id, 'height');
                    setSplitting(false);
                    setShowSplitModal(false);
                  }}
                  disabled={splitting}
                  className="split-option-btn"
                  style={{
                    padding: '14px', background: 'rgba(139,0,0,0.2)', border: '1px solid rgba(255,215,0,0.3)',
                    color: 'rgba(255,255,255,0.9)', borderRadius: '8px', cursor: 'pointer', textAlign: 'left'
                  }}
                >
                  <strong>Nach Gre</strong><br/>
                  <small style={{ color: 'rgba(255,255,255,0.6)' }}>Kleinere vs Grere</small>
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Merge Brackets Modal */}
      {showMergeModal && (
        <div className="modal-overlay">
          <div className="modal-content" style={{ maxWidth: '500px' }}>
            <div className="modal-header">
              <h3>Brackets zusammenfhren</h3>
              <button className="close-btn" onClick={() => {
                setShowMergeModal(false);
                setMergeSelectedBracket(null);
                setMergeNewName('');
              }}></button>
            </div>
            <div style={{ padding: '1.5rem' }}>
              {!mergeSelectedBracket ? (
                <>
                  <p style={{ color: 'rgba(255,255,255,0.7)', marginBottom: '20px' }}>
                    Whlen Sie ein Bracket, dessen Teilnehmer in <strong style={{ color: '#d4af37' }}>{bracket.bracket_name}</strong> bernommen werden sollen:
                  </p>

                  <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                    {otherBrackets.map(b => (
                      <button
                        key={b.bracket_id}
                        onClick={() => {
                          setMergeSelectedBracket(b);
                          // Bereinigten Namen als Vorschlag setzen
                          const cleanName = bracket.bracket_name
                            .replace(/\s*\([AB]\s*-\s*(Halb\/Halb|Nach Geschlecht|Nach Gewicht|Nach Gre)\)\s*$/i, '')
                            .trim();
                          setMergeNewName(cleanName);
                        }}
                        style={{
                          padding: '14px', background: 'rgba(74,85,104,0.3)', border: '1px solid rgba(255,215,0,0.3)',
                          color: 'rgba(255,255,255,0.9)', borderRadius: '8px', cursor: 'pointer', textAlign: 'left'
                        }}
                      >
                        <strong>{b.bracket_name}</strong>
                        <div style={{ fontSize: '0.85rem', color: 'rgba(255,255,255,0.6)', marginTop: '4px' }}>
                          {b.current_participants || 0} Teilnehmer  {b.division_code || 'Keine Kategorie'}
                        </div>
                      </button>
                    ))}
                  </div>

                  {otherBrackets.length === 0 && (
                    <p style={{ color: 'rgba(255,255,255,0.5)', textAlign: 'center' }}>
                      Keine anderen Brackets im selben Turnier vorhanden.
                    </p>
                  )}
                </>
              ) : (
                <>
                  <p style={{ color: 'rgba(255,255,255,0.7)', marginBottom: '15px' }}>
                    <strong style={{ color: '#d4af37' }}>{mergeSelectedBracket.bracket_name}</strong> ({mergeSelectedBracket.current_participants || 0} Teilnehmer) wird mit diesem Bracket zusammengefhrt.
                  </p>

                  <div style={{ marginBottom: '20px' }}>
                    <label style={{ display: 'block', color: 'rgba(255,255,255,0.8)', marginBottom: '8px', fontSize: '0.9rem' }}>
                      Neuer Name fr das zusammengefhrte Bracket (turnierspezifisch):
                    </label>
                    <input
                      type="text"
                      value={mergeNewName}
                      onChange={(e) => setMergeNewName(e.target.value)}
                      placeholder="z.B. Continuous Fighting 6-12 Jahre"
                      style={{
                        width: '100%',
                        padding: '10px 12px',
                        background: 'rgba(0,0,0,0.3)',
                        border: '1px solid rgba(255,215,0,0.3)',
                        borderRadius: '6px',
                        color: '#fff',
                        fontSize: '0.95rem'
                      }}
                    />
                    <small style={{ color: 'rgba(255,255,255,0.5)', display: 'block', marginTop: '6px' }}>
                      Dieser Name gilt nur fr dieses Turnier.
                    </small>
                  </div>

                  <div style={{ display: 'flex', gap: '10px' }}>
                    <button
                      onClick={() => {
                        setMergeSelectedBracket(null);
                        setMergeNewName('');
                      }}
                      style={{
                        flex: 1,
                        padding: '12px',
                        background: 'transparent',
                        border: '1px solid rgba(255,255,255,0.3)',
                        color: 'rgba(255,255,255,0.7)',
                        borderRadius: '6px',
                        cursor: 'pointer'
                      }}
                    >
                      Zurck
                    </button>
                    <button
                      onClick={async () => {
                        setMerging(true);
                        await onMergeBrackets(mergeSelectedBracket.bracket_id, bracket.bracket_id, mergeNewName || null);
                        setMerging(false);
                        setShowMergeModal(false);
                        setMergeSelectedBracket(null);
                        setMergeNewName('');
                      }}
                      disabled={merging}
                      style={{
                        flex: 2,
                        padding: '12px',
                        background: 'linear-gradient(180deg, #8B0000 0%, #5c0000 100%)',
                        border: '1px solid #d4af37',
                        color: '#fff',
                        borderRadius: '6px',
                        cursor: 'pointer',
                        fontWeight: 600
                      }}
                    >
                      {merging ? 'Wird zusammengefhrt...' : 'Zusammenfhren'}
                    </button>
                  </div>
                </>
              )}
            </div>
          </div>
        </div>
      )}

      {/* Move Participant Modal */}
      {showMoveModal && (
        <div className="modal-overlay">
          <div className="modal-content" style={{ maxWidth: '500px' }}>
            <div className="modal-header">
              <h3>Teilnehmer verschieben</h3>
              <button className="close-btn" onClick={() => setShowMoveModal(null)}></button>
            </div>
            <div style={{ padding: '1.5rem' }}>
              <p style={{ color: 'rgba(255,255,255,0.7)', marginBottom: '20px' }}>
                <strong style={{ color: '#d4af37' }}>{showMoveModal.vorname} {showMoveModal.nachname}</strong> verschieben nach:
              </p>

              <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                {otherBrackets.map(b => (
                  <button
                    key={b.bracket_id}
                    onClick={async () => {
                      await onMoveParticipant(showMoveModal.wettkaempfer_id, bracket.bracket_id, b.bracket_id);
                      setShowMoveModal(null);
                    }}
                    style={{
                      padding: '14px', background: 'rgba(74,85,104,0.3)', border: '1px solid rgba(255,215,0,0.3)',
                      color: 'rgba(255,255,255,0.9)', borderRadius: '8px', cursor: 'pointer', textAlign: 'left'
                    }}
                  >
                    <strong>{b.bracket_name}</strong>
                    <div style={{ fontSize: '0.85rem', color: 'rgba(255,255,255,0.6)', marginTop: '4px' }}>
                      {b.current_participants || 0} Teilnehmer  {b.division_code || 'Keine Kategorie'}
                    </div>
                  </button>
                ))}
              </div>

              {otherBrackets.length === 0 && (
                <p style={{ color: 'rgba(255,255,255,0.5)', textAlign: 'center' }}>
                  Keine anderen Brackets im selben Turnier vorhanden.
                </p>
              )}
            </div>
          </div>
        </div>
      )}

      {/* Turniermodus Hilfe-Modal */}
      {showModeHelpModal && (
        <div className="modal-overlay">
          <div className="modal-content" style={{ maxWidth: '600px' }}>
            <div className="modal-header">
              <h3>Turniermodi erklrt</h3>
              <button className="close-btn" onClick={() => setShowModeHelpModal(false)}></button>
            </div>
            <div style={{ padding: '1.5rem' }}>
              <div style={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
                {tournamentModes.map(mode => (
                  <div
                    key={mode.value}
                    style={{
                      background: tournamentMode === mode.value ? 'rgba(139,0,0,0.2)' : 'rgba(0,0,0,0.2)',
                      border: tournamentMode === mode.value ? '1px solid #d4af37' : '1px solid rgba(255,255,255,0.1)',
                      borderRadius: '8px',
                      padding: '14px'
                    }}
                  >
                    <div style={{ display: 'flex', alignItems: 'center', gap: '10px', marginBottom: '6px' }}>
                      <strong style={{ color: '#d4af37', fontSize: '0.95rem' }}>{mode.label}</strong>
                      {tournamentMode === mode.value && (
                        <span style={{
                          background: '#8B0000',
                          color: '#fff',
                          fontSize: '0.65rem',
                          padding: '2px 6px',
                          borderRadius: '3px'
                        }}>AKTUELL</span>
                      )}
                    </div>
                    <p style={{ color: 'rgba(255,255,255,0.7)', margin: 0, fontSize: '0.85rem', lineHeight: 1.5 }}>
                      {mode.description}
                    </p>
                    {mode.details && (
                      <p style={{ color: 'rgba(255,255,255,0.5)', margin: '8px 0 0 0', fontSize: '0.8rem', fontStyle: 'italic' }}>
                        {mode.details}
                      </p>
                    )}
                  </div>
                ))}
              </div>

              <div style={{
                marginTop: '20px',
                padding: '12px',
                background: 'rgba(212,175,55,0.1)',
                borderRadius: '6px',
                border: '1px solid rgba(212,175,55,0.3)'
              }}>
                <p style={{ color: '#d4af37', margin: 0, fontSize: '0.85rem' }}>
                  <strong>Hinweis:</strong> Der Standard-Turniermodus kann in den Turniereinstellungen festgelegt werden.
                  Hier kann er fr diese Kategorie individuell angepasst werden.
                </p>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Reset Besttigungs-Modal */}
      {showResetModal && (
        <div className="modal-overlay">
          <div className="modal-content" style={{ maxWidth: '450px' }}>
            <div className="modal-header" style={{ background: 'linear-gradient(180deg, #8B0000 0%, #5c0000 100%)' }}>
              <h3 style={{ color: '#fff' }}>Bracket zurcksetzen</h3>
              <button className="close-btn" onClick={() => setShowResetModal(false)}></button>
            </div>
            <div style={{ padding: '1.5rem' }}>
              <div style={{
                background: 'rgba(139,0,0,0.2)',
                border: '1px solid #8B0000',
                borderRadius: '8px',
                padding: '16px',
                marginBottom: '20px'
              }}>
                <p style={{ color: '#ff6b6b', margin: 0, fontSize: '0.95rem', fontWeight: 600 }}>
                   WARNUNG: Diese Aktion kann nicht rckgngig gemacht werden!
                </p>
              </div>

              <p style={{ color: 'rgba(255,255,255,0.8)', marginBottom: '16px' }}>
                Mchten Sie das Bracket <strong style={{ color: '#d4af37' }}>"{bracket.bracket_name}"</strong> wirklich zurcksetzen?
              </p>

              <p style={{ color: 'rgba(255,255,255,0.6)', fontSize: '0.9rem', marginBottom: '20px' }}>
                Folgendes wird gelscht:
              </p>
              <ul style={{ color: 'rgba(255,255,255,0.6)', fontSize: '0.85rem', paddingLeft: '20px', margin: '0 0 20px 0' }}>
                <li>Alle {kaempfe.length} Kmpfe</li>
                <li>Alle Ergebnisse und Punkte</li>
                <li>Alle Backup-Daten</li>
              </ul>

              <p style={{ color: 'rgba(255,255,255,0.6)', fontSize: '0.85rem' }}>
                <strong style={{ color: '#6adb6a' }}> Teilnehmer bleiben erhalten</strong> und knnen neu zugewiesen werden.
              </p>

              <div style={{ display: 'flex', gap: '12px', marginTop: '24px', justifyContent: 'flex-end' }}>
                <button
                  onClick={() => setShowResetModal(false)}
                  style={{
                    background: 'rgba(100,100,100,0.3)',
                    color: '#ccc',
                    border: '1px solid #555',
                    padding: '10px 20px',
                    borderRadius: '4px',
                    cursor: 'pointer',
                    fontSize: '0.9rem'
                  }}
                >
                  Abbrechen
                </button>
                <button
                  onClick={async () => {
                    setResetting(true);
                    await onResetBracket(bracket.bracket_id, bracket.bracket_name);
                    setResetting(false);
                    setShowResetModal(false);
                  }}
                  disabled={resetting}
                  style={{
                    background: resetting ? '#555' : 'linear-gradient(180deg, #8B0000 0%, #5c0000 100%)',
                    color: '#fff',
                    border: '1px solid #8B0000',
                    padding: '10px 20px',
                    borderRadius: '4px',
                    cursor: resetting ? 'wait' : 'pointer',
                    fontSize: '0.9rem',
                    fontWeight: 600
                  }}
                >
                  {resetting ? 'Wird zurckgesetzt...' : 'Ja, zurcksetzen'}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

// Turnierbaum - Japanese Traditional Style ()
function TournamentBracketTree({ kaempfe, teilnehmer, onFightClick, showLosersBracket = true, tournamentMode = 'single_elimination_bronze' }) {
  const getFighterName = (fighterId) => {
    if (!fighterId) return '';
    const fighter = teilnehmer.find(t => t.wettkaempfer_id === fighterId);
    return fighter ? `${fighter.vorname} ${fighter.nachname}` : '';
  };

  // Separate winners bracket from losers bracket by bracket_typ
  const winnersKaempfe = kaempfe.filter(k => k.bracket_typ === 'winners' || !k.bracket_typ);
  const losersKaempfe = kaempfe.filter(k => k.bracket_typ === 'losers');
  const kleinesFinale = losersKaempfe.find(k => k.ist_trostrunde);

  // Bestimme ob Verlierer-Bracket angezeigt werden soll basierend auf Modus
  const shouldShowLosersBracket = () => {
    switch (tournamentMode) {
      case 'single_elimination':
        return false; // Kein Verlierer-Bracket
      case 'single_elimination_bronze':
        return true; // Nur kleines Finale zeigen
      case 'single_elimination_double_bronze':
        return false; // Kein Verlierer-Bracket, beide bekommen Bronze
      case 'consolation_bracket':
        return true; // Separates Trostrunden-Bracket
      case 'double_elimination':
        return true; // Volles Losers Bracket
      case 'repechage':
        return true; // Repechage Brackets
      case 'round_robin':
        return false; // Kein Bracket-System
      default:
        return true;
    }
  };

  // Bestimme den Titel fr das Verlierer-Bracket
  const getLosersBracketTitle = () => {
    switch (tournamentMode) {
      case 'single_elimination_bronze':
        return '  KAMPF UM PLATZ 3';
      case 'consolation_bracket':
        return '  TROSTRUNDEN-BRACKET';
      case 'double_elimination':
        return '  VERLIERER-BRACKET';
      case 'repechage':
        return '  REPECHAGE';
      default:
        return '  VERLIERER-BRACKET';
    }
  };

  // Kmpfe nach Runden gruppieren
  const groupByRound = (kaempfeList) => {
    const rounds = {};
    kaempfeList.forEach(k => {
      const runde = k.runde || 1;
      if (!rounds[runde]) rounds[runde] = [];
      rounds[runde].push(k);
    });
    return rounds;
  };

  const rounds = groupByRound(winnersKaempfe);
  const losersRounds = groupByRound(losersKaempfe.filter(k => !k.ist_trostrunde));
  // Trostrunden-Kmpfe fr consolation_bracket Modus (alle Kmpfe mit ist_trostrunde: true)
  const trostrundenKaempfe = losersKaempfe.filter(k => k.ist_trostrunde && k.kampf_nummer !== 99);
  const trostrundenRounds = groupByRound(trostrundenKaempfe);
  const trostrundenRoundNumbers = Object.keys(trostrundenRounds).map(Number).sort((a, b) => a - b);
  const roundNumbers = Object.keys(rounds).map(Number).sort((a, b) => a - b);
  const losersRoundNumbers = Object.keys(losersRounds).map(Number).sort((a, b) => a - b);

  // Detect Vorrunde: Round 1 has fewer fights than Round 2
  const hasVorrunde = roundNumbers.length > 1 &&
    rounds[roundNumbers[0]]?.length < rounds[roundNumbers[1]]?.length;

  const vorrundeKaempfe = hasVorrunde ? rounds[roundNumbers[0]] : [];
  const mainRounds = hasVorrunde ? roundNumbers.slice(1) : roundNumbers;
  const totalMainRounds = mainRounds.length;

  // For display: merge Vorrunde into first main round
  const firstMainRound = hasVorrunde ? roundNumbers[1] : roundNumbers[0];
  const firstRoundKaempfe = rounds[firstMainRound] || [];

  const finaleRound = roundNumbers[roundNumbers.length - 1];
  const finaleKampf = winnersKaempfe.find(k => k.runde === finaleRound);
  const champion = finaleKampf?.gewinner_id ? getFighterName(finaleKampf.gewinner_id) : null;

  // Find which main bracket positions receive Vorrunde winners
  const vorrundeTargetPositions = new Set();
  firstRoundKaempfe.forEach((k, idx) => {
    if (!k.kaempfer1_id || !k.kaempfer2_id) {
      vorrundeTargetPositions.add(idx);
    }
  });

  const getRoundName = (roundNum, isFirst = false) => {
    const mainRoundIndex = mainRounds.indexOf(roundNum);
    const roundsFromEnd = totalMainRounds - mainRoundIndex;
    if (roundsFromEnd === 1) return ' FINALE';
    if (roundsFromEnd === 2) return ' HALBFINALE';
    if (roundsFromEnd === 3) return '';
    if (isFirst && hasVorrunde) return '1 + ';
    if (roundsFromEnd === 4) return '1';
    return `${mainRoundIndex + 1}`;
  };

  // Match height und spacing
  const MATCH_HEIGHT = 70;
  const CONNECTOR_WIDTH = 40;

  return (
    <div style={{
      background: 'linear-gradient(180deg, #1a1510 0%, #0d0a07 100%)',
      borderRadius: '4px',
      border: '2px solid #3d2d1f',
      boxShadow: 'inset 0 0 60px rgba(0,0,0,0.5)'
    }}>
      {/* Kompakter Header - Traditional Japanese Style */}
      <div style={{
        background: 'linear-gradient(180deg, #8B0000 0%, #5c0000 100%)',
        padding: '0.6rem 1.5rem',
        textAlign: 'center',
        borderBottom: '2px solid #d4af37',
        position: 'relative'
      }}>
        {/* Decorative corners - kleiner */}
        <div style={{ position: 'absolute', top: '4px', left: '4px', width: '12px', height: '12px', borderTop: '2px solid #d4af37', borderLeft: '2px solid #d4af37' }}></div>
        <div style={{ position: 'absolute', top: '4px', right: '4px', width: '12px', height: '12px', borderTop: '2px solid #d4af37', borderRight: '2px solid #d4af37' }}></div>
        <div style={{ position: 'absolute', bottom: '4px', left: '4px', width: '12px', height: '12px', borderBottom: '2px solid #d4af37', borderLeft: '2px solid #d4af37' }}></div>
        <div style={{ position: 'absolute', bottom: '4px', right: '4px', width: '12px', height: '12px', borderBottom: '2px solid #d4af37', borderRight: '2px solid #d4af37' }}></div>

        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', gap: '12px' }}>
          <span style={{ fontSize: '0.6rem', letterSpacing: '4px', color: '#d4af37', fontWeight: 500 }}>TOURNAMENT</span>
          <h1 style={{
            margin: 0,
            fontSize: '1.2rem',
            fontWeight: 700,
            color: '#fff',
            letterSpacing: '3px'
          }}>
            
          </h1>
          <span style={{ fontSize: '0.6rem', color: 'rgba(255,255,255,0.7)', letterSpacing: '2px' }}>TURNIERBAUM</span>
        </div>
      </div>

      {/* Bracket Area - weniger Padding */}
      <div style={{
        padding: '1rem',
        overflow: 'auto',
        position: 'relative'
      }}>
        {(() => {
          const displayRounds = hasVorrunde ? mainRounds : roundNumbers;

          // Calculate SVG height based on ALL matches in first display round
          const firstDisplayRound = displayRounds[0];
          const firstRoundMatches = firstDisplayRound ? (rounds[firstDisplayRound] || []) : [];
          const firstRoundMatchCount = firstRoundMatches.length || 1;

          // Spacing for first round: 2^1 * (MATCH_HEIGHT/2 + 10)
          const baseSpacing = Math.pow(2, 1) * (MATCH_HEIGHT / 2 + 10);

          // Calculate actual maximum y position that will be rendered
          const startOffsetCalc = (baseSpacing - MATCH_HEIGHT) / 2;

          // Calculate the Y position of the LAST match in the first round
          // yOffset formula: startOffset + matchIdx * spacing + 40
          const lastMatchIdx = firstRoundMatchCount - 1;
          const lastMatchY = startOffsetCalc + lastMatchIdx * baseSpacing + 40 + MATCH_HEIGHT;

          // Calculate based on maximum possible matches (half of participants)
          const maxFirstRoundMatches = Math.ceil(teilnehmer.length / 2);
          const calculatedHeight = maxFirstRoundMatches * baseSpacing + 300;

          // Use the LARGEST of all calculations to ensure nothing is cut off
          // Double the calculated heights to be safe
          const svgHeight = Math.max(
            (lastMatchY + 200) * 1.2,  // Based on actual last match position + 20%
            firstRoundMatchCount * baseSpacing * 1.5 + 300,  // Based on displayed match count
            calculatedHeight * 1.3, // Based on participant count + 30%
            maxFirstRoundMatches * 120 + 300, // 120px per match
            1000 // Minimum height increased
          );

          console.log('SVG Height Debug:', {
            firstRoundMatchCount,
            maxFirstRoundMatches,
            baseSpacing,
            lastMatchY,
            calculatedHeight,
            svgHeight,
            teilnehmerCount: teilnehmer.length
          });

          return (
            <svg
              width={displayRounds.length * 220 + 180}
              height={svgHeight}
              style={{ display: 'block', margin: '0 auto' }}
            >
              {/* Alle Runden - direkt ohne Teilnehmer-Spalte */}
              {displayRounds.map((roundNum, roundIndex) => {
                const roundMatches = rounds[roundNum].sort((a, b) => a.kampf_nummer - b.kampf_nummer);
                const matchCount = roundMatches.length;
                const spacing = Math.pow(2, roundIndex + 1) * (MATCH_HEIGHT / 2 + 10);
                const roundStartOffset = (spacing - MATCH_HEIGHT) / 2;
                const xOffset = roundIndex * 220;

                return (
                  <g key={roundNum}>
                    <text x={xOffset + 90} y={20} fill="#d4af37" fontSize="12" fontWeight="600" textAnchor="middle">
                      {getRoundName(roundNum, roundIndex === 0)}
                    </text>

                    {roundMatches.map((kampf, matchIdx) => {
                      // Verwende kampf_nummer fr korrekte Bracket-Position statt Array-Index
                      const bracketPosition = kampf.kampf_nummer - 1;  // 0-basiert
                      const yOffset = roundStartOffset + bracketPosition * spacing + 40;
                      const isWinner1 = kampf.gewinner_id === kampf.kaempfer1_id;
                      const isWinner2 = kampf.gewinner_id === kampf.kaempfer2_id;
                      const isLoser1 = kampf.gewinner_id && !isWinner1 && kampf.kaempfer1_id;
                      const isLoser2 = kampf.gewinner_id && !isWinner2 && kampf.kaempfer2_id;
                      const hasWinner = kampf.gewinner_id != null;

                      return (
                        <g key={kampf.kampf_id} onClick={() => onFightClick(kampf)} style={{ cursor: 'pointer' }}>
                          <rect x={xOffset} y={yOffset} width={180} height={MATCH_HEIGHT} fill="#1a1510" stroke="#3d2d1f" strokeWidth="1" rx="2" />
                          <rect x={xOffset + 1} y={yOffset + 1} width={178} height={MATCH_HEIGHT / 2 - 1} fill={isWinner1 ? 'rgba(76, 175, 80, 0.35)' : isLoser1 ? 'rgba(139, 0, 0, 0.3)' : 'transparent'} rx="2" />
                          <rect x={xOffset + 1} y={yOffset + MATCH_HEIGHT / 2} width={178} height={MATCH_HEIGHT / 2 - 1} fill={isWinner2 ? 'rgba(76, 175, 80, 0.35)' : isLoser2 ? 'rgba(139, 0, 0, 0.3)' : 'transparent'} rx="2" />
                          <line x1={xOffset + 5} y1={yOffset + MATCH_HEIGHT / 2} x2={xOffset + 175} y2={yOffset + MATCH_HEIGHT / 2} stroke="#3d2d1f" strokeWidth="1" />
                          <text x={xOffset + 12} y={yOffset + MATCH_HEIGHT / 4 + 5} fill={isWinner1 ? '#4CAF50' : isLoser1 ? '#d4af37' : (kampf.kaempfer1_id ? '#c4b89a' : '#555')} fontSize="13" fontWeight={isWinner1 ? '700' : '500'}>
                            {getFighterName(kampf.kaempfer1_id)}
                          </text>
                          <text x={xOffset + 12} y={yOffset + MATCH_HEIGHT * 3 / 4 + 5} fill={isWinner2 ? '#4CAF50' : isLoser2 ? '#d4af37' : (kampf.kaempfer2_id ? '#c4b89a' : '#555')} fontSize="13" fontWeight={isWinner2 ? '700' : '500'}>
                            {getFighterName(kampf.kaempfer2_id)}
                          </text>
                          {hasWinner && (
                            <g>
                              <circle cx={xOffset + 168} cy={isWinner1 ? yOffset + MATCH_HEIGHT / 4 : yOffset + MATCH_HEIGHT * 3 / 4} r="10" fill="none" stroke="#4CAF50" strokeWidth="1.5" />
                              <text x={xOffset + 168} y={(isWinner1 ? yOffset + MATCH_HEIGHT / 4 : yOffset + MATCH_HEIGHT * 3 / 4) + 4} fill="#4CAF50" fontSize="11" fontWeight="700" textAnchor="middle"></text>
                            </g>
                          )}
                          {/* Connector to next round */}
                          {roundIndex < displayRounds.length - 1 && (
                            <>
                              <line x1={xOffset + 180} y1={yOffset + MATCH_HEIGHT / 2} x2={xOffset + 200} y2={yOffset + MATCH_HEIGHT / 2} stroke="#5c4033" strokeWidth="2" />
                              {bracketPosition % 2 === 0 && (
                                <>
                                  {/* Vertikale Linie zur nchsten Bracket-Position */}
                                  <line x1={xOffset + 200} y1={yOffset + MATCH_HEIGHT / 2} x2={xOffset + 200} y2={yOffset + spacing + MATCH_HEIGHT / 2} stroke="#5c4033" strokeWidth="2" />
                                  <line x1={xOffset + 200} y1={yOffset + MATCH_HEIGHT / 2 + spacing / 2} x2={xOffset + 220} y2={yOffset + MATCH_HEIGHT / 2 + spacing / 2} stroke="#5c4033" strokeWidth="2" />
                                </>
                              )}
                            </>
                          )}
                        </g>
                      );
                    })}
                  </g>
                );
              })}

              {/* Champion Box */}
              {(() => {
                const xOffset = displayRounds.length * 220;
                const lastRoundMatches = rounds[displayRounds[displayRounds.length - 1]] || [];
                const lastSpacing = Math.pow(2, displayRounds.length) * (MATCH_HEIGHT / 2 + 10);
                const lastMatchY = lastRoundMatches.length > 0 ? (lastSpacing - MATCH_HEIGHT) / 2 + 40 + MATCH_HEIGHT / 2 : 100;

                return (
                  <g>
                    <line x1={xOffset - 20} y1={lastMatchY} x2={xOffset} y2={lastMatchY} stroke="#5c4033" strokeWidth="2" />
                    <text x={xOffset + 70} y={lastMatchY - 35} fill="#d4af37" fontSize="11" fontWeight="600" textAnchor="middle"> CHAMPION</text>
                    <rect x={xOffset + 10} y={lastMatchY - 25} width={120} height={50} fill="url(#championGradient)" stroke={champion ? '#d4af37' : '#3d2d1f'} strokeWidth={champion ? '2' : '1'} rx="3" />
                    <text x={xOffset + 70} y={lastMatchY + 5} fill={champion ? '#d4af37' : '#555'} fontSize={champion ? '13' : '20'} fontWeight="700" textAnchor="middle">{champion || '?'}</text>
                  </g>
                );
              })()}

              <defs>
                <linearGradient id="championGradient" x1="0%" y1="0%" x2="0%" y2="100%">
                  <stop offset="0%" stopColor="#8B0000" />
                  <stop offset="100%" stopColor="#5c0000" />
                </linearGradient>
              </defs>
            </svg>
          );
        })()}
      </div>

      {/* Verlierer-Bracket / Kampf um Platz 3 - nur anzeigen wenn Modus es erfordert */}
      {shouldShowLosersBracket() && (kleinesFinale || losersRoundNumbers.length > 0) && (
        <div style={{
          borderTop: '2px solid #3d2d1f',
          padding: '1rem 1.5rem',
          background: 'linear-gradient(180deg, #0d0a07 0%, #1a1510 100%)'
        }}>
          {/* Header */}
          <div style={{
            display: 'flex',
            alignItems: 'center',
            marginBottom: '1rem',
            gap: '1rem'
          }}>
            <div style={{
              flex: 1,
              height: '1px',
              background: 'linear-gradient(90deg, transparent, #5c4033, transparent)'
            }}></div>
            <div style={{
              color: '#cd7f32',
              fontSize: '0.85rem',
              fontWeight: 700,
              letterSpacing: '2px',
              textAlign: 'center'
            }}>
              {getLosersBracketTitle()}
              {losersKaempfe.length > 1 && (
                <div style={{ fontSize: '0.65rem', color: '#888', marginTop: '0.2rem', letterSpacing: '1px' }}>
                  {losersKaempfe.length} KMPFE
                </div>
              )}
            </div>
            <div style={{
              flex: 1,
              height: '1px',
              background: 'linear-gradient(90deg, transparent, #5c4033, transparent)'
            }}></div>
          </div>

          {/* Losers Bracket SVG - nur fr double_elimination und repechage, NICHT fr single_elimination_bronze oder consolation_bracket */}
          {losersRoundNumbers.length > 0 &&
           tournamentMode !== 'single_elimination_bronze' &&
           tournamentMode !== 'consolation_bracket' && (
          <div style={{ overflowX: 'auto' }}>
            <svg
              width={losersRoundNumbers.length * 220 + 200}
              height={Math.max(losersRounds[losersRoundNumbers[0]]?.length || 1, 2) * (MATCH_HEIGHT + 20) + 100}
              style={{ display: 'block' }}
            >
              {/* Losers Bracket Runden */}
              {losersRoundNumbers.map((roundNum, roundIndex) => {
                const roundMatches = (losersRounds[roundNum] || []).sort((a, b) => a.kampf_nummer - b.kampf_nummer);
                const matchCount = roundMatches.length;
                const spacing = Math.pow(2, roundIndex) * (MATCH_HEIGHT + 20);
                const startOffset = (spacing - MATCH_HEIGHT) / 2;
                const xOffset = roundIndex * 220;

                return (
                  <g key={`losers-${roundNum}`}>
                    {/* Runden-Label */}
                    <text
                      x={xOffset + 90}
                      y={20}
                      fill="#cd7f32"
                      fontSize="11"
                      fontWeight="600"
                      textAnchor="middle"
                      style={{ letterSpacing: '1px' }}
                    >
                      {`${roundNum}`}
                    </text>

                    {/* Matches */}
                    {roundMatches.map((kampf, matchIdx) => {
                      const yOffset = startOffset + matchIdx * spacing + 40;
                      const isWinner1 = kampf.gewinner_id === kampf.kaempfer1_id;
                      const isWinner2 = kampf.gewinner_id === kampf.kaempfer2_id;
                      const isLoser1 = kampf.gewinner_id && !isWinner1 && kampf.kaempfer1_id;
                      const isLoser2 = kampf.gewinner_id && !isWinner2 && kampf.kaempfer2_id;

                      return (
                        <g
                          key={kampf.kampf_id}
                          onClick={() => onFightClick(kampf)}
                          style={{ cursor: 'pointer' }}
                        >
                          {/* Match Box Background */}
                          <rect
                            x={xOffset}
                            y={yOffset}
                            width={180}
                            height={MATCH_HEIGHT}
                            fill="#1a1510"
                            stroke="#5c4033"
                            strokeWidth="1"
                            rx="2"
                          />

                          {/* Fighter 1 Background */}
                          <rect
                            x={xOffset + 1}
                            y={yOffset + 1}
                            width={178}
                            height={MATCH_HEIGHT / 2 - 1}
                            fill={isWinner1 ? 'rgba(76, 175, 80, 0.35)' : isLoser1 ? 'rgba(139, 0, 0, 0.3)' : 'transparent'}
                            rx="2"
                          />

                          {/* Fighter 2 Background */}
                          <rect
                            x={xOffset + 1}
                            y={yOffset + MATCH_HEIGHT / 2}
                            width={178}
                            height={MATCH_HEIGHT / 2 - 1}
                            fill={isWinner2 ? 'rgba(76, 175, 80, 0.35)' : isLoser2 ? 'rgba(139, 0, 0, 0.3)' : 'transparent'}
                            rx="2"
                          />

                          {/* Divider Line */}
                          <line
                            x1={xOffset + 5}
                            y1={yOffset + MATCH_HEIGHT / 2}
                            x2={xOffset + 175}
                            y2={yOffset + MATCH_HEIGHT / 2}
                            stroke="#5c4033"
                            strokeWidth="1"
                          />

                          {/* Fighter 1 Name */}
                          <text
                            x={xOffset + 12}
                            y={yOffset + MATCH_HEIGHT / 4 + 5}
                            fill={isWinner1 ? '#cd7f32' : (kampf.kaempfer1_id ? '#a09080' : '#555')}
                            fontSize="12"
                            fontWeight={isWinner1 ? '700' : '500'}
                          >
                            {getFighterName(kampf.kaempfer1_id)}
                          </text>

                          {/* Fighter 2 Name */}
                          <text
                            x={xOffset + 12}
                            y={yOffset + MATCH_HEIGHT * 3 / 4 + 5}
                            fill={isWinner2 ? '#cd7f32' : (kampf.kaempfer2_id ? '#a09080' : '#555')}
                            fontSize="12"
                            fontWeight={isWinner2 ? '700' : '500'}
                          >
                            {getFighterName(kampf.kaempfer2_id)}
                          </text>

                          {/* Connector to next round */}
                          {roundIndex < losersRoundNumbers.length - 1 && (
                            <path
                              d={`M ${xOffset + 180} ${yOffset + MATCH_HEIGHT / 2}
                                  L ${xOffset + 200} ${yOffset + MATCH_HEIGHT / 2}
                                  L ${xOffset + 200} ${startOffset + Math.floor(matchIdx / 2) * spacing * 2 + MATCH_HEIGHT / 2 + 40}
                                  L ${xOffset + 220} ${startOffset + Math.floor(matchIdx / 2) * spacing * 2 + MATCH_HEIGHT / 2 + 40}`}
                              fill="none"
                              stroke="#5c4033"
                              strokeWidth="1"
                            />
                          )}
                        </g>
                      );
                    })}
                  </g>
                );
              })}
            </svg>
          </div>
          )}

          {/* Trostrunden-Bracket fr consolation_bracket Modus */}
          {tournamentMode === 'consolation_bracket' && trostrundenRoundNumbers.length > 0 && (
          <div style={{ overflowX: 'auto', marginTop: '1rem' }}>
            <div style={{
              color: '#888',
              fontSize: '0.75rem',
              marginBottom: '0.5rem',
              textAlign: 'center',
              letterSpacing: '1px'
            }}>
              TROSTRUNDEN  
            </div>
            <svg
              width={trostrundenRoundNumbers.length * 220 + 200}
              height={(() => {
                // Berechne Hhe basierend auf erster Runde und deren Spacing
                const firstRoundMatches = trostrundenRounds[trostrundenRoundNumbers[0]]?.length || 1;
                const baseSpacing = MATCH_HEIGHT + 20;
                return Math.max(firstRoundMatches * baseSpacing + 150, 300);
              })()}
              style={{ display: 'block', margin: '0 auto' }}
            >
              {trostrundenRoundNumbers.map((roundNum, roundIndex) => {
                const roundMatches = (trostrundenRounds[roundNum] || []).sort((a, b) => a.kampf_nummer - b.kampf_nummer);
                const matchCount = roundMatches.length;
                const spacing = Math.pow(2, roundIndex) * (MATCH_HEIGHT + 20);
                const startOffset = (spacing - MATCH_HEIGHT) / 2;
                const xOffset = roundIndex * 220;

                return (
                  <g key={`trost-${roundNum}`}>
                    <text
                      x={xOffset + 90}
                      y={20}
                      fill="#cd7f32"
                      fontSize="11"
                      fontWeight="600"
                      textAnchor="middle"
                    >
                      Trostrunde {roundIndex + 1}
                    </text>

                    {roundMatches.map((kampf, matchIdx) => {
                      // Bracket-Position aus kampf_nummer extrahieren (1010, 1021, 2010, etc.)
                      const bracketPosition = (kampf.kampf_nummer % 100) - 1;
                      const yOffset = startOffset + bracketPosition * spacing + 40;
                      const isWinner1 = kampf.gewinner_id === kampf.kaempfer1_id;
                      const isWinner2 = kampf.gewinner_id === kampf.kaempfer2_id;
                      const isLoser1 = kampf.gewinner_id && !isWinner1 && kampf.kaempfer1_id;
                      const isLoser2 = kampf.gewinner_id && !isWinner2 && kampf.kaempfer2_id;

                      // Nchste Runde Spacing berechnen
                      const nextSpacing = Math.pow(2, roundIndex + 1) * (MATCH_HEIGHT + 20);
                      const nextStartOffset = (nextSpacing - MATCH_HEIGHT) / 2;
                      // Ziel-Position in nchster Runde
                      const targetPosition = Math.floor(bracketPosition / 2);
                      const targetY = nextStartOffset + targetPosition * nextSpacing + MATCH_HEIGHT / 2 + 40;

                      return (
                        <g key={kampf.kampf_id} onClick={() => onFightClick(kampf)} style={{ cursor: 'pointer' }}>
                          <rect
                            x={xOffset}
                            y={yOffset}
                            width={180}
                            height={MATCH_HEIGHT}
                            fill="#1a1510"
                            stroke="#5c4033"
                            strokeWidth="1"
                            rx="2"
                          />
                          {/* Fighter 1 Background */}
                          <rect
                            x={xOffset + 1}
                            y={yOffset + 1}
                            width={178}
                            height={MATCH_HEIGHT / 2 - 1}
                            fill={isWinner1 ? 'rgba(76, 175, 80, 0.35)' : isLoser1 ? 'rgba(139, 0, 0, 0.3)' : 'transparent'}
                            rx="2"
                          />
                          {/* Fighter 2 Background */}
                          <rect
                            x={xOffset + 1}
                            y={yOffset + MATCH_HEIGHT / 2}
                            width={178}
                            height={MATCH_HEIGHT / 2 - 1}
                            fill={isWinner2 ? 'rgba(76, 175, 80, 0.35)' : isLoser2 ? 'rgba(139, 0, 0, 0.3)' : 'transparent'}
                            rx="2"
                          />
                          <line
                            x1={xOffset + 5}
                            y1={yOffset + MATCH_HEIGHT / 2}
                            x2={xOffset + 175}
                            y2={yOffset + MATCH_HEIGHT / 2}
                            stroke="#5c4033"
                            strokeWidth="1"
                          />
                          <text
                            x={xOffset + 12}
                            y={yOffset + MATCH_HEIGHT / 4 + 5}
                            fill={isWinner1 ? '#4CAF50' : isLoser1 ? '#d4af37' : (kampf.kaempfer1_id ? '#a09080' : '#555')}
                            fontSize="12"
                            fontWeight={isWinner1 ? '700' : '500'}
                          >
                            {getFighterName(kampf.kaempfer1_id)}
                          </text>
                          <text
                            x={xOffset + 12}
                            y={yOffset + MATCH_HEIGHT * 3 / 4 + 5}
                            fill={isWinner2 ? '#4CAF50' : isLoser2 ? '#d4af37' : (kampf.kaempfer2_id ? '#a09080' : '#555')}
                            fontSize="12"
                            fontWeight={isWinner2 ? '700' : '500'}
                          >
                            {getFighterName(kampf.kaempfer2_id)}
                          </text>

                          {/* Connector to next round */}
                          {roundIndex < trostrundenRoundNumbers.length - 1 && (
                            <path
                              d={`M ${xOffset + 180} ${yOffset + MATCH_HEIGHT / 2}
                                  L ${xOffset + 200} ${yOffset + MATCH_HEIGHT / 2}
                                  L ${xOffset + 200} ${targetY}
                                  L ${xOffset + 220} ${targetY}`}
                              fill="none"
                              stroke="#5c4033"
                              strokeWidth="1"
                            />
                          )}
                        </g>
                      );
                    })}
                  </g>
                );
              })}
            </svg>
          </div>
          )}

          {/* Kleines Finale (3. Platz) */}
          {kleinesFinale && (
            <div style={{
              marginTop: '1.5rem',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              gap: '2rem'
            }}>
              <div style={{
                background: '#1a1510',
                border: '1px solid #5c4033',
                borderRadius: '4px',
                padding: '12px 20px',
                minWidth: '160px',
                textAlign: 'center'
              }}>
                <div style={{
                  color: kleinesFinale.gewinner_id === kleinesFinale.kaempfer1_id ? '#cd7f32' : '#a09080',
                  fontSize: '0.9rem',
                  fontWeight: kleinesFinale.gewinner_id === kleinesFinale.kaempfer1_id ? 700 : 500
                }}>
                  {getFighterName(kleinesFinale.kaempfer1_id)}
                </div>
              </div>

              <div style={{ color: '#cd7f32', fontSize: '1.2rem', fontWeight: 700 }}>VS</div>

              <div style={{
                background: '#1a1510',
                border: '1px solid #5c4033',
                borderRadius: '4px',
                padding: '12px 20px',
                minWidth: '160px',
                textAlign: 'center'
              }}>
                <div style={{
                  color: kleinesFinale.gewinner_id === kleinesFinale.kaempfer2_id ? '#cd7f32' : '#a09080',
                  fontSize: '0.9rem',
                  fontWeight: kleinesFinale.gewinner_id === kleinesFinale.kaempfer2_id ? 700 : 500
                }}>
                  {getFighterName(kleinesFinale.kaempfer2_id)}
                </div>
              </div>

              <div style={{ color: '#5c4033', fontSize: '1.5rem' }}></div>

              {/* Platz 3 Box */}
              <div style={{
                background: 'linear-gradient(180deg, #654321 0%, #3d2d1f 100%)',
                border: '2px solid #cd7f32',
                borderRadius: '4px',
                padding: '12px 24px',
                minWidth: '140px',
                textAlign: 'center'
              }}>
                <div style={{
                  color: '#cd7f32',
                  fontSize: '0.7rem',
                  fontWeight: 700,
                  marginBottom: '4px',
                  letterSpacing: '2px'
                }}>
                  3. PLATZ
                </div>
                <div style={{
                  color: kleinesFinale.gewinner_id ? '#fff' : '#888',
                  fontSize: '0.95rem',
                  fontWeight: 700
                }}>
                  {kleinesFinale.gewinner_id
                    ? getFighterName(kleinesFinale.gewinner_id)
                    : '?'}
                </div>
              </div>
            </div>
          )}

          {/* Info Text - basierend auf Modus */}
          {tournamentMode !== 'single_elimination_bronze' && (
            <div style={{
              marginTop: '1rem',
              textAlign: 'center',
              color: '#666',
              fontSize: '0.75rem',
              fontStyle: 'italic'
            }}>
              {tournamentMode === 'consolation_bracket' && 'Alle Verlierer kmpfen im Trostrunden-Bracket um Platz 3'}
              {tournamentMode === 'double_elimination' && 'Verlierer haben eine zweite Chance im Losers-Bracket'}
              {tournamentMode === 'repechage' && 'Verlierer gegen Finalisten qualifizieren sich fr Bronze'}
            </div>
          )}
        </div>
      )}
    </div>
  );
}

// Bracket Visualization Component
function BracketVisualization({ fights, participants, onFightClick }) {
  const getFighterName = (fighterId) => {
    const fighter = participants.find(p => p.wettkaempfer_id === fighterId);
    return fighter ? `${fighter.vorname} ${fighter.nachname}` : 'TBD';
  };

  const getWinnerName = (fight) => {
    if (!fight.gewinner_id) return null;
    return getFighterName(fight.gewinner_id);
  };

  const groupFightsByRound = (fights) => {
    const rounds = {};
    fights.forEach(fight => {
      const round = fight.runde || 1;
      if (!rounds[round]) rounds[round] = [];
      rounds[round].push(fight);
    });
    return rounds;
  };

  const rounds = groupFightsByRound(fights);
  const roundNumbers = Object.keys(rounds).sort((a, b) => parseInt(a) - parseInt(b));

  return (
    <div className="bracket-visualization">
      <h3> Turnierbaum</h3>
      
      <div className="bracket-tree">
        {roundNumbers.map(roundNum => (
          <div key={roundNum} className="bracket-round">
            <h4>Runde {roundNum}</h4>
            <div className="fights-column">
              {rounds[roundNum].map(fight => (
                <div 
                  key={fight.kampf_id} 
                  className={`fight-box ${fight.gewinner_id ? 'completed' : 'pending'}`}
                  onClick={() => onFightClick(fight)}
                >
                  <div className="fight-header">
                    <span className="fight-number">Kampf {fight.kampf_nummer}</span>
                    {fight.gewinner_id && <span className="completed-badge"></span>}
                  </div>
                  
                  <div className="fight-participants">
                    <div className={`fighter ${fight.gewinner_id === fight.kaempfer1_id ? 'winner' : ''}`}>
                      {getFighterName(fight.kaempfer1_id)}
                      {fight.punkte_kaempfer1 !== null && (
                        <span className="score">{fight.punkte_kaempfer1}</span>
                      )}
                    </div>
                    
                    <div className="vs">VS</div>
                    
                    <div className={`fighter ${fight.gewinner_id === fight.kaempfer2_id ? 'winner' : ''}`}>
                      {getFighterName(fight.kaempfer2_id)}
                      {fight.punkte_kaempfer2 !== null && (
                        <span className="score">{fight.punkte_kaempfer2}</span>
                      )}
                    </div>
                  </div>
                  
                  {getWinnerName(fight) && (
                    <div className="winner-info">
                       {getWinnerName(fight)}
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

