// BruchtestDashboard.jsx - Bruchtest-Wertungssystem
// Komplexes Punktesystem fr Bruchtest-Kategorien
import React, { useState, useEffect, useCallback } from 'react';
import './BruchtestDashboard.css';

const API_URL = process.env.REACT_APP_API_URL || '';

function BruchtestDashboard({ turnierId: propTurnierId }) {
  const [kategorien, setKategorien] = useState([]);
  const [selectedKategorie, setSelectedKategorie] = useState(null);
  const [teilnehmer, setTeilnehmer] = useState([]);
  const [currentTeilnehmer, setCurrentTeilnehmer] = useState(null);
  const [loading, setLoading] = useState(true);
  const [turniere, setTurniere] = useState([]);
  const [selectedTurnier, setSelectedTurnier] = useState(null);

  // Modus-Einstellungen
  const [modus, setModus] = useState('traditionell'); // 'traditionell' oder 'kreativ'
  const [maxTechniken, setMaxTechniken] = useState(5);

  // Gespeicherte Wertungen pro Teilnehmer (fr Technik-Zhlung)
  const [teilnehmerWertungen, setTeilnehmerWertungen] = useState({});

  // Zugewiesene Techniken pro Teilnehmer (Traditionell-Modus)
  // Format: { anmeldung_id: [{ technikId, mitSprung, erledigt, punkte }] }
  const [zugewieseneTechniken, setZugewieseneTechniken] = useState({});

  // Aktuell ausgewhlte Technik zum Bewerten (Traditionell-Modus)
  const [selectedAssignedIndex, setSelectedAssignedIndex] = useState(null);

  // Aufgeklappte Techniken (fr erledigte Items)
  const [expandedItems, setExpandedItems] = useState({});

  // Auto-Save zu localStorage
  useEffect(() => {
    if (Object.keys(zugewieseneTechniken).length > 0) {
      localStorage.setItem('bruchtest_techniken', JSON.stringify(zugewieseneTechniken));
    }
  }, [zugewieseneTechniken]);

  // Lade gespeicherte Daten beim Start
  useEffect(() => {
    const saved = localStorage.getItem('bruchtest_techniken');
    if (saved) {
      try {
        setZugewieseneTechniken(JSON.parse(saved));
      } catch (e) {
        console.error('Fehler beim Laden der gespeicherten Techniken:', e);
      }
    }
  }, []);

  // Bruchtest-spezifische Bewertung
  const [bewertung, setBewertung] = useState({
    material: 'bretter',
    anzahl: 0,
    technik: '',
    mitSprung: false,
    schwierigkeitsgrad: 5,
    ausfuehrung: 5,
    kraft: 5,
    zusatzpunkte: 0,
    erfolg: true,
    versuche: 1,
    bemerkung: ''
  });

  const token = localStorage.getItem('token');

  // Verfgbare Materialien
  const MATERIALIEN = [
    { id: 'bretter', name: 'Bretter', icon: '', basisPunkte: 10 },
    { id: 'dachziegel', name: 'Dachziegel', icon: '', basisPunkte: 15 },
    { id: 'ziegel', name: 'Ziegel', icon: '', basisPunkte: 20 },
    { id: 'baseballschlaeger', name: 'Baseballschlger', icon: '', basisPunkte: 25 }
  ];

  // Verfgbare Techniken - erweitert
  const TECHNIKEN = [
    // Handtechniken
    { id: 'faust', name: 'Fauststo', kategorie: 'hand', multiplikator: 1.0 },
    { id: 'handkante', name: 'Handkante', kategorie: 'hand', multiplikator: 1.1 },
    { id: 'hammerfaust', name: 'Hammerfaust', kategorie: 'hand', multiplikator: 1.1 },
    { id: 'handruecken', name: 'Handrcken', kategorie: 'hand', multiplikator: 1.2 },
    { id: 'ellbogen', name: 'Ellbogensto', kategorie: 'hand', multiplikator: 1.3 },
    // Futechniken
    { id: 'frontkick', name: 'Frontkick', kategorie: 'fuss', multiplikator: 1.1 },
    { id: 'sidekick', name: 'Sidekick', kategorie: 'fuss', multiplikator: 1.2 },
    { id: 'roundhouse', name: 'Roundhousekick', kategorie: 'fuss', multiplikator: 1.2 },
    { id: 'axtkick', name: 'Axtkick', kategorie: 'fuss', multiplikator: 1.3 },
    { id: 'backkick', name: 'Backkick', kategorie: 'fuss', multiplikator: 1.4 },
    { id: 'drehkick', name: 'Drehkick (360)', kategorie: 'fuss', multiplikator: 1.5 },
    { id: 'hookkick', name: 'Hookkick', kategorie: 'fuss', multiplikator: 1.3 },
    // Spezial
    { id: 'kopfstoss', name: 'Kopfsto', kategorie: 'spezial', multiplikator: 1.8 },
    { id: 'knie', name: 'Kniesto', kategorie: 'spezial', multiplikator: 1.2 },
    { id: 'sonstiges', name: 'Sonstige Technik', kategorie: 'spezial', multiplikator: 1.0 }
  ];

  // Sprung-Multiplikatoren je nach Kategorie
  const SPRUNG_MULTIPLIKATOREN = {
    hand: 1.2,
    fuss: 1.4,
    spezial: 1.6
  };

  // Turniere laden
  useEffect(() => {
    fetchTurniere();
  }, []);

  const fetchTurniere = async () => {
    try {
      const response = await fetch(`${API_URL}/api/turniere`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      if (response.ok) {
        const data = await response.json();
        const turniereList = data.data || data || [];
        setTurniere(Array.isArray(turniereList) ? turniereList : []);
        if (turniereList.length > 0 && !propTurnierId) {
          setSelectedTurnier(turniereList[0]);
        }
      }
    } catch (err) {
      console.error('Fehler beim Laden der Turniere:', err);
    }
  };

  // Kategorien laden wenn Turnier ausgewhlt
  useEffect(() => {
    const turnierId = propTurnierId || selectedTurnier?.turnier_id;
    if (turnierId) {
      fetchKategorien(turnierId);
    }
  }, [propTurnierId, selectedTurnier]);

  const fetchKategorien = async (turnierId) => {
    setLoading(true);
    try {
      // Versuche zuerst die Bruchtest-API
      const bruchtestResponse = await fetch(`${API_URL}/api/bruchtest/turnier/${turnierId}`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });

      if (bruchtestResponse.ok) {
        const bruchtestData = await bruchtestResponse.json();
        if (bruchtestData.success && bruchtestData.data?.length > 0) {
          setKategorien(bruchtestData.data);
          handleKategorieSelect(bruchtestData.data[0]);
          setLoading(false);
          return;
        }
      }

      // Fallback: Hole alle Kategorien und filtere
      const response = await fetch(`${API_URL}/api/turniere/${turnierId}/kategorien`, {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      if (response.ok) {
        const data = await response.json();
        const allKategorien = data.data || data || [];
        // Filtere nur Bruchtest-Kategorien
        const bruchtestKategorien = allKategorien.filter(k =>
          k.kategorie?.toLowerCase().includes('bruchtest') ||
          k.kategorie_typ === 'bruchtest'
        );
        setKategorien(bruchtestKategorien);

        if (bruchtestKategorien.length > 0) {
          handleKategorieSelect(bruchtestKategorien[0]);
        }
      }
    } catch (err) {
      console.error('Fehler beim Laden der Kategorien:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleKategorieSelect = async (kategorie) => {
    setSelectedKategorie(kategorie);
    // Lade Teilnehmer fr diese Kategorie
    try {
      const turnierId = propTurnierId || selectedTurnier?.turnier_id;
      const response = await fetch(
        `${API_URL}/api/anmeldungen/turnier/${turnierId}?kategorie=${kategorie.kategorie}`,
        { headers: { 'Authorization': `Bearer ${token}` } }
      );
      if (response.ok) {
        const data = await response.json();
        // API gibt gruppiert nach Verein zurck unter data.anmeldungen_by_verein
        let anmeldungen = [];
        const byVerein = data.data?.anmeldungen_by_verein || data.anmeldungen_by_verein || data.data || {};
        if (typeof byVerein === 'object' && !Array.isArray(byVerein)) {
          Object.values(byVerein).forEach(verein => {
            if (verein.wettkampfer && Array.isArray(verein.wettkampfer)) {
              anmeldungen = anmeldungen.concat(verein.wettkampfer);
            }
          });
        }
        if (anmeldungen.length === 0 && Array.isArray(data.data)) {
          anmeldungen = data.data;
        }
        setTeilnehmer(anmeldungen);
        if (anmeldungen.length > 0) {
          setCurrentTeilnehmer(anmeldungen[0]);
          resetBewertung();
        }
      }
    } catch (err) {
      console.error('Fehler beim Laden der Teilnehmer:', err);
    }
  };

  const resetBewertung = () => {
    setBewertung({
      material: 'bretter',
      anzahl: 0,
      technik: '',
      mitSprung: false,
      schwierigkeitsgrad: 5,
      ausfuehrung: 5,
      kraft: 5,
      zusatzpunkte: 0,
      erfolg: true,
      versuche: 1,
      bemerkung: ''
    });
  };

  const berechnePunkte = () => {
    // Bei Traditionell-Modus: Technik, Sprung und Anzahl aus zugewiesener Technik
    let technikId = bewertung.technik;
    let mitSprung = bewertung.mitSprung;
    let anzahl = bewertung.anzahl;

    if (modus === 'traditionell' && selectedAssignedIndex !== null && currentTeilnehmer) {
      const assigned = (zugewieseneTechniken[currentTeilnehmer.anmeldung_id] || [])[selectedAssignedIndex];
      if (assigned) {
        technikId = assigned.technikId;
        mitSprung = assigned.mitSprung;
        anzahl = assigned.anzahl || 0;
      }
    }

    const technikObj = TECHNIKEN.find(t => t.id === technikId);
    const technikMultiplikator = technikObj?.multiplikator || 1.0;

    const materialObj = MATERIALIEN.find(m => m.id === bewertung.material);
    const basisPunkte = materialObj?.basisPunkte || 10;

    // Basispunkte (Material * Anzahl)
    const materialPunkte = anzahl * basisPunkte;

    // Technik-Multiplikator
    const technikPunkte = materialPunkte * technikMultiplikator;

    // Sprung-Bonus (je nach Kategorie)
    const sprungMultiplikator = mitSprung ? (SPRUNG_MULTIPLIKATOREN[technikObj?.kategorie] || 1.2) : 1;
    const sprungPunkte = technikPunkte * sprungMultiplikator;

    // Schwierigkeitsgrad (1-10)
    const schwierigkeitsFaktor = bewertung.schwierigkeitsgrad / 10;

    // Ausfhrung (1-10)
    const ausfuehrungsFaktor = bewertung.ausfuehrung / 10;

    // Kraft (1-10)
    const kraftFaktor = bewertung.kraft / 10;

    // Versuch-Abzug (jeder weitere Versuch -20%)
    const versuchAbzug = Math.max(0, 1 - (bewertung.versuche - 1) * 0.2);

    // Erfolg-Faktor (Misserfolg = 0 Punkte)
    const erfolgFaktor = bewertung.erfolg ? 1 : 0;

    // Gesamtpunkte (mit Sprung-Bonus)
    const basisGesamt = sprungPunkte * schwierigkeitsFaktor * ausfuehrungsFaktor * kraftFaktor * versuchAbzug * erfolgFaktor;

    // Zusatzpunkte hinzufgen
    const gesamt = basisGesamt + (bewertung.zusatzpunkte || 0);

    return Math.round(gesamt * 100) / 100;
  };

  // Einzelne Technik speichern (fr Auto-Save)
  const saveSingleTechnik = async (item, idx) => {
    if (!currentTeilnehmer || !selectedKategorie) return;

    const turnierId = propTurnierId || selectedTurnier?.turnier_id || selectedKategorie?.turnier_id;
    const wkId = currentTeilnehmer.wettkaempfer_id || currentTeilnehmer.wettkampfer_id;

    if (!turnierId || !wkId || !item.technikId) return;

    try {
      const response = await fetch(`${API_URL}/api/bruchtest/wertung`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          turnier_id: turnierId,
          kategorie_id: selectedKategorie?.kategorie_id,
          kategorie: selectedKategorie?.kategorie,
          wettkaempfer_id: wkId,
          anmeldung_id: currentTeilnehmer.anmeldung_id,
          technik: item.technikId,
          material: item.material,
          anzahl_bretter: item.anzahl,
          mitSprung: item.mitSprung,
          schwierigkeitsgrad: item.schwierigkeit,
          ausfuehrung: item.ausfuehrung,
          kraft: item.kraft,
          versuche: item.versuche,
          erfolg: item.erfolg,
          zusatzpunkte: item.zusatzpunkte
        })
      });
      const data = await response.json();
      if (data.success) {
        console.log(` Technik ${idx + 1} auto-gespeichert`);
      }
    } catch (err) {
      console.error('Auto-Save Fehler:', err);
    }
  };

  // Alle Techniken speichern und zum nchsten Teilnehmer
  const handleSaveBewertung = async () => {
    if (!currentTeilnehmer || !selectedKategorie) return;

    const techniken = zugewieseneTechniken[currentTeilnehmer.anmeldung_id] || [];

    if (modus === 'traditionell' && techniken.length === 0) {
      alert('Bitte fgen Sie mindestens eine Technik hinzu');
      return;
    }

    const turnierId = propTurnierId || selectedTurnier?.turnier_id || selectedKategorie?.turnier_id;
    const wkId = currentTeilnehmer.wettkaempfer_id || currentTeilnehmer.wettkampfer_id;

    if (!turnierId || !wkId) {
      alert('Fehlende Daten fr Speicherung');
      return;
    }

    try {
      // Alle Techniken speichern
      for (let idx = 0; idx < techniken.length; idx++) {
        const item = techniken[idx];
        if (!item.technikId) continue;

        const response = await fetch(`${API_URL}/api/bruchtest/wertung`, {
          method: 'POST',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            turnier_id: turnierId,
            kategorie_id: selectedKategorie?.kategorie_id,
            kategorie: selectedKategorie?.kategorie,
            wettkaempfer_id: wkId,
            anmeldung_id: currentTeilnehmer.anmeldung_id,
            technik: item.technikId,
            material: item.material,
            anzahl_bretter: item.anzahl,
            mitSprung: item.mitSprung,
            schwierigkeitsgrad: item.schwierigkeit,
            ausfuehrung: item.ausfuehrung,
            kraft: item.kraft,
            versuche: item.versuche,
            erfolg: item.erfolg,
            zusatzpunkte: item.zusatzpunkte
          })
        });
        const data = await response.json();
        if (!data.success) {
          throw new Error(data.error || 'Fehler beim Speichern');
        }
      }

      // Alle als erledigt markieren
      setZugewieseneTechniken(prev => ({
        ...prev,
        [currentTeilnehmer.anmeldung_id]: techniken.map(t => ({ ...t, erledigt: true }))
      }));

      // Zum nchsten Teilnehmer springen
      const currentIndex = teilnehmer.findIndex(t => t.anmeldung_id === currentTeilnehmer.anmeldung_id);
      if (currentIndex < teilnehmer.length - 1) {
        setCurrentTeilnehmer(teilnehmer[currentIndex + 1]);
        setSelectedAssignedIndex(null);
        resetBewertung();
      } else {
        alert('Alle Teilnehmer bewertet!');
      }

    } catch (err) {
      console.error('Fehler beim Speichern:', err);
      alert('Fehler beim Speichern: ' + err.message);
    }
  };

  const selectTeilnehmer = (t) => {
    setCurrentTeilnehmer(t);
    setSelectedAssignedIndex(null);
    resetBewertung();
  };

  if (loading) {
    return (
      <div className="bruchtest-dashboard">
        <div className="loading-state">
          <div className="loading-spinner"></div>
          <p>Lade Bruchtest-Kategorien...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="bruchtest-dashboard">
      {/* Header mit Kategorie und Modus */}
      <div className="bruchtest-header">
        <div className="header-title">
          <h1> Bruchtest Wertung</h1>
          {kategorien.length > 0 && (
            <div className="header-kategorie">
              <select
                value={selectedKategorie?.kategorie_id || ''}
                onChange={(e) => {
                  const kat = kategorien.find(k => String(k.kategorie_id) === e.target.value);
                  if (kat) {
                    handleKategorieSelect(kat);
                    const katName = (kat.kategorie || '').toLowerCase();
                    if (katName.includes('kreativ')) {
                      setModus('kreativ');
                    } else if (katName.includes('traditionell')) {
                      setModus('traditionell');
                    }
                  }
                }}
              >
                <option value="">-- Kategorie --</option>
                {kategorien.map(k => (
                  <option key={k.kategorie_id || k.kategorie} value={k.kategorie_id}>
                    {k.kategorie}
                  </option>
                ))}
              </select>
              {selectedKategorie && (
                <span className="modus-badge">{modus === 'kreativ' ? 'Kreativ' : 'Traditionell'}</span>
              )}
            </div>
          )}
        </div>

        <div className="header-settings">
          {/* Max Techniken (nur bei Traditionell) */}
          {modus === 'traditionell' && (
            <div className="setting-group">
              <label>Max:</label>
              <div className="max-techniken-input">
                <button onClick={() => setMaxTechniken(Math.max(1, maxTechniken - 1))}>-</button>
                <span>{maxTechniken}</span>
                <button onClick={() => setMaxTechniken(maxTechniken + 1)}>+</button>
              </div>
            </div>
          )}

          {/* Turnier-Selector */}
          {!propTurnierId && (
            <div className="setting-group">
              <select
                value={selectedTurnier?.turnier_id || ''}
                onChange={(e) => {
                  const t = turniere.find(t => t.turnier_id === parseInt(e.target.value));
                  setSelectedTurnier(t);
                }}
              >
                <option value="">-- Turnier --</option>
                {turniere.map(t => (
                  <option key={t.turnier_id} value={t.turnier_id}>{t.name}</option>
                ))}
              </select>
            </div>
          )}
        </div>
      </div>

      {kategorien.length === 0 ? (
        <div className="empty-state">
          <div className="empty-icon"></div>
          <h3>Keine Bruchtest-Kategorien gefunden</h3>
          <p>Erstellen Sie zuerst eine Bruchtest-Kategorie fr dieses Turnier.</p>
        </div>
      ) : (
        <div className="bruchtest-content new-layout">
          {/* Main: Bewertung (links) */}
          <div className="bruchtest-main">
            {currentTeilnehmer ? (
              <>
                {/* Teilnehmer-Info mit Technik-Zhler */}
                <div className="teilnehmer-header">
                  <div className="teilnehmer-info">
                    <h2>{currentTeilnehmer.vorname} {currentTeilnehmer.nachname}</h2>
                    <span className="verein">{currentTeilnehmer.verein_name}</span>
                  </div>
                  <div className="technik-counter">
                    <span className="counter-label">Techniken:</span>
                    <span className="counter-value">
                      {teilnehmerWertungen[currentTeilnehmer.anmeldung_id]?.length || 0}
                      {modus === 'traditionell' ? ` / ${maxTechniken}` : ''}
                    </span>
                  </div>
                </div>

                {/* TRADITIONELL MODUS: Technik-Zuweisung & Bewertung */}
                {modus === 'traditionell' && (
                  <div className="traditionell-section">
                    {/* Zugewiesene Techniken Liste */}
                    <div className="assigned-techniken">
                      <div className="assigned-header">
                        <span>Zugewiesene Techniken ({(zugewieseneTechniken[currentTeilnehmer.anmeldung_id] || []).length}/{maxTechniken})</span>
                      </div>
                      <div className="assigned-list">
                        {(zugewieseneTechniken[currentTeilnehmer.anmeldung_id] || []).map((item, idx) => {
                          const technik = TECHNIKEN.find(t => t.id === item.technikId);
                          const sprungMult = item.mitSprung ? SPRUNG_MULTIPLIKATOREN[technik?.kategorie] : 1;
                          const materialObj = MATERIALIEN.find(m => m.id === item.material);

                          // Punkte fr diese Technik berechnen
                          const calcItemPunkte = () => {
                            if (!item.erfolg) return 0;
                            const basisPkt = materialObj?.basisPunkte || 10;
                            const matPunkte = (item.anzahl || 1) * basisPkt;
                            const techMult = technik?.multiplikator || 1;
                            const techPunkte = matPunkte * techMult;
                            const sprungPunkte = techPunkte * (sprungMult || 1);
                            const schwierigkeit = (item.schwierigkeit || 5) / 10;
                            const technikWert = (item.technikWert || 5) / 10;
                            const ausfuehrung = (item.ausfuehrung || 5) / 10;
                            const kraft = (item.kraft || 5) / 10;
                            const faktoren = schwierigkeit * technikWert * ausfuehrung * kraft;
                            const versuchAbzug = Math.max(0, 1 - ((item.versuche || 1) - 1) * 0.2);
                            const punkte = sprungPunkte * faktoren * versuchAbzug + (item.zusatzpunkte || 0);
                            return Math.round(punkte * 100) / 100;
                          };
                          const itemPunkte = calcItemPunkte() || 0;

                          const updateItem = (field, value) => {
                            const updatedItem = { ...item, [field]: value };
                            setZugewieseneTechniken(prev => ({
                              ...prev,
                              [currentTeilnehmer.anmeldung_id]: prev[currentTeilnehmer.anmeldung_id].map((t, i) =>
                                i === idx ? updatedItem : t
                              )
                            }));
                            // Auto-Save nach nderung
                            saveSingleTechnik(updatedItem, idx);
                          };

                          const isExpanded = !item.erledigt || expandedItems[`${currentTeilnehmer.anmeldung_id}-${idx}`];

                          return (
                            <div key={idx} className={`assigned-item-full ${item.erledigt ? 'done' : ''} ${isExpanded ? 'expanded' : 'collapsed'}`}>
                              {/* Header: Technik Name + Multiplikatoren + Lschen */}
                              <div
                                className="item-header"
                                onClick={() => {
                                  if (item.erledigt) {
                                    setExpandedItems(prev => ({
                                      ...prev,
                                      [`${currentTeilnehmer.anmeldung_id}-${idx}`]: !prev[`${currentTeilnehmer.anmeldung_id}-${idx}`]
                                    }));
                                  }
                                }}
                                style={{ cursor: item.erledigt ? 'pointer' : 'default' }}
                              >
                                <span className="assigned-num">{idx + 1}.</span>
                                <span className="assigned-name">{technik?.name}</span>
                                <span className="mult-badge">x{technik?.multiplikator}</span>
                                {item.mitSprung && <span className="sprung-mult-badge">Sprung x{sprungMult}</span>}
                                {item.erledigt && <span className="done-badge">{Math.round(itemPunkte * 100) / 100} Pkt</span>}
                                {item.erledigt && <span className="expand-icon">{isExpanded ? '' : ''}</span>}
                                {!item.erledigt && (
                                  <button className="remove-btn" onClick={(e) => {
                                    e.stopPropagation();
                                    setZugewieseneTechniken(prev => ({
                                      ...prev,
                                      [currentTeilnehmer.anmeldung_id]: prev[currentTeilnehmer.anmeldung_id].filter((_, i) => i !== idx)
                                    }));
                                  }}></button>
                                )}
                              </div>

                              {/* Bewertungsfelder */}
                              {isExpanded && (
                                <div className="item-controls">
                                  {/* Zeile 1: Material, Anzahl, Sprung, Versuche, Erfolg */}
                                  <div className="control-row">
                                    <div className="control-group material-group">
                                      <label>Material</label>
                                      <div className="material-btns">
                                        {MATERIALIEN.map(m => (
                                          <button
                                            key={m.id}
                                            className={`mat-btn-sm ${item.material === m.id ? 'active' : ''}`}
                                            onClick={() => updateItem('material', m.id)}
                                            title={`${m.name} (${m.basisPunkte} Pkt)`}
                                          >
                                            {m.icon}
                                          </button>
                                        ))}
                                      </div>
                                    </div>
                                    <div className="control-group">
                                      <label>Anzahl</label>
                                      <div className="num-ctrl">
                                        <button onClick={() => updateItem('anzahl', Math.max(0, (item.anzahl || 0) - 1))}>-</button>
                                        <span>{item.anzahl || 0}</span>
                                        <button onClick={() => updateItem('anzahl', (item.anzahl || 0) + 1)}>+</button>
                                      </div>
                                    </div>
                                    <div className="control-group">
                                      <label>Sprung</label>
                                      <button
                                        className={`toggle-btn ${item.mitSprung ? 'yes' : 'no'}`}
                                        onClick={() => updateItem('mitSprung', !item.mitSprung)}
                                      >
                                        {item.mitSprung ? 'Ja' : 'Nein'}
                                      </button>
                                    </div>
                                    <div className="control-group">
                                      <label>Versuche</label>
                                      <div className="num-ctrl">
                                        <button onClick={() => updateItem('versuche', Math.max(1, (item.versuche || 1) - 1))}>-</button>
                                        <span>{item.versuche || 1}</span>
                                        <button onClick={() => updateItem('versuche', (item.versuche || 1) + 1)}>+</button>
                                      </div>
                                    </div>
                                    <div className="control-group">
                                      <label>Erfolg</label>
                                      <button
                                        className={`toggle-btn ${item.erfolg ? 'success' : 'fail'}`}
                                        onClick={() => updateItem('erfolg', !item.erfolg)}
                                      >
                                        {item.erfolg ? 'Ja' : 'Nein'}
                                      </button>
                                    </div>
                                  </div>

                                  {/* Zeile 2: Slider */}
                                  <div className="control-row sliders">
                                    <div className="slider-group">
                                      <label>Schwierigkeit <strong>{item.schwierigkeit || 5}</strong></label>
                                      <input type="range" min="1" max="10" value={item.schwierigkeit || 5}
                                        onChange={(e) => updateItem('schwierigkeit', parseInt(e.target.value))} />
                                    </div>
                                    <div className="slider-group">
                                      <label>Technik <strong>{item.technikWert || 5}</strong></label>
                                      <input type="range" min="1" max="10" value={item.technikWert || 5}
                                        onChange={(e) => updateItem('technikWert', parseInt(e.target.value))} />
                                    </div>
                                    <div className="slider-group">
                                      <label>Ausfhrung <strong>{item.ausfuehrung || 5}</strong></label>
                                      <input type="range" min="1" max="10" value={item.ausfuehrung || 5}
                                        onChange={(e) => updateItem('ausfuehrung', parseInt(e.target.value))} />
                                    </div>
                                    <div className="slider-group">
                                      <label>Kraft <strong>{item.kraft || 5}</strong></label>
                                      <input type="range" min="1" max="10" value={item.kraft || 5}
                                        onChange={(e) => updateItem('kraft', parseInt(e.target.value))} />
                                    </div>
                                    <div className="zusatz-group">
                                      <label>Zusatz</label>
                                      <input type="number" value={item.zusatzpunkte || 0}
                                        onChange={(e) => updateItem('zusatzpunkte', parseInt(e.target.value) || 0)} />
                                    </div>
                                  </div>

                                  {/* Zeile 3: Punkte-Anzeige */}
                                  <div className="control-row save-row" style={{ background: 'rgba(255,51,51,0.1)', padding: '0.75rem', borderRadius: '6px' }}>
                                    <div className="item-punkte" style={{ display: 'flex', alignItems: 'center', gap: '0.5rem' }}>
                                      <span style={{ color: 'rgba(255,255,255,0.7)', fontSize: '0.9rem' }}>Punkte:</span>
                                      <span style={{ fontSize: '1.5rem', fontWeight: 'bold', color: item.erfolg ? '#ff3333' : '#888' }}>
                                        {itemPunkte}
                                      </span>
                                    </div>
                                  </div>
                                </div>
                              )}
                            </div>
                          );
                        })}
                      </div>

                      {/* Technik hinzufgen */}
                      {(zugewieseneTechniken[currentTeilnehmer.anmeldung_id] || []).length < maxTechniken && (
                        <div className="add-technik">
                          <span className="add-label">Technik hinzufgen:</span>

                          <div className="technik-kategorie">
                            <span className="kat-label">Hand</span>
                            <div className="technik-buttons">
                              {TECHNIKEN.filter(t => t.kategorie === 'hand').map(t => (
                                <button
                                  key={t.id}
                                  className="tech-btn"
                                  onClick={() => {
                                    setZugewieseneTechniken(prev => ({
                                      ...prev,
                                      [currentTeilnehmer.anmeldung_id]: [
                                        ...(prev[currentTeilnehmer.anmeldung_id] || []),
                                        { technikId: t.id, mitSprung: false, erledigt: false, punkte: 0, anzahl: 1, material: 'bretter', versuche: 1, erfolg: true, schwierigkeit: 5, ausfuehrung: 5, kraft: 5, technikWert: 5, zusatzpunkte: 0 }
                                      ]
                                    }));
                                  }}
                                >
                                  {t.name}
                                </button>
                              ))}
                            </div>
                          </div>

                          <div className="technik-kategorie">
                            <span className="kat-label">Fu</span>
                            <div className="technik-buttons">
                              {TECHNIKEN.filter(t => t.kategorie === 'fuss').map(t => (
                                <button
                                  key={t.id}
                                  className="tech-btn"
                                  onClick={() => {
                                    setZugewieseneTechniken(prev => ({
                                      ...prev,
                                      [currentTeilnehmer.anmeldung_id]: [
                                        ...(prev[currentTeilnehmer.anmeldung_id] || []),
                                        { technikId: t.id, mitSprung: false, erledigt: false, punkte: 0, anzahl: 1, material: 'bretter', versuche: 1, erfolg: true, schwierigkeit: 5, ausfuehrung: 5, kraft: 5, technikWert: 5, zusatzpunkte: 0 }
                                      ]
                                    }));
                                  }}
                                >
                                  {t.name}
                                </button>
                              ))}
                            </div>
                          </div>

                          <div className="technik-kategorie">
                            <span className="kat-label">Spezial</span>
                            <div className="technik-buttons">
                              {TECHNIKEN.filter(t => t.kategorie === 'spezial').map(t => (
                                <button
                                  key={t.id}
                                  className="tech-btn"
                                  onClick={() => {
                                    setZugewieseneTechniken(prev => ({
                                      ...prev,
                                      [currentTeilnehmer.anmeldung_id]: [
                                        ...(prev[currentTeilnehmer.anmeldung_id] || []),
                                        { technikId: t.id, mitSprung: false, erledigt: false, punkte: 0, anzahl: 1, material: 'bretter', versuche: 1, erfolg: true, schwierigkeit: 5, ausfuehrung: 5, kraft: 5, technikWert: 5, zusatzpunkte: 0 }
                                      ]
                                    }));
                                  }}
                                >
                                  {t.name}
                                </button>
                              ))}
                            </div>
                          </div>
                        </div>
                      )}
                    </div>

                  </div>
                )}

                {/* KREATIV MODUS: Freie Eingabe */}
                {modus === 'kreativ' && (
                  <>
                    {/* Bereits durchgefhrte Techniken */}
                    {teilnehmerWertungen[currentTeilnehmer.anmeldung_id]?.length > 0 && (
                      <div className="performed-techniken">
                        <span className="performed-label">Absolviert:</span>
                        <div className="performed-list">
                          {teilnehmerWertungen[currentTeilnehmer.anmeldung_id].map((w, idx) => {
                            const technik = TECHNIKEN.find(t => t.id === w.technik);
                            return (
                              <div key={idx} className={`performed-item ${w.mitSprung ? 'mit-sprung' : ''}`}>
                                <span className="performed-num">{idx + 1}.</span>
                                <span className="performed-name">{technik?.name || w.technik}</span>
                                {w.mitSprung && <span className="sprung-badge">Sprung</span>}
                                <span className="performed-punkte">{w.punkte} Pkt</span>
                              </div>
                            );
                          })}
                        </div>
                      </div>
                    )}

                    {/* Bewertungsformular - Kompakt */}
                    <div className="bewertung-form compact">
                      {/* Obere Reihe: Material + Anzahl + Sprung */}
                      <div className="form-row top-row">
                        <div className="form-cell material-cell">
                          <label>Material</label>
                          <div className="material-buttons">
                            {MATERIALIEN.map(m => (
                              <button
                                key={m.id}
                                className={`mat-btn ${bewertung.material === m.id ? 'active' : ''}`}
                                onClick={() => setBewertung(prev => ({ ...prev, material: m.id }))}
                              >
                                {m.icon} {m.name}
                              </button>
                            ))}
                          </div>
                        </div>
                        <div className="form-cell anzahl-cell">
                          <label>Anzahl</label>
                          <div className="number-input compact">
                            <button onClick={() => setBewertung(prev => ({ ...prev, anzahl: Math.max(0, prev.anzahl - 1) }))}>-</button>
                            <span>{bewertung.anzahl}</span>
                            <button onClick={() => setBewertung(prev => ({ ...prev, anzahl: prev.anzahl + 1 }))}>+</button>
                          </div>
                        </div>
                        <div className="form-cell sprung-cell">
                          <label>Sprung</label>
                          <div className="sprung-buttons">
                            <button
                              className={`spr-btn ${!bewertung.mitSprung ? 'active' : ''}`}
                              onClick={() => setBewertung(prev => ({ ...prev, mitSprung: false }))}
                            >
                              Ohne
                            </button>
                            <button
                              className={`spr-btn bonus ${bewertung.mitSprung ? 'active' : ''}`}
                              onClick={() => setBewertung(prev => ({ ...prev, mitSprung: true }))}
                            >
                              Mit (+50%)
                            </button>
                          </div>
                        </div>
                      </div>

                      {/* Techniken - alle in einer kompakten Ansicht */}
                      <div className="form-row technik-row">
                        <div className="technik-compact">
                          <div className="technik-col">
                            <span className="col-label">Hand</span>
                            {TECHNIKEN.filter(t => t.kategorie === 'hand').map(t => (
                              <button
                                key={t.id}
                                className={`tech-btn ${bewertung.technik === t.id ? 'active' : ''}`}
                                onClick={() => setBewertung(prev => ({ ...prev, technik: t.id }))}
                              >
                                {t.name} <small>x{t.multiplikator}</small>
                              </button>
                            ))}
                          </div>
                          <div className="technik-col">
                            <span className="col-label">Fu</span>
                            {TECHNIKEN.filter(t => t.kategorie === 'fuss').map(t => (
                              <button
                                key={t.id}
                                className={`tech-btn ${bewertung.technik === t.id ? 'active' : ''}`}
                                onClick={() => setBewertung(prev => ({ ...prev, technik: t.id }))}
                              >
                                {t.name} <small>x{t.multiplikator}</small>
                              </button>
                            ))}
                          </div>
                          <div className="technik-col">
                            <span className="col-label">Spezial</span>
                            {TECHNIKEN.filter(t => t.kategorie === 'spezial').map(t => (
                              <button
                                key={t.id}
                                className={`tech-btn ${bewertung.technik === t.id ? 'active' : ''}`}
                                onClick={() => setBewertung(prev => ({ ...prev, technik: t.id }))}
                              >
                                {t.name} <small>x{t.multiplikator}</small>
                              </button>
                            ))}
                          </div>
                        </div>
                      </div>

                      {/* Bewertungs-Slider in einer Reihe */}
                      <div className="form-row slider-row">
                        <div className="slider-compact">
                          <label>Schwierigkeit <strong>{bewertung.schwierigkeitsgrad}</strong></label>
                          <input type="range" min="1" max="10" value={bewertung.schwierigkeitsgrad}
                            onChange={(e) => setBewertung(prev => ({ ...prev, schwierigkeitsgrad: parseInt(e.target.value) }))} />
                        </div>
                        <div className="slider-compact">
                          <label>Ausfhrung <strong>{bewertung.ausfuehrung}</strong></label>
                          <input type="range" min="1" max="10" value={bewertung.ausfuehrung}
                            onChange={(e) => setBewertung(prev => ({ ...prev, ausfuehrung: parseInt(e.target.value) }))} />
                        </div>
                        <div className="slider-compact">
                          <label>Kraft <strong>{bewertung.kraft}</strong></label>
                          <input type="range" min="1" max="10" value={bewertung.kraft}
                            onChange={(e) => setBewertung(prev => ({ ...prev, kraft: parseInt(e.target.value) }))} />
                        </div>
                        <div className="zusatz-compact">
                          <label>Zusatzpunkte</label>
                          <input
                            type="number"
                            value={bewertung.zusatzpunkte}
                            onChange={(e) => setBewertung(prev => ({ ...prev, zusatzpunkte: parseInt(e.target.value) || 0 }))}
                            placeholder="0"
                          />
                        </div>
                      </div>

                      {/* Versuche & Erfolg */}
                      <div className="form-section inline">
                        <div className="versuche-group">
                          <label>Versuche:</label>
                          <div className="number-input small">
                            <button onClick={() => setBewertung(prev => ({ ...prev, versuche: Math.max(1, prev.versuche - 1) }))}>-</button>
                            <span>{bewertung.versuche}</span>
                            <button onClick={() => setBewertung(prev => ({ ...prev, versuche: prev.versuche + 1 }))}>+</button>
                          </div>
                        </div>

                        <div className="erfolg-group">
                          <label>Erfolg:</label>
                          <div className="erfolg-toggle">
                            <button
                              className={`erfolg-btn ${bewertung.erfolg ? 'active success' : ''}`}
                              onClick={() => setBewertung(prev => ({ ...prev, erfolg: true }))}
                            >
                              Erfolg
                            </button>
                            <button
                              className={`erfolg-btn ${!bewertung.erfolg ? 'active fail' : ''}`}
                              onClick={() => setBewertung(prev => ({ ...prev, erfolg: false }))}
                            >
                              Fehlversuch
                            </button>
                          </div>
                        </div>
                      </div>

                      {/* Bemerkung */}
                      <div className="form-section">
                        <label>Bemerkung (optional):</label>
                        <textarea
                          value={bewertung.bemerkung}
                          onChange={(e) => setBewertung(prev => ({ ...prev, bemerkung: e.target.value }))}
                          placeholder="Zustzliche Anmerkungen..."
                        />
                      </div>
                    </div>
                  </>
                )}

                {/* Punkte-Anzeige & Speichern (fr beide Modi) */}
                <div className="bewertung-footer">
                  <div className="punkte-display">
                    <span className="punkte-label">Gesamtpunkte:</span>
                    <span className="punkte-value">
                      {(() => {
                        const techniken = zugewieseneTechniken[currentTeilnehmer?.anmeldung_id] || [];
                        let total = 0;
                        techniken.forEach(item => {
                          if (!item.erfolg) return;
                          const technik = TECHNIKEN.find(t => t.id === item.technikId);
                          const materialObj = MATERIALIEN.find(m => m.id === item.material);
                          const sprungMult = item.mitSprung ? SPRUNG_MULTIPLIKATOREN[technik?.kategorie] || 1 : 1;
                          const basisPkt = materialObj?.basisPunkte || 10;
                          const matPunkte = (item.anzahl || 1) * basisPkt;
                          const techPunkte = matPunkte * (technik?.multiplikator || 1);
                          const sprungPunkte = techPunkte * sprungMult;
                          const faktoren = ((item.schwierigkeit || 5) / 10) * ((item.technikWert || 5) / 10) * ((item.ausfuehrung || 5) / 10) * ((item.kraft || 5) / 10);
                          const versuchAbzug = Math.max(0, 1 - ((item.versuche || 1) - 1) * 0.2);
                          total += sprungPunkte * faktoren * versuchAbzug + (item.zusatzpunkte || 0);
                        });
                        return Math.round(total * 100) / 100;
                      })()}
                    </span>
                  </div>

                  <button className="save-btn" onClick={handleSaveBewertung}>
                    Bewertung speichern
                  </button>
                </div>
              </>
            ) : (
              <div className="no-teilnehmer">
                <p>Whlen Sie einen Teilnehmer aus der Liste</p>
              </div>
            )}
          </div>

          {/* Rechte Seite: Teilnehmer mit Platzierung */}
          <div className="bruchtest-right-panel">
            <div className="panel-header">
              <h3>Teilnehmer</h3>
              <span className="teilnehmer-anzahl">{teilnehmer.length}</span>
            </div>
            <div className="teilnehmer-ranking-list">
              {Array.isArray(teilnehmer) && teilnehmer
                .map(t => {
                  // Gesamtpunkte fr diesen Teilnehmer dynamisch berechnen
                  const techniken = zugewieseneTechniken[t.anmeldung_id] || [];
                  let gesamtPunkte = 0;
                  techniken.forEach(item => {
                    if (!item.erfolg) return;
                    const technik = TECHNIKEN.find(tech => tech.id === item.technikId);
                    const materialObj = MATERIALIEN.find(m => m.id === item.material);
                    const sprungMult = item.mitSprung ? SPRUNG_MULTIPLIKATOREN[technik?.kategorie] || 1 : 1;
                    const basisPkt = materialObj?.basisPunkte || 10;
                    const matPunkte = (item.anzahl || 1) * basisPkt;
                    const techPunkte = matPunkte * (technik?.multiplikator || 1);
                    const sprungPunkte = techPunkte * sprungMult;
                    const faktoren = ((item.schwierigkeit || 5) / 10) * ((item.technikWert || 5) / 10) * ((item.ausfuehrung || 5) / 10) * ((item.kraft || 5) / 10);
                    const versuchAbzug = Math.max(0, 1 - ((item.versuche || 1) - 1) * 0.2);
                    gesamtPunkte += sprungPunkte * faktoren * versuchAbzug + (item.zusatzpunkte || 0);
                  });
                  return { ...t, gesamtPunkte: Math.round(gesamtPunkte * 100) / 100 };
                })
                .sort((a, b) => b.gesamtPunkte - a.gesamtPunkte)
                .map((t, idx) => {
                  const count = teilnehmerWertungen[t.anmeldung_id]?.length || 0;
                  const isComplete = modus === 'traditionell' && count >= maxTechniken;
                  const platzierung = idx + 1;
                  return (
                    <div
                      key={t.anmeldung_id}
                      className={`ranking-item ${currentTeilnehmer?.anmeldung_id === t.anmeldung_id ? 'active' : ''} ${isComplete ? 'complete' : ''}`}
                      onClick={() => selectTeilnehmer(t)}
                    >
                      <span className={`platzierung ${platzierung <= 3 ? `top-${platzierung}` : ''}`}>
                        {platzierung}.
                      </span>
                      <span className="ranking-name">
                        {t.vorname} {t.nachname}
                      </span>
                      <span className="ranking-punkte">
                        {t.gesamtPunkte > 0 ? `${Math.round(t.gesamtPunkte * 100) / 100} Pkt` : '-'}
                      </span>
                    </div>
                  );
                })}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default BruchtestDashboard;
