// frontend/src/components/dashboard/StaffManager.jsx
// STAFF MANAGEMENT - Kampfrichter & Helfer Verwaltung (Dashboard Style)

import React, { useState, useEffect } from 'react';
import './StaffManager.css';

const STAFF_ROLLEN = [
  { value: 'hauptkampfrichter', label: 'Hauptkampfrichter', icon: '', color: '#ff3333' },
  { value: 'seitenkampfrichter', label: 'Seitenkampfrichter', icon: '', color: '#4CAF50' },
  { value: 'punktrichter', label: 'Punktrichter', icon: '', color: '#3b82f6' },
  { value: 'zeitnehmer', label: 'Zeitnehmer', icon: '', color: '#9333ea' },
  { value: 'mattenleiter', label: 'Mattenleiter', icon: '', color: '#f7931e' },
  { value: 'schreiber', label: 'Schreiber', icon: '', color: '#6b7280' },
  { value: 'helfer', label: 'Helfer', icon: '', color: '#64748b' },
  { value: 'arzt', label: 'Arzt', icon: '', color: '#ef4444' },
  { value: 'sanitaeter', label: 'Sanitter', icon: '', color: '#f87171' }
];

const QUALIFIKATIONEN = [
  { value: 'keine', label: 'Keine Lizenz', icon: '' },
  { value: 'c_lizenz', label: 'C-Lizenz', icon: '' },
  { value: 'b_lizenz', label: 'B-Lizenz', icon: '' },
  { value: 'a_lizenz', label: 'A-Lizenz', icon: '' },
  { value: 'international', label: 'International', icon: '' }
];

const STATUS_OPTIONEN = [
  { value: 'verfuegbar', label: 'Verfgbar', color: '#4CAF50', icon: '' },
  { value: 'im_einsatz', label: 'Im Einsatz', color: '#ff3333', icon: '' },
  { value: 'pause', label: 'Pause', color: '#f7931e', icon: '' },
  { value: 'abwesend', label: 'Abwesend', color: '#666', icon: '' }
];

function StaffManager({ turnierId }) {
  const [staff, setStaff] = useState([]);
  const [turniere, setTurniere] = useState([]);
  const [selectedTurnier, setSelectedTurnier] = useState(turnierId || null);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [editingStaff, setEditingStaff] = useState(null);
  const [saving, setSaving] = useState(false);
  const [activeTab, setActiveTab] = useState('uebersicht');
  const [filterRolle, setFilterRolle] = useState('alle');
  const [expandedCards, setExpandedCards] = useState({});

  const toggleCard = (id) => {
    setExpandedCards(prev => ({ ...prev, [id]: !prev[id] }));
  };

  const [formData, setFormData] = useState({
    vorname: '',
    nachname: '',
    email: '',
    telefon: '',
    primaer_rolle: 'helfer',
    qualifikation: 'keine',
    lizenz_nummer: '',
    verband: '',
    notizen: '',
    erfahrung_jahre: 0
  });

  useEffect(() => {
    fetchTurniere();
    fetchStaff();
  }, []);

  useEffect(() => {
    if (selectedTurnier) {
      fetchStaff();
    }
  }, [selectedTurnier]);

  const fetchTurniere = async () => {
    try {
      const token = localStorage.getItem('token');
      const res = await fetch('/api/turniere', {
        headers: { 'Authorization': `Bearer ${token}` }
      });
      const data = await res.json();
      if (data.data) {
        setTurniere(data.data);
        if (!selectedTurnier && data.data.length > 0) {
          setSelectedTurnier(data.data[0].turnier_id);
        }
      }
    } catch (err) {
      console.error('Fehler beim Laden der Turniere:', err);
    }
  };

  const fetchStaff = async () => {
    setLoading(true);
    try {
      const token = localStorage.getItem('token');
      const res = await fetch('/api/personal', {
        headers: { 'Authorization': `Bearer ${token}` }
      });

      if (res.ok) {
        const data = await res.json();
        if (data.success) {
          // Mapping fr Kompatibilitt
          const mappedStaff = data.data.map(p => ({
            id: p.personal_id,
            name: p.name || `${p.vorname} ${p.nachname}`,
            vorname: p.vorname,
            nachname: p.nachname,
            email: p.email,
            telefon: p.telefon,
            rolle: p.primaer_rolle,
            qualifikation: p.qualifikation,
            lizenz_nummer: p.lizenz_nummer,
            verband: p.verband,
            notizen: p.notizen,
            erfahrung_jahre: p.erfahrung_jahre,
            anzahl_turniere: p.anzahl_turniere,
            status: 'verfuegbar', // Default-Status
            matte: null
          }));
          setStaff(mappedStaff);
        }
      } else {
        setStaff([]);
      }
    } catch (err) {
      console.error('Fehler beim Laden des Personals:', err);
      setStaff([]);
    } finally {
      setLoading(false);
    }
  };

  const handleCreate = async () => {
    setSaving(true);
    try {
      const token = localStorage.getItem('token');
      const url = editingStaff
        ? `/api/personal/${editingStaff.id}`
        : '/api/personal';
      const method = editingStaff ? 'PUT' : 'POST';

      const res = await fetch(url, {
        method,
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      });

      const data = await res.json();

      if (data.success) {
        await fetchStaff();
        setShowModal(false);
        resetForm();
      } else {
        alert(data.message || 'Fehler beim Speichern');
      }
    } catch (err) {
      console.error('Fehler beim Speichern:', err);
      alert('Fehler beim Speichern');
    } finally {
      setSaving(false);
    }
  };

  const handleStatusChange = (staffId, newStatus) => {
    setStaff(staff.map(s =>
      s.id === staffId ? { ...s, status: newStatus } : s
    ));
  };

  const handleAssignToMatte = (staffId, matteNummer) => {
    setStaff(staff.map(s =>
      s.id === staffId
        ? { ...s, matte: matteNummer, status: matteNummer ? 'im_einsatz' : 'verfuegbar' }
        : s
    ));
  };

  const handleDelete = async (staffId) => {
    if (!window.confirm('Mitarbeiter wirklich deaktivieren?')) return;

    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`/api/personal/${staffId}`, {
        method: 'DELETE',
        headers: { 'Authorization': `Bearer ${token}` }
      });

      if (res.ok) {
        await fetchStaff();
      }
    } catch (err) {
      console.error('Fehler beim Lschen:', err);
    }
  };

  const openEditModal = (member) => {
    setEditingStaff(member);
    setFormData({
      vorname: member.vorname || member.name?.split(' ')[0] || '',
      nachname: member.nachname || member.name?.split(' ').slice(1).join(' ') || '',
      email: member.email || '',
      telefon: member.telefon || '',
      primaer_rolle: member.rolle,
      qualifikation: member.qualifikation,
      lizenz_nummer: member.lizenz_nummer || '',
      verband: member.verband || '',
      notizen: member.notizen || '',
      erfahrung_jahre: member.erfahrung_jahre || 0
    });
    setShowModal(true);
  };

  const openNewModal = () => {
    setEditingStaff(null);
    resetForm();
    setShowModal(true);
  };

  const resetForm = () => {
    setFormData({
      vorname: '',
      nachname: '',
      email: '',
      telefon: '',
      primaer_rolle: 'helfer',
      qualifikation: 'keine',
      lizenz_nummer: '',
      verband: '',
      notizen: '',
      erfahrung_jahre: 0
    });
  };

  const getRolleInfo = (rolle) => STAFF_ROLLEN.find(r => r.value === rolle) || STAFF_ROLLEN[6]; // Helfer as default
  const getQualInfo = (qual) => QUALIFIKATIONEN.find(q => q.value === qual) || QUALIFIKATIONEN[0];
  const getStatusInfo = (status) => STATUS_OPTIONEN.find(s => s.value === status) || STATUS_OPTIONEN[0];

  const filteredStaff = filterRolle === 'alle'
    ? staff
    : staff.filter(s => s.rolle === filterRolle);

  const staffStats = {
    gesamt: staff.length,
    verfuegbar: staff.filter(s => s.status === 'verfuegbar').length,
    imEinsatz: staff.filter(s => s.status === 'im_einsatz').length,
    pause: staff.filter(s => s.status === 'pause').length
  };

  if (loading) {
    return (
      <div className="staff-manager loading">
        <div className="loading-spinner"></div>
        <p>Lade Staff-Daten...</p>
      </div>
    );
  }

  return (
    <div className="staff-manager">
      {/* Header */}
      <div className="staff-header">
        <div className="header-title">
          <span className="header-icon"></span>
          <div className="header-text">
            <h1>Staff Management</h1>
            <p>Kampfrichter, Zeitnehmer und Helfer verwalten</p>
          </div>
        </div>
        <div className="header-actions">
          <button className="btn-add" onClick={openNewModal}>
            + Neuer Mitarbeiter
          </button>
        </div>
      </div>

      {/* Statistik-Cards */}
      <div className="staff-stats">
        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-info">
            <span className="stat-value">{staffStats.gesamt}</span>
            <span className="stat-label">Gesamt</span>
          </div>
        </div>
        <div className="stat-card verfuegbar">
          <div className="stat-icon"></div>
          <div className="stat-info">
            <span className="stat-value">{staffStats.verfuegbar}</span>
            <span className="stat-label">Verfgbar</span>
          </div>
        </div>
        <div className="stat-card einsatz">
          <div className="stat-icon"></div>
          <div className="stat-info">
            <span className="stat-value">{staffStats.imEinsatz}</span>
            <span className="stat-label">Im Einsatz</span>
          </div>
        </div>
        <div className="stat-card pause">
          <div className="stat-icon"></div>
          <div className="stat-info">
            <span className="stat-value">{staffStats.pause}</span>
            <span className="stat-label">Pause</span>
          </div>
        </div>
      </div>

      {/* Tabs */}
      <div className="staff-tabs">
        <button
          className={`tab-btn ${activeTab === 'uebersicht' ? 'active' : ''}`}
          onClick={() => setActiveTab('uebersicht')}
        >
           bersicht
        </button>
        <button
          className={`tab-btn ${activeTab === 'einsatzplan' ? 'active' : ''}`}
          onClick={() => setActiveTab('einsatzplan')}
        >
           Einsatzplan
        </button>
        <button
          className={`tab-btn ${activeTab === 'matten' ? 'active' : ''}`}
          onClick={() => setActiveTab('matten')}
        >
           Matten-Zuweisung
        </button>
      </div>

      {/* Filter Buttons */}
      <div className="staff-filter">
        <button
          className={`filter-btn ${filterRolle === 'alle' ? 'active' : ''}`}
          onClick={() => setFilterRolle('alle')}
        >
           Alle
        </button>
        {STAFF_ROLLEN.map(r => (
          <button
            key={r.value}
            className={`filter-btn ${filterRolle === r.value ? 'active' : ''}`}
            style={{ '--filter-color': r.color }}
            onClick={() => setFilterRolle(r.value)}
          >
            {r.icon} {r.label}
          </button>
        ))}
        <span className="filter-count">{filteredStaff.length} Mitarbeiter</span>
      </div>

      {/* Staff Liste */}
      {activeTab === 'uebersicht' && (
        <div className="staff-grid">
          {filteredStaff.length === 0 ? (
            <div className="empty-state">
              <div className="empty-icon"></div>
              <h3>Keine Mitarbeiter gefunden</h3>
              <p>Fgen Sie Kampfrichter und Helfer hinzu</p>
              <button className="btn-add" onClick={openNewModal}>
                + Ersten Mitarbeiter hinzufgen
              </button>
            </div>
          ) : (
            filteredStaff.map(member => {
              const rolleInfo = getRolleInfo(member.rolle);
              const qualInfo = getQualInfo(member.qualifikation);
              const statusInfo = getStatusInfo(member.status);

              const isExpanded = expandedCards[member.id];

              return (
                <div
                  key={member.id}
                  className={`staff-card status-${member.status} ${isExpanded ? 'expanded' : 'collapsed'}`}
                  style={{ '--rolle-color': rolleInfo.color }}
                >
                  <div className="staff-card-header" onClick={() => toggleCard(member.id)}>
                    <div className="staff-avatar" style={{ background: rolleInfo.color }}>
                      {member.name.charAt(0).toUpperCase()}
                    </div>
                    <div className="staff-info">
                      <h3>{member.name}</h3>
                      <span className="staff-rolle">
                        {rolleInfo.icon} {rolleInfo.label}
                      </span>
                    </div>
                    <div
                      className={`staff-status ${member.status === 'im_einsatz' ? 'pulsing' : ''}`}
                      style={{ background: statusInfo.color }}
                    >
                      {statusInfo.icon} {statusInfo.label}
                    </div>
                    <button className="card-toggle">
                      {isExpanded ? '' : ''}
                    </button>
                  </div>

                  {isExpanded && (
                    <>
                      <div className="staff-details">
                        <div className="detail-item">
                          <span className="detail-label">Qualifikation:</span>
                          <span className="detail-value">{qualInfo.icon} {qualInfo.label}</span>
                        </div>
                        {member.lizenz_nummer && (
                          <div className="detail-item">
                            <span className="detail-label">Lizenz:</span>
                            <span className="detail-value">{member.lizenz_nummer}</span>
                          </div>
                        )}
                        {member.anzahl_turniere > 0 && (
                          <div className="detail-item">
                            <span className="detail-label">Turniere:</span>
                            <span className="detail-value">{member.anzahl_turniere} Einstze</span>
                          </div>
                        )}
                        {member.matte && (
                          <div className="detail-item highlight">
                            <span className="detail-label">Eingesetzt auf:</span>
                            <span className="detail-value"> Matte {member.matte}</span>
                          </div>
                        )}
                      </div>

                      {/* Status Buttons */}
                      <div className="status-buttons">
                        {STATUS_OPTIONEN.map(s => (
                          <button
                            key={s.value}
                            className={`status-btn ${member.status === s.value ? 'active' : ''}`}
                            style={{ '--status-color': s.color }}
                            onClick={() => handleStatusChange(member.id, s.value)}
                          >
                            {s.icon}
                          </button>
                        ))}
                      </div>

                      {/* Aktionen */}
                      <div className="staff-actions">
                        <button className="btn-edit" onClick={() => openEditModal(member)}>
                          Bearbeiten
                        </button>
                        <button className="btn-delete" onClick={() => handleDelete(member.id)}>
                          
                        </button>
                      </div>
                    </>
                  )}
                </div>
              );
            })
          )}
        </div>
      )}

      {/* Einsatzplan Tab */}
      {activeTab === 'einsatzplan' && (
        <div className="einsatzplan">
          <div className="coming-soon">
            <span className="icon"></span>
            <h3>Einsatzplan</h3>
            <p>Zeitliche Planung der Einstze - Demnchst verfgbar</p>
          </div>
        </div>
      )}

      {/* Matten-Zuweisung Tab */}
      {activeTab === 'matten' && (
        <div className="matten-zuweisung">
          <div className="matten-grid">
            {[1, 2, 3, 4].map(matteNr => {
              const zugewiesen = staff.filter(s => s.matte === matteNr);
              return (
                <div key={matteNr} className="matte-box">
                  <div className="matte-header">
                    <span className="matte-nummer"> Matte {matteNr}</span>
                    <span className="matte-count">{zugewiesen.length} Mitarbeiter</span>
                  </div>
                  <div className="matte-staff">
                    {zugewiesen.map(member => (
                      <div key={member.id} className="matte-staff-item">
                        <span>{getRolleInfo(member.rolle).icon} {member.name}</span>
                        <button
                          className="btn-remove"
                          onClick={() => handleAssignToMatte(member.id, null)}
                        >
                          
                        </button>
                      </div>
                    ))}
                    {zugewiesen.length === 0 && (
                      <p className="no-staff">Keine Zuweisung</p>
                    )}
                  </div>
                  <div className="matte-assign">
                    <select
                      onChange={(e) => {
                        if (e.target.value) {
                          handleAssignToMatte(parseInt(e.target.value), matteNr);
                          e.target.value = '';
                        }
                      }}
                    >
                      <option value="">+ Mitarbeiter zuweisen</option>
                      {staff.filter(s => !s.matte && s.status === 'verfuegbar').map(s => (
                        <option key={s.id} value={s.id}>
                          {getRolleInfo(s.rolle).icon} {s.name}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      )}

      {/* Modal */}
      {showModal && (
        <div className="modal-overlay" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={e => e.stopPropagation()}>
            <div className="modal-header">
              <h2>{editingStaff ? 'Mitarbeiter bearbeiten' : 'Neuer Mitarbeiter'}</h2>
              <button className="modal-close" onClick={() => setShowModal(false)}></button>
            </div>

            <div className="modal-body">
              <div className="form-row">
                <div className="form-group">
                  <label>Vorname *</label>
                  <input
                    type="text"
                    value={formData.vorname}
                    onChange={(e) => setFormData({ ...formData, vorname: e.target.value })}
                    placeholder="Vorname"
                  />
                </div>
                <div className="form-group">
                  <label>Nachname *</label>
                  <input
                    type="text"
                    value={formData.nachname}
                    onChange={(e) => setFormData({ ...formData, nachname: e.target.value })}
                    placeholder="Nachname"
                  />
                </div>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>E-Mail</label>
                  <input
                    type="email"
                    value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    placeholder="email@beispiel.de"
                  />
                </div>
                <div className="form-group">
                  <label>Telefon</label>
                  <input
                    type="tel"
                    value={formData.telefon}
                    onChange={(e) => setFormData({ ...formData, telefon: e.target.value })}
                    placeholder="+49 123 456789"
                  />
                </div>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>Primre Rolle</label>
                  <select
                    value={formData.primaer_rolle}
                    onChange={(e) => setFormData({ ...formData, primaer_rolle: e.target.value })}
                  >
                    {STAFF_ROLLEN.map(r => (
                      <option key={r.value} value={r.value}>
                        {r.icon} {r.label}
                      </option>
                    ))}
                  </select>
                </div>
                <div className="form-group">
                  <label>Qualifikation</label>
                  <select
                    value={formData.qualifikation}
                    onChange={(e) => setFormData({ ...formData, qualifikation: e.target.value })}
                  >
                    {QUALIFIKATIONEN.map(q => (
                      <option key={q.value} value={q.value}>
                        {q.icon} {q.label}
                      </option>
                    ))}
                  </select>
                </div>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label>Lizenz-Nr.</label>
                  <input
                    type="text"
                    value={formData.lizenz_nummer}
                    onChange={(e) => setFormData({ ...formData, lizenz_nummer: e.target.value })}
                    placeholder="z.B. DTU-12345"
                  />
                </div>
                <div className="form-group">
                  <label>Verband</label>
                  <input
                    type="text"
                    value={formData.verband}
                    onChange={(e) => setFormData({ ...formData, verband: e.target.value })}
                    placeholder="z.B. DTU, WTF"
                  />
                </div>
              </div>

              <div className="form-group">
                <label>Erfahrung (Jahre)</label>
                <input
                  type="number"
                  min="0"
                  max="50"
                  value={formData.erfahrung_jahre}
                  onChange={(e) => setFormData({ ...formData, erfahrung_jahre: parseInt(e.target.value) || 0 })}
                />
              </div>

              <div className="form-group">
                <label>Notizen</label>
                <textarea
                  value={formData.notizen}
                  onChange={(e) => setFormData({ ...formData, notizen: e.target.value })}
                  placeholder="Besondere Hinweise, Einschrnkungen..."
                  rows={3}
                />
              </div>
            </div>

            <div className="modal-footer">
              <button className="btn-cancel" onClick={() => setShowModal(false)}>
                Abbrechen
              </button>
              <button
                className="btn-save"
                onClick={handleCreate}
                disabled={saving || !formData.vorname || !formData.nachname}
              >
                {saving ? 'Speichern...' : (editingStaff ? 'Aktualisieren' : 'Hinzufgen')}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default StaffManager;
