// TurnierDashboard.jsx - TDA DARK THEME
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './TurnierDashboard.css';

function TurnierDashboard() {
  const navigate = useNavigate();
  const [turniere, setTurniere] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterStatus, setFilterStatus] = useState('');
  const [stats, setStats] = useState({
    total: 0,
    upcoming: 0,
    active: 0,
    completed: 0,
    vereine: 0,
    wettkaempfer: 0
  });

  useEffect(() => {
    fetchTurniere();
    fetchExtraStats();
  }, []);

  const fetchExtraStats = async () => {
    try {
      const token = localStorage.getItem('token');
      const headers = {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      };

      const [vereineRes, wettkaempferRes] = await Promise.all([
        fetch('/api/vereine', { headers }).catch(() => null),
        fetch('/api/wettkaempfer/admin/stats', { headers }).catch(() => null)
      ]);

      let vereineCount = 0;
      let wettkaempferCount = 0;

      if (vereineRes?.ok) {
        const vereineData = await vereineRes.json();
        if (vereineData.success && Array.isArray(vereineData.data)) {
          vereineCount = vereineData.data.length;
        } else if (Array.isArray(vereineData)) {
          vereineCount = vereineData.length;
        } else if (vereineData.count !== undefined) {
          vereineCount = vereineData.count;
        }
      }

      if (wettkaempferRes?.ok) {
        const wkData = await wettkaempferRes.json();
        wettkaempferCount = wkData.totalWettkaempfer || wkData.total || wkData.count || 0;
      }

      setStats(prev => ({
        ...prev,
        vereine: vereineCount,
        wettkaempfer: wettkaempferCount
      }));
    } catch (err) {
      console.error('Fehler beim Laden der Extra-Statistiken:', err);
    }
  };

  useEffect(() => {
    calculateStats();
  }, [turniere]);

  const fetchTurniere = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch('/api/turniere', {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });

      if (!response.ok) {
        throw new Error('Fehler beim Abrufen der Turniere');
      }

      const result = await response.json();

      let turniereList = [];
      if (result.success && result.data) {
        turniereList = Array.isArray(result.data) ? result.data : [];
      } else if (Array.isArray(result)) {
        turniereList = result;
      }

      // Kategorien fr jedes Turnier laden
      const turniereWithKategorien = await Promise.all(
        turniereList.map(async (turnier) => {
          try {
            const katResponse = await fetch(`/api/turniere/${turnier.turnier_id}/kategorien`, {
              headers: {
                'Authorization': `Bearer ${token}`,
                'Content-Type': 'application/json'
              }
            });
            if (katResponse.ok) {
              const katResult = await katResponse.json();
              return {
                ...turnier,
                kategorien: katResult.data || katResult || []
              };
            }
          } catch (e) {
            console.error('Fehler beim Laden der Kategorien fr Turnier', turnier.turnier_id, e);
          }
          return { ...turnier, kategorien: [] };
        })
      );

      setTurniere(turniereWithKategorien);
      setLoading(false);
    } catch (err) {
      console.error('Fehler:', err);
      setError(err.message);
      setLoading(false);
    }
  };

  const calculateStats = () => {
    if (!turniere.length) return;

    const now = new Date();
    const newStats = {
      ...stats,
      total: turniere.length,
      upcoming: turniere.filter(t => new Date(t.start_datum || t.datum) > now).length,
      active: turniere.filter(t => {
        const start = new Date(t.start_datum || t.datum);
        const end = new Date(t.end_datum || t.datum);
        return start <= now && now <= end;
      }).length,
      completed: turniere.filter(t => new Date(t.end_datum || t.datum) < now).length
    };

    setStats(newStats);
  };

  const handleAddTurnier = () => {
    navigate('/dashboard/turnier-bearbeiten/neu');
  };

  const handleEditTurnier = (turnier) => {
    navigate(`/dashboard/turnier-bearbeiten/${turnier.turnier_id}`, {
      state: { turnier }
    });
  };

  const handleDeleteTurnier = async (id) => {
    if (!window.confirm('Sind Sie sicher, dass Sie dieses Turnier lschen mchten?')) {
      return;
    }

    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`/api/turniere/${id}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (!response.ok) {
        throw new Error('Fehler beim Lschen des Turniers');
      }

      await fetchTurniere();
    } catch (err) {
      console.error('Fehler:', err);
      setError(err.message);
    }
  };

  const handleToggleVeroeffentlicht = async (turnier) => {
    try {
      const token = localStorage.getItem('token');
      const newValue = !turnier.veroeffentlicht;

      const response = await fetch(`/api/turniere/${turnier.turnier_id}/veroeffentlichen`, {
        method: 'PATCH',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ veroeffentlicht: newValue })
      });

      if (!response.ok) {
        throw new Error('Fehler beim ndern der Sichtbarkeit');
      }

      // Update local state
      setTurniere(prev => prev.map(t =>
        t.turnier_id === turnier.turnier_id
          ? { ...t, veroeffentlicht: newValue ? 1 : 0 }
          : t
      ));
    } catch (err) {
      console.error('Fehler:', err);
      alert('Fehler: ' + err.message);
    }
  };

  const getFilteredTurniere = () => {
    return turniere.filter(t => {
      const matchesSearch = t.name.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesStatus = !filterStatus || getTurnierStatus(t) === filterStatus;
      return matchesSearch && matchesStatus;
    });
  };

  const getTurnierStatus = (turnier) => {
    const now = new Date();
    const start = new Date(turnier.start_datum || turnier.datum);
    const end = new Date(turnier.end_datum || turnier.datum);

    if (start > now) return 'upcoming';
    if (start <= now && now <= end) return 'active';
    return 'completed';
  };

  const formatDate = (dateString) => {
    if (!dateString) return 'TBD';
    try {
      const date = new Date(dateString);
      if (isNaN(date.getTime())) return 'TBD';
      return date.toLocaleDateString('de-DE', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric'
      });
    } catch {
      return 'TBD';
    }
  };

  const getStatusBadge = (status) => {
    switch (status) {
      case 'upcoming': return { text: 'Geplant', class: 'status-upcoming' };
      case 'active': return { text: 'Laufend', class: 'status-active' };
      case 'completed': return { text: 'Abgeschlossen', class: 'status-completed' };
      default: return { text: 'Unbekannt', class: '' };
    }
  };

  const formatKategorie = (kat) => {
    // Kategorie-Namen schner formatieren
    const kategorieMap = {
      'kumite': 'Kumite',
      'kata': 'Kata',
      'formen': 'Formen',
      'point_fighting': 'Point Fighting',
      'kickboxen': 'Kickboxen',
      'light_contact': 'Light Contact',
      'full_contact': 'Full Contact',
      'musical_forms': 'Musical Forms',
      'weapons': 'Waffen',
      'team': 'Team'
    };
    return kategorieMap[kat] || kat;
  };

  if (loading) {
    return (
      <div className="turnier-dashboard">
        <div className="dashboard-loading">
          <div className="loading-spinner"></div>
          <p>Turniere werden geladen...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="turnier-dashboard">
        <div className="dashboard-error">
          <h3>Fehler</h3>
          <p>{error}</p>
          <button className="btn btn-primary" onClick={fetchTurniere}>Erneut versuchen</button>
        </div>
      </div>
    );
  }

  const filteredTurniere = getFilteredTurniere();

  return (
    <div className="turnier-dashboard">
      {/* Header */}
      <div className="dashboard-header">
        <div className="header-title">
          <h1>Turnierverwaltung</h1>
          <p>Erstellen und verwalten Sie Ihre Turniere</p>
        </div>

        <div className="header-actions">
          <div className="search-box">
            <input
              type="text"
              placeholder="Turnier suchen..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>

          <select
            className="filter-select"
            value={filterStatus}
            onChange={(e) => setFilterStatus(e.target.value)}
          >
            <option value="">Alle Status</option>
            <option value="upcoming">Geplant</option>
            <option value="active">Laufend</option>
            <option value="completed">Abgeschlossen</option>
          </select>

          <button className="btn btn-primary" onClick={handleAddTurnier}>
            + Neues Turnier
          </button>
        </div>
      </div>

      {/* Statistics */}
      <div className="stats-grid">
        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-content">
            <h3>{stats.total}</h3>
            <p>Turniere gesamt</p>
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-content">
            <h3>{stats.upcoming}</h3>
            <p>Geplant</p>
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-content">
            <h3>{stats.active}</h3>
            <p>Laufend</p>
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-content">
            <h3>{stats.completed}</h3>
            <p>Abgeschlossen</p>
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-content">
            <h3>{stats.vereine}</h3>
            <p>Vereine</p>
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-icon"></div>
          <div className="stat-content">
            <h3>{stats.wettkaempfer}</h3>
            <p>Wettkmpfer</p>
          </div>
        </div>
      </div>

      {/* Turnier Liste */}
      {filteredTurniere.length === 0 ? (
        <div className="empty-state">
          <div className="empty-icon"></div>
          <h3>Keine Turniere gefunden</h3>
          <p>Erstellen Sie ein neues Turnier, um zu beginnen.</p>
          <button className="btn btn-primary" onClick={handleAddTurnier}>
            + Neues Turnier erstellen
          </button>
        </div>
      ) : (
        <div className="turnier-grid">
          {filteredTurniere.map(turnier => {
            const status = getTurnierStatus(turnier);
            const badge = getStatusBadge(status);

            return (
              <div key={turnier.turnier_id} className="turnier-card">
                <div className="card-header">
                  <div className="card-title">
                    <h3>{turnier.name}</h3>
                    <span className="card-subtitle">{turnier.disziplin || turnier.sportart || 'Kampfsport'}</span>
                  </div>
                  <span className={`status-badge ${badge.class}`}>
                    {badge.text}
                  </span>
                </div>

                <div className="card-body">
                  <div className="info-row">
                    <span className="info-icon"></span>
                    <span className="info-label">Datum:</span>
                    <span className="info-value">
                      {formatDate(turnier.datum || turnier.start_datum)}
                      {turnier.end_datum && turnier.end_datum !== turnier.datum &&
                        ` - ${formatDate(turnier.end_datum)}`
                      }
                    </span>
                  </div>

                  <div className="info-row">
                    <span className="info-icon"></span>
                    <span className="info-label">Ort:</span>
                    <span className="info-value">{turnier.ort || 'TBD'}</span>
                  </div>

                  <div className="info-row">
                    <span className="info-icon"></span>
                    <span className="info-label">Max. Teilnehmer:</span>
                    <span className="info-value">{turnier.max_teilnehmer || ''}</span>
                  </div>

                  {/* Kategorien */}
                  <div className="kategorien-row">
                    <div className="kategorien-label">Kategorien</div>
                    {turnier.kategorien && turnier.kategorien.length > 0 ? (
                      <div className="kategorien-tags">
                        {turnier.kategorien.map((kat, idx) => (
                          <span key={idx} className="kategorie-tag">
                            {formatKategorie(typeof kat === 'string' ? kat : kat.kategorie)}
                          </span>
                        ))}
                      </div>
                    ) : (
                      <span className="no-kategorien">Keine Kategorien definiert</span>
                    )}
                  </div>
                </div>

                <div className="card-footer">
                  <button
                    className="btn btn-danger btn-sm"
                    onClick={() => handleDeleteTurnier(turnier.turnier_id)}
                  >
                     Lschen
                  </button>
                  <button
                    className={`btn btn-sm ${turnier.veroeffentlicht ? 'btn-success' : 'btn-warning'}`}
                    onClick={() => handleToggleVeroeffentlicht(turnier)}
                    title={turnier.veroeffentlicht ? 'Turnier verbergen' : 'Turnier verffentlichen'}
                  >
                    {turnier.veroeffentlicht ? ' Online' : ' Privat'}
                  </button>
                  <button
                    className="btn btn-primary btn-sm"
                    onClick={() => handleEditTurnier(turnier)}
                  >
                     Bearbeiten
                  </button>
                </div>
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
}

export default TurnierDashboard;
