// src/components/registration/WettkampferAuswahlModal.jsx - VOLLSTNDIG KORRIGIERT
import React, { useState, useEffect } from 'react';
import '../../styles/WettkampferAuswahlModal.css';

const WettkampferAuswahlModal = ({ turnier, onClose, onAnmeldungAbgeschlossen, preselectedVerein, editingWettkaempfer }) => {
  const [currentStep, setCurrentStep] = useState(1);
  const [userRole, setUserRole] = useState('');
  const [userVereinId, setUserVereinId] = useState(null);

  // Schritt 1: Verein-Auswahl (nur fr Admin)
  const [vereine, setVereine] = useState([]);
  const [selectedVerein, setSelectedVerein] = useState(preselectedVerein || null);
  
  // Schritt 2: Wettkmpfer-Auswahl
  const [wettkampfer, setWettkampfer] = useState([]);
  const [selectedWettkampfer, setSelectedWettkampfer] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');

  // Schritt 3: Divisionen-Auswahl
  const [alleDivisionen, setAlleDivisionen] = useState([]); // Alle Divisionen aus DB
  const [wettkampferDivisionen, setWettkampferDivisionen] = useState({}); // { wettkaempfer_id: { passend: [], ausgewaehlt: [] } }
  const [activeWettkampferTab, setActiveWettkampferTab] = useState(null); // Aktiver Tab in Schritt 3
  // Filter fr Unterkategorien pro Typ (z.B. { formen: 'alle', kickboxen: 'alle' })
  const [categoryFilters, setCategoryFilters] = useState({});
  const [activeMainCategory, setActiveMainCategory] = useState('alle'); // Haupt-Kategorie-Filter
  const [divisionSearch, setDivisionSearch] = useState(''); // Suchfeld fr Divisionen
  const [divisionSort, setDivisionSort] = useState('code'); // Sortierung: code, alter, gewicht, skill
  const [showSummary, setShowSummary] = useState(false); // Zusammenfassung anzeigen
  const [favoriten, setFavoriten] = useState(() => {
    // Favoriten aus localStorage laden
    const saved = localStorage.getItem('divisionFavoriten');
    return saved ? JSON.parse(saved) : [];
  });

  // Wettkmpfer-Bearbeitung
  const [editingWettkampfer, setEditingWettkampfer] = useState(null);
  const [editFormData, setEditFormData] = useState({});
  const [savingEdit, setSavingEdit] = useState(false);

  // Loading & Error States
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [submitting, setSubmitting] = useState(false);

  useEffect(() => {
    initializeModal();
  }, []);

  // Wenn editingWettkaempfer gesetzt ist, direkt zu Schritt 3 springen
  useEffect(() => {
    if (editingWettkaempfer && wettkampfer.length > 0 && currentStep === 2) {
      console.log(' Bearbeitungsmodus fr Wettkmpfer:', editingWettkaempfer);
      // Pre-select den Wettkmpfer
      setSelectedWettkampfer([editingWettkaempfer.wettkaempfer_id]);
      // Direkt zu Schritt 3
      setCurrentStep(3);
    }
  }, [editingWettkaempfer, wettkampfer, currentStep]);

  // Divisionen laden wenn Schritt 3 erreicht und noch nicht geladen
  useEffect(() => {
    if (currentStep === 3 && alleDivisionen.length === 0 && selectedWettkampfer.length > 0 && !loading) {
      console.log(' Lade Divisionen fr Schritt 3...');
      loadDivisionen();
    }
  }, [currentStep, alleDivisionen.length, selectedWettkampfer.length, loading]);

  //  VOLLSTNDIG KORRIGIERTE getAuthToken FUNKTION
  const getAuthToken = () => {
    console.log(' Token-Extraktion gestartet...');
    
    // Versuche beide localStorage-Keys
    const clubDataStr = localStorage.getItem('clubData');
    const adminDataStr = localStorage.getItem('adminData');
    
    console.log(' ClubData vorhanden:', !!clubDataStr);
    console.log(' AdminData vorhanden:', !!adminDataStr);
    
    if (clubDataStr) {
      try {
        const clubData = JSON.parse(clubDataStr);
        console.log(' ClubData Structure:', Object.keys(clubData));
        console.log(' ClubData Token verfgbar:', !!clubData.token);
        
        if (clubData.token && typeof clubData.token === 'string' && clubData.token.length > 10) {
          console.log(' Token aus ClubData extrahiert:', clubData.token.substring(0, 20) + '...');
          return clubData.token;
        }
      } catch (e) {
        console.error(' ClubData Parse Error:', e);
      }
    }
    
    if (adminDataStr) {
      try {
        const adminData = JSON.parse(adminDataStr);
        console.log(' AdminData Structure:', Object.keys(adminData));
        console.log(' AdminData Token verfgbar:', !!adminData.token);
        
        if (adminData.token && typeof adminData.token === 'string' && adminData.token.length > 10) {
          console.log(' Token aus AdminData extrahiert:', adminData.token.substring(0, 20) + '...');
          return adminData.token;
        }
      } catch (e) {
        console.error(' AdminData Parse Error:', e);
      }
    }
    
    //  FALLBACK: Versuche alte Token-Keys
    const fallbackToken = localStorage.getItem('authToken') || localStorage.getItem('token');
    if (fallbackToken && typeof fallbackToken === 'string' && fallbackToken.length > 10) {
      console.log(' Fallback Token gefunden:', fallbackToken.substring(0, 20) + '...');
      return fallbackToken;
    }
    
    console.error(' Kein gltiger Token gefunden in localStorage');
    console.error(' localStorage Keys:', Object.keys(localStorage));
    
    return null;
  };

  //  KORRIGIERTE getUserData FUNKTION
  const getUserData = () => {
    console.log(' User-Daten-Extraktion gestartet...');
    
    const clubDataStr = localStorage.getItem('clubData');
    const adminDataStr = localStorage.getItem('adminData');
    
    if (clubDataStr) {
      try {
        const clubData = JSON.parse(clubDataStr);
        console.log(' ClubData User:', clubData.user);
        
        if (clubData.user) {
          return {
            role: clubData.user.role || 'verein',
            vereinId: clubData.user.vereins_id,
            name: clubData.user.name,
            email: clubData.user.email,
            token: clubData.token
          };
        }
      } catch (e) {
        console.error(' ClubData User Parse Error:', e);
      }
    }
    
    if (adminDataStr) {
      try {
        const adminData = JSON.parse(adminDataStr);
        console.log(' AdminData User:', adminData.user);
        
        if (adminData.user) {
          return {
            role: 'admin',
            vereinId: null,
            name: adminData.user.name || 'Administrator',
            email: adminData.user.email,
            token: adminData.token
          };
        }
      } catch (e) {
        console.error(' AdminData User Parse Error:', e);
      }
    }
    
    console.error(' Keine gltigen User-Daten gefunden');
    return null;
  };

  const initializeModal = async () => {
    try {
      //  KORRIGIERTE User-Daten-Extraktion
      const userData = getUserData();

      if (!userData) {
        throw new Error('Nicht angemeldet. Bitte loggen Sie sich ein.');
      }

      const { role, vereinId, name, token } = userData;

      if (!token) {
        throw new Error('Kein Authentifizierungstoken gefunden. Bitte loggen Sie sich erneut ein.');
      }

      setUserRole(role);
      setUserVereinId(vereinId);

      console.log(' User-Daten initialisiert:', { role, vereinId, name, preselectedVerein });

      // Wenn ein Verein vorausgewhlt wurde (z.B. von VereinDetailPage), direkt zu Schritt 2
      if (preselectedVerein && preselectedVerein.vereins_id) {
        console.log(' Vorausgewhlter Verein:', preselectedVerein);
        setSelectedVerein(preselectedVerein);
        setCurrentStep(2);
        await loadWettkampfer(preselectedVerein.vereins_id);
        return;
      }

      if (role === 'admin') {
        // Admin: Zeige Verein-Auswahl
        setCurrentStep(1);
        await loadVereine();
      } else {
        // Verein: Lade direkt Wettkmpfer
        if (!vereinId) {
          throw new Error('Verein-ID nicht gefunden. Bitte loggen Sie sich erneut ein.');
        }

        setSelectedVerein({
          vereins_id: vereinId,
          name: name || 'Mein Verein'
        });
        setCurrentStep(2);
        await loadWettkampfer(vereinId);
      }

    } catch (err) {
      console.error(' Initialisierung fehlgeschlagen:', err);
      setError(err.message);
    }
  };

  //  KORRIGIERTE loadVereine FUNKTION
  const loadVereine = async () => {
    setLoading(true);
    try {
      const token = getAuthToken();
      
      if (!token) {
        throw new Error('Kein Authentifizierungstoken gefunden');
      }
      
      console.log(' Lade Vereine...');
      
      const response = await fetch(`/api/vereine`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });

      console.log(' Vereine Response Status:', response.status);

      if (!response.ok) {
        if (response.status === 401) {
          throw new Error('Session abgelaufen. Bitte loggen Sie sich erneut ein.');
        }
        throw new Error(`Fehler beim Laden der Vereine: ${response.status}`);
      }

      const data = await response.json();
      console.log(' Vereine-Daten:', data);
      
      let vereineData = [];
      
      if (data.success && Array.isArray(data.data)) {
        vereineData = data.data;
      } else if (Array.isArray(data)) {
        vereineData = data;
      }

      // Nur aktive Vereine anzeigen
      const aktiveVereine = vereineData.filter(v => v.rolle === 'verein' || !v.rolle);
      setVereine(aktiveVereine);
      
      console.log(' Vereine geladen:', aktiveVereine.length);
    } catch (err) {
      console.error(' Fehler beim Laden der Vereine:', err);
      setError('Fehler beim Laden der Vereine: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  //  VOLLSTNDIG KORRIGIERTE loadWettkampfer FUNKTION - CORS-PROBLEM BEHOBEN
  const loadWettkampfer = async (vereinId) => {
    console.log(' loadWettkampfer gestartet fr Verein:', vereinId);
    setLoading(true);
    setError(''); // Clear previous errors
    
    try {
      const token = getAuthToken();
      
      if (!token) {
        throw new Error('Kein Authentifizierungstoken gefunden. Bitte loggen Sie sich erneut ein.');
      }
      
      console.log(' Token verfgbar fr Wettkmpfer-API');
      console.log(' User-Role:', userRole);
      console.log(' User-Vereins-ID:', userVereinId);
      
      const url = `/api/wettkaempfer/verein/${vereinId}`;
      console.log(' API-URL:', url);
      
      //  KORRIGIERT: Minimale Headers - KEIN cache-control!
      const headers = {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      };
      
      console.log(' Request Headers:', headers);
      
      const response = await fetch(url, {
        method: 'GET',
        headers: headers
        //  ENTFERNT: cache, X-Requested-With, Cache-Control
      });
      
      console.log(' Wettkmpfer Response Status:', response.status);
      console.log(' Response Headers:', Object.fromEntries(response.headers.entries()));
      
      if (!response.ok) {
        let errorMessage = `HTTP ${response.status}`;
        
        try {
          const errorText = await response.text();
          console.error(' API Error Response:', errorText);
          
          if (response.status === 401) {
            // Auth-Fehler: Token abgelaufen oder ungltig
            errorMessage = 'Authentifizierungsfehler. Bitte schlieen Sie das Modal und versuchen Sie es erneut.';
          } else if (response.status === 403) {
            errorMessage = 'Zugriff verweigert. Sie haben keine Berechtigung fr diese Wettkmpfer.';
          } else if (response.status === 400) {
            errorMessage = `Ungltige Anfrage: ${errorText}`;
          } else if (response.status === 500) {
            errorMessage = 'Server-Fehler. Bitte versuchen Sie es spter erneut.';
          } else if (response.status === 0) {
            errorMessage = 'Netzwerk-Fehler. Prfen Sie Ihre Internetverbindung.';
          }
        } catch (e) {
          console.error(' Error parsing response:', e);
          errorMessage = 'Unbekannter Server-Fehler aufgetreten.';
        }
        
        throw new Error(errorMessage);
      }
      
      const data = await response.json();
      console.log(' Wettkmpfer-Daten empfangen:', data);
      
      let wettkampferData = [];
      
      if (data.success && Array.isArray(data.data)) {
        wettkampferData = data.data;
      } else if (Array.isArray(data)) {
        wettkampferData = data;
      } else {
        console.warn(' Unerwartetes Datenformat:', data);
        throw new Error('Unerwartetes Datenformat vom Server erhalten.');
      }

      setWettkampfer(wettkampferData);
      console.log(` ${wettkampferData.length} Wettkmpfer erfolgreich geladen`);
      
      if (wettkampferData.length === 0) {
        console.log(' Keine Wettkmpfer fr Verein', vereinId, 'gefunden');
        setError(`Keine Wettkmpfer fr diesen Verein gefunden.`);
      }
      
    } catch (err) {
      console.error(' Fehler beim Laden der Wettkmpfer:', err);
      
      //  SPEZIELLE CORS-FEHLER-BEHANDLUNG
      if (err.message.includes('Failed to fetch') || err.name === 'TypeError') {
        console.error(' CORS oder Netzwerk-Fehler erkannt');
        setError('Verbindungsfehler zum Server. Bitte prfen Sie:\n Ist das Backend gestartet?\n CORS-Konfiguration korrekt?\n Internetverbindung verfgbar?');
      } else {
        setError('Fehler beim Laden der Wettkmpfer: ' + err.message);
      }
      
      setWettkampfer([]);
      
      //  ZUSTZLICHE ERROR-LOGGING
      console.error(' Error Details:', {
        message: err.message,
        name: err.name,
        stack: err.stack,
        vereinId: vereinId,
        userRole: userRole,
        userVereinId: userVereinId,
        timestamp: new Date().toISOString(),
        url: `/api/wettkaempfer/verein/${vereinId}`
      });
      
    } finally {
      setLoading(false);
    }
  };

  const handleVereinSelect = async (verein) => {
    console.log(' Verein ausgewhlt:', verein);
    setSelectedVerein(verein);
    setCurrentStep(2);
    await loadWettkampfer(verein.vereins_id);
  };

  const handleWettkampferToggle = (wettkampferId) => {
    setSelectedWettkampfer(prev => {
      if (prev.includes(wettkampferId)) {
        return prev.filter(id => id !== wettkampferId);
      } else {
        return [...prev, wettkampferId];
      }
    });
  };

  const handleSelectAll = () => {
    const filteredIds = filteredWettkampfer.map(w => w.wettkaempfer_id);
    if (selectedWettkampfer.length === filteredIds.length) {
      setSelectedWettkampfer([]);
    } else {
      setSelectedWettkampfer(filteredIds);
    }
  };

  // Divisionen laden und fr jeden Wettkmpfer filtern
  const loadDivisionen = async () => {
    setLoading(true);
    setError('');

    try {
      const token = getAuthToken();

      if (!token) {
        throw new Error('Kein Authentifizierungstoken gefunden');
      }

      // Lade alle Divisionen
      console.log(' Lade Divisionen...');
      const response = await fetch('/api/divisionen?limit=2000', {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });

      if (!response.ok) {
        throw new Error(`Fehler beim Laden der Divisionen: ${response.status}`);
      }

      const data = await response.json();
      console.log(' Divisionen geladen:', data);

      let divisionen = [];
      if (data.success && Array.isArray(data.data)) {
        divisionen = data.data;
      } else if (Array.isArray(data)) {
        divisionen = data;
      }

      setAlleDivisionen(divisionen);
      console.log(` ${divisionen.length} Divisionen geladen`);

      // Skill-Level Reihenfolge und Mapping
      const skillLevelOrder = ['Beginner', 'Intermediate', 'Advanced', 'Black Belts'];

      // Basis Skill-Level Mapping (Wettkmpfer -> Divisionen)
      const skillLevelMapping = {
        'beginner': ['Beginner', 'Amateur', 'Rookie Grappling'],
        'intermediate': ['Intermediate', 'Amateur', 'Advanced Grappling'],
        'advanced': ['Advanced', 'Amateur', 'Master Grappling'],
        'black belt': ['Black Belts', 'Advanced', 'Master Grappling', 'Grand Champion']
      };

      // Erweiterte Skills fr Formen/Selbstverteidigung (eine Stufe hher erlaubt)
      const skillLevelMappingFormen = {
        'beginner': ['Beginner', 'Intermediate', 'Amateur', 'Rookie Grappling'],
        'intermediate': ['Intermediate', 'Advanced', 'Amateur', 'Advanced Grappling'],
        'advanced': ['Advanced', 'Black Belts', 'Amateur', 'Master Grappling'],
        'black belt': ['Black Belts', 'Advanced', 'Master Grappling', 'Grand Champion']
      };

      // Kategorien die Formen/Selbstverteidigung sind (Skill eine Stufe hher)
      const formenTypes = ['Formen', 'Selbstverteidigung', 'Forms', 'Self Defense', 'Breaking'];
      // Kategorien die Kampf sind (Alter/Gewicht eine Klasse hher)
      const kampfTypes = [
        'Kumite', 'Sparring', 'Fighting', 'Combat', 'Grappling',
        'Kickboxen', 'Kickboxing', 'Pointfighting', 'Continous Fighting',
        'Continuous Fighting', 'Fullcontact', 'Full Contact', 'K1', 'K-1',
        'Rumble'
      ];

      // Fr jeden ausgewhlten Wettkmpfer passende Divisionen filtern
      const wettkampferDivisionenMap = {};

      selectedWettkampfer.forEach(wId => {
        const w = wettkampfer.find(wk => wk.wettkaempfer_id === wId);
        if (!w) return;

        const alter = calculateAge(w.geburtsdatum);
        const gewicht = parseFloat(w.gewicht) || 0;
        const geschlecht = w.geschlecht?.toLowerCase();
        const skillLevel = w.skill_level?.toLowerCase();

        console.log(` Filtere fr ${w.vorname} ${w.nachname}:`, {
          alter, gewicht, geschlecht, skillLevel
        });

        // Finde passende Divisionen
        const passendeDivisionen = divisionen.filter(div => {
          const divType = div.Division_Type || '';
          const isFormen = formenTypes.some(t => divType.toLowerCase().includes(t.toLowerCase()));
          const isKampf = kampfTypes.some(t => divType.toLowerCase().includes(t.toLowerCase()));

          // Alterscheck
          const ageFrom = parseInt(div.Age_from);
          const ageTo = parseInt(div.Age_to);

          if (!isNaN(ageFrom) && !isNaN(ageTo)) {
            if (isKampf) {
              // Kampf: Eine Altersklasse hher erlaubt
              // Wettkmpfer passt wenn sein Alter in der Klasse ist ODER
              // wenn er in der nchst-niedrigeren Klasse wre (d.h. er ist jnger als ageFrom aber max. eine Klasse)
              const alterPasstExakt = alter >= ageFrom && alter <= ageTo;
              const alterPasstHoeher = alter < ageFrom && alter >= (ageFrom - 5); // ca. eine Altersklasse (~5 Jahre)
              if (!alterPasstExakt && !alterPasstHoeher) return false;
            } else {
              // Formen/Andere: Exakte Alterspassung
              if (alter < ageFrom || alter > ageTo) return false;
            }
          }

          // Gewichtscheck
          const minWeight = parseFloat(div.Minimun_Weight);
          const maxWeight = parseFloat(div.Maximum_Weight);

          if (gewicht > 0 && !isNaN(minWeight) && !isNaN(maxWeight)) {
            if (isKampf) {
              // Kampf: Eine Gewichtsklasse hher erlaubt
              const gewichtPasstExakt = gewicht >= minWeight && gewicht <= maxWeight;
              const gewichtPasstHoeher = gewicht < minWeight && gewicht >= (minWeight - 10); // ca. eine Gewichtsklasse (~10kg)
              if (!gewichtPasstExakt && !gewichtPasstHoeher) return false;
            } else {
              // Formen/Andere: Exakte Gewichtspassung (falls definiert)
              if (gewicht < minWeight || gewicht > maxWeight) return false;
            }
          }

          // Skill-Level Check
          if (div.Skill_Level && skillLevel) {
            // Fr Formen: eine Skill-Stufe hher erlaubt
            const erlaubteSkills = isFormen
              ? (skillLevelMappingFormen[skillLevel] || [])
              : (skillLevelMapping[skillLevel] || []);

            if (erlaubteSkills.length > 0 && !erlaubteSkills.includes(div.Skill_Level)) {
              return false;
            }
          }

          // Geschlechtscheck
          if (div.Gender && geschlecht) {
            const divGender = div.Gender.toLowerCase();
            if (divGender !== 'mixed' && divGender !== 'm/f' && divGender !== 'all' && divGender !== '') {
              const isMale = geschlecht === 'mnnlich' || geschlecht === 'm' || geschlecht === 'male';
              const isFemale = geschlecht === 'weiblich' || geschlecht === 'f' || geschlecht === 'female';
              const divIsMale = divGender === 'm' || divGender === 'male' || divGender === 'mnnlich';
              const divIsFemale = divGender === 'f' || divGender === 'female' || divGender === 'weiblich';

              if (divIsMale && isFemale) return false;
              if (divIsFemale && isMale) return false;
            }
          }

          return true;
        });

        console.log(` ${w.vorname} ${w.nachname}: ${passendeDivisionen.length} passende Divisionen`);
        if (passendeDivisionen.length > 0) {
          console.log(`   Beispiele:`, passendeDivisionen.slice(0, 5).map(d => `${d.Division_Code} (${d.Division_Type}, ${d.Skill_Level})`));
        }

        // Im Bearbeitungsmodus: Bereits angemeldete Kategorien vorauswhlen
        let vorausgewaehlteKategorien = [];
        if (editingWettkaempfer && editingWettkaempfer.wettkaempfer_id === wId && editingWettkaempfer.existingDivisions) {
          vorausgewaehlteKategorien = editingWettkaempfer.existingDivisions.filter(divCode =>
            passendeDivisionen.some(d => d.Division_Code === divCode)
          );
          console.log(` Bearbeitungsmodus: ${vorausgewaehlteKategorien.length} bereits angemeldete Kategorien vorausgewhlt`);
        }

        wettkampferDivisionenMap[wId] = {
          passend: passendeDivisionen,
          ausgewaehlt: vorausgewaehlteKategorien
        };
      });

      setWettkampferDivisionen(wettkampferDivisionenMap);

      // Ersten Wettkmpfer als aktiven Tab setzen
      if (selectedWettkampfer.length > 0) {
        setActiveWettkampferTab(selectedWettkampfer[0]);
      }

    } catch (err) {
      console.error(' Fehler beim Laden der Divisionen:', err);
      setError('Fehler beim Laden der Divisionen: ' + err.message);
    } finally {
      setLoading(false);
    }
  };


  // Division fr Wettkmpfer togglen
  const handleDivisionToggle = (wettkampferId, divisionCode) => {
    setWettkampferDivisionen(prev => {
      const current = prev[wettkampferId] || { passend: [], ausgewaehlt: [] };
      const ausgewaehlt = current.ausgewaehlt || [];

      if (ausgewaehlt.includes(divisionCode)) {
        return {
          ...prev,
          [wettkampferId]: {
            ...current,
            ausgewaehlt: ausgewaehlt.filter(code => code !== divisionCode)
          }
        };
      } else {
        return {
          ...prev,
          [wettkampferId]: {
            ...current,
            ausgewaehlt: [...ausgewaehlt, divisionCode]
          }
        };
      }
    });
  };

  // Alle Divisionen fr einen Wettkmpfer auswhlen/abwhlen
  const handleSelectAllDivisionen = (wettkampferId) => {
    setWettkampferDivisionen(prev => {
      const current = prev[wettkampferId] || { passend: [], ausgewaehlt: [] };
      const allCodes = current.passend.map(d => d.Division_Code);

      if (current.ausgewaehlt.length === allCodes.length) {
        return { ...prev, [wettkampferId]: { ...current, ausgewaehlt: [] } };
      } else {
        return { ...prev, [wettkampferId]: { ...current, ausgewaehlt: allCodes } };
      }
    });
  };

  // Weiter zu Schritt 3
  const handleWeiterZuDivisionen = async () => {
    if (selectedWettkampfer.length === 0) {
      setError('Bitte whlen Sie mindestens einen Wettkmpfer aus.');
      return;
    }
    setCurrentStep(3);
    await loadDivisionen();
  };

  // Prfen ob alle Wettkmpfer mindestens eine Division haben
  const alleHabenDivisionen = () => {
    return selectedWettkampfer.every(wId =>
      wettkampferDivisionen[wId] &&
      wettkampferDivisionen[wId].ausgewaehlt &&
      wettkampferDivisionen[wId].ausgewaehlt.length > 0
    );
  };

  // Favoriten toggle
  const toggleFavorit = (divisionCode) => {
    setFavoriten(prev => {
      const newFavoriten = prev.includes(divisionCode)
        ? prev.filter(c => c !== divisionCode)
        : [...prev, divisionCode];
      localStorage.setItem('divisionFavoriten', JSON.stringify(newFavoriten));
      return newFavoriten;
    });
  };

  // Sortier-Funktion fr Divisionen
  const sortDivisionen = (divs) => {
    const sorted = [...divs];
    switch (divisionSort) {
      case 'alter':
        return sorted.sort((a, b) => (parseInt(a.Age_from) || 0) - (parseInt(b.Age_from) || 0));
      case 'gewicht':
        return sorted.sort((a, b) => (parseFloat(a.Minimun_Weight) || 0) - (parseFloat(b.Minimun_Weight) || 0));
      case 'skill':
        const skillOrder = { 'Beginner': 1, 'Intermediate': 2, 'Amateur': 3, 'Advanced': 4, 'Black Belts': 5 };
        return sorted.sort((a, b) => (skillOrder[a.Skill_Level] || 99) - (skillOrder[b.Skill_Level] || 99));
      case 'favoriten':
        return sorted.sort((a, b) => {
          const aFav = favoriten.includes(a.Division_Code) ? 0 : 1;
          const bFav = favoriten.includes(b.Division_Code) ? 0 : 1;
          return aFav - bFav;
        });
      default: // code
        return sorted.sort((a, b) => (a.Division_Code || '').localeCompare(b.Division_Code || ''));
    }
  };

  // Such-Filter fr Divisionen
  const filterBySearch = (divs) => {
    if (!divisionSearch.trim()) return divs;
    const search = divisionSearch.toLowerCase();
    return divs.filter(d =>
      d.Division_Code?.toLowerCase().includes(search) ||
      d.Division_Name?.toLowerCase().includes(search)
    );
  };

  // Zusammenfassung aller Auswahlen
  const getZusammenfassung = () => {
    return selectedWettkampfer.map(wId => {
      const w = wettkampfer.find(wk => wk.wettkaempfer_id === wId);
      const divData = wettkampferDivisionen[wId] || { passend: [], ausgewaehlt: [] };
      const ausgewaehlte = divData.ausgewaehlt || [];
      const divisionDetails = ausgewaehlte.map(code =>
        alleDivisionen.find(d => d.Division_Code === code)
      ).filter(Boolean);

      return {
        wettkampfer: w,
        divisionen: divisionDetails
      };
    });
  };

  // Wettkmpfer bearbeiten - Modal ffnen
  const openEditModal = (w) => {
    setEditFormData({
      wettkaempfer_id: w.wettkaempfer_id,
      vorname: w.vorname || '',
      nachname: w.nachname || '',
      geburtsdatum: w.geburtsdatum ? w.geburtsdatum.split('T')[0] : '',
      geschlecht: w.geschlecht || '',
      gewicht: w.gewicht || '',
      groesse: w.groesse || '',
      gurtfarbe: w.gurtfarbe || '',
      skill_level: w.skill_level || '',
      kampfstil: w.kampfstil || ''
    });
    setEditingWettkampfer(w);
  };

  // Wettkmpfer speichern
  const saveWettkampferEdit = async () => {
    setSavingEdit(true);
    try {
      const token = getAuthToken();
      if (!token) {
        throw new Error('Kein Authentifizierungstoken gefunden');
      }

      const response = await fetch(`/api/wettkaempfer/${editFormData.wettkaempfer_id}`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(editFormData)
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || `HTTP ${response.status}`);
      }

      // Aktualisiere lokalen State
      setWettkampfer(prev => prev.map(w =>
        w.wettkaempfer_id === editFormData.wettkaempfer_id
          ? { ...w, ...editFormData }
          : w
      ));

      setEditingWettkampfer(null);
      setEditFormData({});
    } catch (err) {
      console.error(' Fehler beim Speichern:', err);
      setError('Fehler beim Speichern: ' + err.message);
    } finally {
      setSavingEdit(false);
    }
  };

  //  submitAnmeldung FUNKTION mit Divisionen
  const submitAnmeldung = async () => {
    if (selectedWettkampfer.length === 0) {
      setError('Bitte whlen Sie mindestens einen Wettkmpfer aus.');
      return;
    }

    // Prfe ob alle Wettkmpfer Divisionen haben
    if (!alleHabenDivisionen()) {
      setError('Bitte whlen Sie fr jeden Wettkmpfer mindestens eine Division aus.');
      return;
    }

    setSubmitting(true);
    try {
      const token = getAuthToken();

      if (!token) {
        throw new Error('Kein Authentifizierungstoken gefunden. Bitte loggen Sie sich erneut ein.');
      }

      // Erstelle Anmeldungen mit Divisionen
      const wettkampferMitDivisionen = selectedWettkampfer.map(wId => ({
        wettkaempfer_id: wId,
        divisionen: wettkampferDivisionen[wId]?.ausgewaehlt || []
      }));

      const anmeldungData = {
        turnier_id: turnier.turnier_id,
        verein_id: selectedVerein.vereins_id,
        wettkampfer_ids: selectedWettkampfer,
        wettkampfer_divisionen: wettkampferMitDivisionen,
        anmelde_datum: new Date().toISOString(),
        // Wenn editingWettkaempfer gesetzt ist, bestehende Anmeldungen aktualisieren
        is_update: !!editingWettkaempfer
      };

      console.log(' Sende Anmeldung mit Divisionen:', anmeldungData);

      const response = await fetch(`/api/anmeldungen`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(anmeldungData)
      });

      console.log(' Anmeldung Response Status:', response.status);

      if (!response.ok) {
        const errorData = await response.json();
        console.error(' Anmeldung Error:', errorData);
        throw new Error(errorData.message || `HTTP ${response.status}`);
      }

      const result = await response.json();
      console.log(' Anmeldung erfolgreich:', result);

      // Erfolg-Callback aufrufen
      if (onAnmeldungAbgeschlossen) {
        onAnmeldungAbgeschlossen({
          turnier,
          verein: selectedVerein,
          wettkampfer: selectedWettkampfer,
          divisionen: wettkampferDivisionen,
          anmeldung_id: result.data?.anmeldung_id
        });
      }

      onClose();
    } catch (err) {
      console.error(' Anmeldung fehlgeschlagen:', err);
      setError('Anmeldung fehlgeschlagen: ' + err.message);
    } finally {
      setSubmitting(false);
    }
  };

  const filteredWettkampfer = wettkampfer.filter(w =>
    w.vorname?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    w.nachname?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    w.gurtfarbe?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    w.kampfstil?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const calculateAge = (birthDate) => {
    const today = new Date();
    const birth = new Date(birthDate);
    let age = today.getFullYear() - birth.getFullYear();
    const monthDiff = today.getMonth() - birth.getMonth();
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birth.getDate())) {
      age--;
    }
    return age;
  };

  //  DEBUGGING-FUNKTIONEN fr Browser-Console
  React.useEffect(() => {
    //  CORS-Test-Funktion
    window.testCORSHeaders = async () => {
      console.log(' CORS-Header Test gestartet...');
      
      try {
        const token = getAuthToken();
        
        //  EINFACHSTER MGLICHER REQUEST
        const response = await fetch(`/api/wettkaempfer/test`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`
            // NUR Authorization-Header
          }
        });
        
        console.log(' CORS-Test erfolgreich, Status:', response.status);
        const data = await response.json();
        console.log(' Test-Data:', data);
        
        return true;
      } catch (error) {
        console.error(' CORS-Test fehlgeschlagen:', error);
        return false;
      }
    };

    //  API-Test ohne Auth
    window.testAPIWithoutAuth = async (vereinId) => {
      console.log(' Test API ohne Auth fr Verein:', vereinId);
      
      try {
        const response = await fetch(`/api/wettkaempfer/verein/${vereinId}/noauth`, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json'
          }
        });
        
        console.log(' NoAuth Test Status:', response.status);
        
        if (response.ok) {
          const data = await response.json();
          console.log(' NoAuth Test erfolgreich:', data);
          return data;
        } else {
          console.log(' NoAuth Test fehlgeschlagen:', response.status);
          return null;
        }
      } catch (error) {
        console.error(' NoAuth Test Error:', error);
        return null;
      }
    };

    // Debug-Funktionen global verfgbar machen
    window.debugModalAuth = () => {
      console.log(' Modal Auth-State Debugging:');
      console.log(' localStorage clubData:', localStorage.getItem('clubData'));
      console.log(' localStorage adminData:', localStorage.getItem('adminData'));
      console.log(' Extrahierter Token:', getAuthToken());
      console.log(' User-Data:', getUserData());
      console.log(' Current userRole:', userRole);
      console.log(' Current userVereinId:', userVereinId);
    };

    window.testWettkampferAPI = async (vereinId) => {
      console.log(' Testing Wettkmpfer API for Verein:', vereinId);
      try {
        const token = getAuthToken();
        const response = await fetch(`/api/wettkaempfer/verein/${vereinId}`, {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          }
        });
        console.log(' Status:', response.status);
        const data = await response.json();
        console.log(' Data:', data);
      } catch (error) {
        console.error(' Test Error:', error);
      }
    };

    console.log(' Modal Debug-Funktionen verfgbar:');
    console.log('   debugModalAuth() - Zeigt Auth-Status');
    console.log('   testWettkampferAPI(vereinId) - Testet API direkt');
    console.log('   testCORSHeaders() - Testet CORS mit minimalen Headers');
    console.log('   testAPIWithoutAuth(9) - Testet API ohne Authorization');
  }, []);

  return (
    <div className="modal-overlay" onClick={(e) => e.target === e.currentTarget && onClose()}>
      <div className={`modal-content ${currentStep === 3 ? 'fullscreen' : 'large'}`}>
        {/* Modal Header mit integriertem Progress */}
        <div className={`modal-header ${currentStep === 3 ? 'compact' : ''}`}>
          <div className="header-left">
            <div className="modal-title">
              <h2> Turnier-Anmeldung</h2>
              <p>{turnier.name} - {turnier.datum_formatted || new Date(turnier.datum).toLocaleDateString('de-DE')}</p>
            </div>
          </div>

          {/* Kompakter Progress Indicator im Header */}
          <div className="progress-indicator-compact">
            <div className={`step-compact ${currentStep >= 1 ? 'active' : ''} ${currentStep === 1 ? 'current' : ''}`}>
              <span className="step-dot">1</span>
              <span className="step-text">Verein</span>
            </div>
            <div className="step-line-compact"></div>
            <div className={`step-compact ${currentStep >= 2 ? 'active' : ''} ${currentStep === 2 ? 'current' : ''}`}>
              <span className="step-dot">2</span>
              <span className="step-text">Wettkmpfer</span>
            </div>
            <div className="step-line-compact"></div>
            <div className={`step-compact ${currentStep >= 3 ? 'active' : ''} ${currentStep === 3 ? 'current' : ''}`}>
              <span className="step-dot">3</span>
              <span className="step-text">Divisionen</span>
            </div>
          </div>

          <button className="modal-close" onClick={onClose}></button>
        </div>

        {/* Modal Body */}
        <div className="modal-body">
          {error && (
            <div className="error-message">
              <span> {error}</span>
              <button onClick={() => setError('')}></button>
            </div>
          )}

          {/* SCHRITT 1: Verein-Auswahl (nur Admin) */}
          {currentStep === 1 && userRole === 'admin' && (
            <div className="step-content">
              <h3>Verein fr Anmeldung auswhlen</h3>
              <p>Whlen Sie den Verein aus, fr den Sie Wettkmpfer anmelden mchten:</p>
              
              {loading ? (
                <div className="loading-spinner-container">
                  <div className="loading-spinner"></div>
                  <p>Vereine werden geladen...</p>
                </div>
              ) : (
                <div className="vereine-grid">
                  {vereine.map(verein => (
                    <div 
                      key={verein.vereins_id}
                      className="verein-card selectable"
                      onClick={() => handleVereinSelect(verein)}
                    >
                      <div className="verein-info">
                        <h4>{verein.name}</h4>
                        <p> {verein.ort}</p>
                        <p> {verein.ansprechpartner}</p>
                      </div>
                      <div className="select-indicator"></div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}

          {/* SCHRITT 1: Verein-Anzeige (fr normale Vereine) */}
          {currentStep === 2 && userRole !== 'admin' && selectedVerein && (
            <div className="current-verein-info">
              <h3>Angemeldet als:</h3>
              <div className="verein-card current">
                <div className="verein-info">
                  <h4>{selectedVerein.name}</h4>
                  <p> Vereins-Login aktiv</p>
                </div>
              </div>
            </div>
          )}

          {/* SCHRITT 2: Wettkmpfer-Auswahl */}
          {currentStep === 2 && selectedVerein && (
            <div className="step-content">
              <h3>Wettkmpfer von {selectedVerein.name} auswhlen</h3>
              
              {/* Such- und Filter-Bereich */}
              <div className="wettkampfer-controls">
                <div className="search-container">
                  <input
                    type="text"
                    placeholder=" Wettkmpfer durchsuchen..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="search-input"
                  />
                </div>
                <div className="selection-info">
                  <span>{selectedWettkampfer.length} von {filteredWettkampfer.length} ausgewhlt</span>
                  <button 
                    className="select-all-button"
                    onClick={handleSelectAll}
                  >
                    {selectedWettkampfer.length === filteredWettkampfer.length ? 'Alle abwhlen' : 'Alle auswhlen'}
                  </button>
                </div>
              </div>

              {/* Wettkmpfer-Liste */}
              {loading ? (
                <div className="loading-spinner-container">
                  <div className="loading-spinner"></div>
                  <p>Wettkmpfer werden geladen...</p>
                </div>
              ) : filteredWettkampfer.length === 0 ? (
                <div className="no-wettkampfer">
                  <p> Keine Wettkmpfer gefunden</p>
                  {searchTerm && (
                    <button onClick={() => setSearchTerm('')}>Suche zurcksetzen</button>
                  )}
                </div>
              ) : (
                <div className="wettkampfer-grid">
                  {filteredWettkampfer.map(w => (
                    <div 
                      key={w.wettkaempfer_id}
                      className={`wettkampfer-card ${selectedWettkampfer.includes(w.wettkaempfer_id) ? 'selected' : ''}`}
                      onClick={() => handleWettkampferToggle(w.wettkaempfer_id)}
                    >
                      <div className="wettkampfer-checkbox">
                        <input
                          type="checkbox"
                          checked={selectedWettkampfer.includes(w.wettkaempfer_id)}
                          onChange={() => handleWettkampferToggle(w.wettkaempfer_id)}
                        />
                      </div>
                      <div className="wettkampfer-info">
                        <h4>{w.vorname} {w.nachname}</h4>
                        <div className="wettkampfer-details">
                          <span className="detail-item"> {calculateAge(w.geburtsdatum)} Jahre</span>
                          <span className="detail-item"> {w.gewicht} kg</span>
                          <span className="detail-item"> {w.gurtfarbe}</span>
                          <span className="detail-item"> {w.skill_level}</span>
                          <span className="detail-item"> {w.kampfstil}</span>
                        </div>
                      </div>
                      <button
                        className="wettkampfer-edit-btn"
                        onClick={(e) => {
                          e.stopPropagation();
                          openEditModal(w);
                        }}
                        title="Wettkmpfer bearbeiten"
                      >
                        
                      </button>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}

          {/* Wettkmpfer-Bearbeitungs-Modal */}
          {editingWettkampfer && (
            <div className="edit-modal-overlay" onClick={() => setEditingWettkampfer(null)}>
              <div className="edit-modal" onClick={(e) => e.stopPropagation()}>
                <div className="edit-modal-header">
                  <h3>Wettkmpfer bearbeiten</h3>
                  <button className="close-edit" onClick={() => setEditingWettkampfer(null)}></button>
                </div>
                <div className="edit-modal-body">
                  <div className="edit-form-row">
                    <div className="edit-form-group">
                      <label>Vorname</label>
                      <input
                        type="text"
                        value={editFormData.vorname}
                        onChange={(e) => setEditFormData({...editFormData, vorname: e.target.value})}
                      />
                    </div>
                    <div className="edit-form-group">
                      <label>Nachname</label>
                      <input
                        type="text"
                        value={editFormData.nachname}
                        onChange={(e) => setEditFormData({...editFormData, nachname: e.target.value})}
                      />
                    </div>
                  </div>
                  <div className="edit-form-row">
                    <div className="edit-form-group">
                      <label>Geburtsdatum</label>
                      <input
                        type="date"
                        value={editFormData.geburtsdatum}
                        onChange={(e) => setEditFormData({...editFormData, geburtsdatum: e.target.value})}
                      />
                    </div>
                    <div className="edit-form-group">
                      <label>Geschlecht</label>
                      <select
                        value={editFormData.geschlecht}
                        onChange={(e) => setEditFormData({...editFormData, geschlecht: e.target.value})}
                      >
                        <option value="">Bitte whlen</option>
                        <option value="mnnlich">Mnnlich</option>
                        <option value="weiblich">Weiblich</option>
                      </select>
                    </div>
                  </div>
                  <div className="edit-form-row">
                    <div className="edit-form-group">
                      <label>Gewicht (kg)</label>
                      <input
                        type="number"
                        step="0.1"
                        value={editFormData.gewicht}
                        onChange={(e) => setEditFormData({...editFormData, gewicht: e.target.value})}
                      />
                    </div>
                    <div className="edit-form-group">
                      <label>Gre (cm)</label>
                      <input
                        type="number"
                        value={editFormData.groesse}
                        onChange={(e) => setEditFormData({...editFormData, groesse: e.target.value})}
                      />
                    </div>
                  </div>
                  <div className="edit-form-row">
                    <div className="edit-form-group">
                      <label>Gurtfarbe</label>
                      <select
                        value={editFormData.gurtfarbe}
                        onChange={(e) => setEditFormData({...editFormData, gurtfarbe: e.target.value})}
                      >
                        <option value="">Bitte whlen</option>
                        <option value="Wei">Wei</option>
                        <option value="Gelb">Gelb</option>
                        <option value="Orange">Orange</option>
                        <option value="Grn">Grn</option>
                        <option value="Blau">Blau</option>
                        <option value="Braun">Braun</option>
                        <option value="Schwarz">Schwarz</option>
                      </select>
                    </div>
                    <div className="edit-form-group">
                      <label>Skill-Level</label>
                      <select
                        value={editFormData.skill_level}
                        onChange={(e) => setEditFormData({...editFormData, skill_level: e.target.value})}
                      >
                        <option value="">Bitte whlen</option>
                        <option value="Beginner">Beginner</option>
                        <option value="Intermediate">Intermediate</option>
                        <option value="Advanced">Advanced</option>
                        <option value="Black Belt">Black Belt</option>
                      </select>
                    </div>
                  </div>
                  <div className="edit-form-row">
                    <div className="edit-form-group full-width">
                      <label>Kampfstil</label>
                      <input
                        type="text"
                        value={editFormData.kampfstil}
                        onChange={(e) => setEditFormData({...editFormData, kampfstil: e.target.value})}
                        placeholder="z.B. Karate, Taekwondo, Kickboxen"
                      />
                    </div>
                  </div>
                </div>
                <div className="edit-modal-footer">
                  <button className="btn-secondary" onClick={() => setEditingWettkampfer(null)}>
                    Abbrechen
                  </button>
                  <button
                    className="btn-primary"
                    onClick={saveWettkampferEdit}
                    disabled={savingEdit}
                  >
                    {savingEdit ? ' Speichern...' : ' Speichern'}
                  </button>
                </div>
              </div>
            </div>
          )}

          {/* SCHRITT 3: Divisionen-Auswahl - Vollbild mit Tabs */}
          {currentStep === 3 && (
            <div className="step-content fullscreen-step">
              {loading ? (
                <div className="loading-spinner-container">
                  <div className="loading-spinner"></div>
                  <p>Divisionen werden geladen und gefiltert...</p>
                </div>
              ) : (
                <div className="sidebar-layout">
                  {/* Wettkmpfer Sidebar Links */}
                  <div className="wettkampfer-sidebar">
                    <div className="sidebar-header">Wettkmpfer</div>
                    {selectedWettkampfer.map(wId => {
                      const w = wettkampfer.find(wk => wk.wettkaempfer_id === wId);
                      if (!w) return null;
                      const divData = wettkampferDivisionen[wId] || { passend: [], ausgewaehlt: [] };
                      const isActive = activeWettkampferTab === wId;
                      const hasSelection = divData.ausgewaehlt?.length > 0;

                      return (
                        <button
                          key={wId}
                          className={`sidebar-item ${isActive ? 'active' : ''} ${hasSelection ? 'has-selection' : ''}`}
                          onClick={() => { setActiveWettkampferTab(wId); setCategoryFilters({}); setActiveMainCategory('alle'); }}
                        >
                          <div className="sidebar-name">{w.vorname} {w.nachname}</div>
                          <div className="sidebar-details">
                            <span>{calculateAge(w.geburtsdatum)}J</span>
                            <span>{w.gewicht}kg</span>
                            <span>{w.geschlecht === 'mnnlich' ? '' : w.geschlecht === 'weiblich' ? '' : ''}</span>
                          </div>
                          <div className="sidebar-details">
                            <span>{w.gurtfarbe}</span>
                            <span>{w.skill_level}</span>
                          </div>
                          {hasSelection ? (
                            <span className="sidebar-badge">{divData.ausgewaehlt.length}</span>
                          ) : divData.passend?.length > 0 ? (
                            <span className="sidebar-warning">!</span>
                          ) : null}
                        </button>
                      );
                    })}
                  </div>

                  {/* Divisionen-Bereich Rechts */}
                  <div className="division-main-area">
                  {activeWettkampferTab && (() => {
                    const w = wettkampfer.find(wk => wk.wettkaempfer_id === activeWettkampferTab);
                    if (!w) return null;
                    const divData = wettkampferDivisionen[activeWettkampferTab] || { passend: [], ausgewaehlt: [] };
                    const passendeDivisionen = divData.passend || [];
                    const ausgewaehlte = divData.ausgewaehlt || [];

                    // Gruppiere nach Division_Type
                    const divisionenByType = passendeDivisionen.reduce((acc, div) => {
                      const type = div.Division_Type || 'Sonstige';
                      if (!acc[type]) acc[type] = [];
                      acc[type].push(div);
                      return acc;
                    }, {});

                    return (
                      <div className="division-content">
                        {/* Kompakte Aktionsleiste */}
                        <div className="compact-action-bar">
                          <span className="division-stats">{ausgewaehlte.length}/{passendeDivisionen.length}</span>
                          <button className="btn-small" onClick={() => setShowSummary(true)}> bersicht</button>
                          <button className="btn-small" onClick={() => handleSelectAllDivisionen(activeWettkampferTab)}>
                            {ausgewaehlte.length === passendeDivisionen.length ? 'Keine' : 'Alle'}
                          </button>
                        </div>

                        {/* Haupt-Kategorie-Tabs */}
                        {(() => {
                          // Sammle alle verfgbaren Hauptkategorien
                          const availableCategories = Object.keys(divisionenByType);

                          // Mapping von Tab-ID zu mglichen Division_Type Namen
                          const categoryMatches = {
                            formen: ['formen', 'forms', 'kata'],
                            kumite: ['kumite', 'sparring'],
                            kickboxen: ['kickboxen', 'kickboxing', 'pointfighting', 'point fighting', 'continuous', 'continous', 'fullcontact', 'full contact', 'k1', 'k-1'],
                            grappling: ['grappling', 'wrestling', 'bjj'],
                            rumble: ['rumble'],
                            selbstverteidigung: ['selbstverteidigung', 'self defense', 'self-defense', 'sv'],
                            bruchtest: ['bruchtest', 'breaking', 'break']
                          };

                          // Prfe ob eine Kategorie verfgbar ist
                          const isCategoryAvailable = (catId) => {
                            const matches = categoryMatches[catId] || [catId];
                            return availableCategories.some(ac =>
                              matches.some(m => ac.toLowerCase().includes(m))
                            );
                          };

                          // Finde passende Division_Type Namen fr eine Kategorie
                          const getMatchingTypes = (catId) => {
                            const matches = categoryMatches[catId] || [catId];
                            return availableCategories.filter(ac =>
                              matches.some(m => ac.toLowerCase().includes(m))
                            );
                          };

                          const mainCategories = [
                            { id: 'alle', label: 'Alle', icon: '' },
                            { id: 'formen', label: 'Formen', icon: '' },
                            { id: 'kumite', label: 'Kumite', icon: '' },
                            { id: 'kickboxen', label: 'Kickboxen', icon: '' },
                            { id: 'grappling', label: 'Grappling', icon: '' },
                            { id: 'selbstverteidigung', label: 'Selbstvert.', icon: '' },
                            { id: 'bruchtest', label: 'Bruchtest', icon: '' },
                            { id: 'rumble', label: 'Rumble', icon: '' }
                          ].filter(cat => cat.id === 'alle' || isCategoryAvailable(cat.id));

                          return (
                            <div className="main-category-tabs">
                              {mainCategories.map(cat => {
                                const matchingTypes = getMatchingTypes(cat.id);
                                const count = cat.id === 'alle'
                                  ? passendeDivisionen.length
                                  : matchingTypes.reduce((sum, type) => sum + (divisionenByType[type] || []).length, 0);
                                return (
                                  <button
                                    key={cat.id}
                                    className={`main-category-tab ${activeMainCategory === cat.id ? 'active' : ''}`}
                                    onClick={() => setActiveMainCategory(cat.id)}
                                  >
                                    <span className="cat-icon">{cat.icon}</span>
                                    <span className="cat-label">{cat.label}</span>
                                    <span className="cat-count">{count}</span>
                                  </button>
                                );
                              })}
                            </div>
                          );
                        })()}

                        {/* Such- und Sortier-Bereich */}
                        <div className="division-toolbar">
                          <div className="division-search">
                            <input
                              type="text"
                              placeholder=" Division suchen..."
                              value={divisionSearch}
                              onChange={(e) => setDivisionSearch(e.target.value)}
                              className="division-search-input"
                            />
                            {divisionSearch && (
                              <button className="clear-search" onClick={() => setDivisionSearch('')}></button>
                            )}
                          </div>
                          <div className="division-sort">
                            <label>Sortieren:</label>
                            <select
                              value={divisionSort}
                              onChange={(e) => setDivisionSort(e.target.value)}
                              className="sort-select"
                            >
                              <option value="code">Code (A-Z)</option>
                              <option value="alter">Alter</option>
                              <option value="gewicht">Gewicht</option>
                              <option value="skill">Skill-Level</option>
                              <option value="favoriten">Favoriten zuerst</option>
                            </select>
                          </div>
                        </div>

                        {passendeDivisionen.length === 0 ? (
                          <div className="no-divisions-warning">
                             Keine passenden Divisionen fr diesen Wettkmpfer gefunden
                          </div>
                        ) : (
                          <div className="divisions-container">
                            {Object.entries(divisionenByType)
                              .filter(([type]) => {
                                if (activeMainCategory === 'alle') return true;
                                // Mapping von Tab-ID zu mglichen Division_Type Namen
                                const categoryMatches = {
                                  formen: ['formen', 'forms', 'kata'],
                                  kumite: ['kumite', 'sparring'],
                                  kickboxen: ['kickboxen', 'kickboxing', 'pointfighting', 'point fighting', 'continuous', 'continous', 'fullcontact', 'full contact', 'k1', 'k-1'],
                                  grappling: ['grappling', 'wrestling', 'bjj'],
                                  rumble: ['rumble'],
                                  selbstverteidigung: ['selbstverteidigung', 'self defense', 'self-defense', 'sv'],
                                  bruchtest: ['bruchtest', 'breaking', 'break']
                                };
                                const matches = categoryMatches[activeMainCategory] || [activeMainCategory];
                                return matches.some(m => type.toLowerCase().includes(m));
                              })
                              .map(([type, divs]) => {
                              const typeLower = type.toLowerCase();

                              // Unterkategorien pro Typ definieren
                              const subKategorien = {
                                formen: [
                                  { id: 'alle', label: 'Alle', filter: () => true },
                                  { id: 'japanese', label: 'Japanese', filter: (d) => d.Division_Name?.toLowerCase().includes('japanese') },
                                  { id: 'korean', label: 'Korean', filter: (d) => d.Division_Name?.toLowerCase().includes('korean') },
                                  { id: 'chinese', label: 'Chinese/Kenpo', filter: (d) => d.Division_Name?.toLowerCase().includes('chinese') || d.Division_Name?.toLowerCase().includes('kenpo') || d.Division_Name?.toLowerCase().includes('kempo') },
                                  { id: 'weapons', label: 'Weapons', filter: (d) => d.Division_Name?.toLowerCase().includes('weapon') },
                                  { id: 'open', label: 'Open/Creative', filter: (d) => d.Division_Name?.toLowerCase().includes('open') || d.Division_Name?.toLowerCase().includes('creative') || d.Division_Name?.toLowerCase().includes('music') },
                                  { id: 'team', label: 'Team/Duo', filter: (d) => d.Division_Name?.toLowerCase().includes('team') || d.Division_Name?.toLowerCase().includes('duo') }
                                ],
                                kickboxen: [
                                  { id: 'alle', label: 'Alle', filter: () => true },
                                  { id: 'pointfighting', label: 'Pointfighting', filter: (d) => d.Division_Name?.toLowerCase().includes('point') },
                                  { id: 'continuous', label: 'Continuous', filter: (d) => d.Division_Name?.toLowerCase().includes('continu') },
                                  { id: 'fullcontact', label: 'Fullcontact', filter: (d) => d.Division_Name?.toLowerCase().includes('fullcontact') || d.Division_Name?.toLowerCase().includes('full contact') },
                                  { id: 'k1', label: 'K1', filter: (d) => d.Division_Name?.toLowerCase().includes('k1') || d.Division_Name?.toLowerCase().includes('k-1') }
                                ],
                                grappling: [
                                  { id: 'alle', label: 'Alle', filter: () => true },
                                  { id: 'nogi', label: 'No-Gi', filter: (d) => d.Division_Name?.toLowerCase().includes('no-gi') || d.Division_Name?.toLowerCase().includes('nogi') },
                                  { id: 'gi', label: 'Gi', filter: (d) => d.Division_Name?.toLowerCase().includes('gi') && !d.Division_Name?.toLowerCase().includes('no-gi') && !d.Division_Name?.toLowerCase().includes('nogi') }
                                ],
                                kumite: [
                                  { id: 'alle', label: 'Alle', filter: () => true },
                                  { id: 'light', label: 'Light Contact', filter: (d) => d.Division_Name?.toLowerCase().includes('light') },
                                  { id: 'semi', label: 'Semi Contact', filter: (d) => d.Division_Name?.toLowerCase().includes('semi') },
                                  { id: 'full', label: 'Full Contact', filter: (d) => d.Division_Name?.toLowerCase().includes('full') }
                                ],
                                rumble: [
                                  { id: 'alle', label: 'Alle', filter: () => true }
                                ]
                              };

                              // Finde passende Unterkategorien fr diesen Typ
                              const kategorien = subKategorien[typeLower] || null;
                              const hasSubKategorien = kategorien && kategorien.length > 1;
                              const activeFilter = categoryFilters[typeLower] || 'alle';

                              // Filtere nach Unterkategorie
                              let filteredDivs = hasSubKategorien
                                ? divs.filter(kategorien.find(k => k.id === activeFilter)?.filter || (() => true))
                                : divs;

                              // Suche anwenden
                              filteredDivs = filterBySearch(filteredDivs);
                              // Sortierung anwenden
                              filteredDivs = sortDivisionen(filteredDivs);

                              // Zhle Divisionen pro Kategorie
                              const kategorieCounts = {};
                              if (hasSubKategorien) {
                                kategorien.forEach(k => {
                                  kategorieCounts[k.id] = divs.filter(k.filter).length;
                                });
                              }

                              // Zeige Sektion nur wenn Divisionen vorhanden (ohne Suchfilter)
                              if (divs.length === 0) return null;

                              return (
                                <div key={type} className="division-type-section">
                                  <div className="type-header">
                                    <h4>{type}</h4>
                                    <span className="type-count">
                                      {filteredDivs.length}{divisionSearch && filteredDivs.length !== divs.length ? ` / ${divs.length}` : ''} Divisionen
                                    </span>
                                  </div>

                                  {/* Filter Tabs fr Unterkategorien */}
                                  {hasSubKategorien && (
                                    <div className="formen-filter-tabs">
                                      {kategorien.map(kat => (
                                        <button
                                          key={kat.id}
                                          className={`formen-filter-tab ${activeFilter === kat.id ? 'active' : ''}`}
                                          onClick={(e) => {
                                            e.stopPropagation();
                                            setCategoryFilters(prev => ({ ...prev, [typeLower]: kat.id }));
                                          }}
                                        >
                                          {kat.label}
                                          <span className="filter-count">{kategorieCounts[kat.id]}</span>
                                        </button>
                                      ))}
                                    </div>
                                  )}

                                  {/* Meldung wenn Suche keine Ergebnisse */}
                                  {filteredDivs.length === 0 && divisionSearch && (
                                    <div className="no-search-results">
                                      Keine Divisionen gefunden fr "{divisionSearch}"
                                    </div>
                                  )}

                                  <div className="divisions-grid">
                                    {filteredDivs.map(div => {
                                      const isSelected = ausgewaehlte.includes(div.Division_Code);
                                      const isFavorit = favoriten.includes(div.Division_Code);
                                      return (
                                        <div
                                          key={div.Division_Code}
                                          className={`division-card ${isSelected ? 'selected' : ''} ${isFavorit ? 'favorit' : ''}`}
                                          onClick={() => handleDivisionToggle(activeWettkampferTab, div.Division_Code)}
                                        >
                                          <div className="division-checkbox">
                                            {isSelected ? '' : ''}
                                          </div>
                                          <button
                                            className={`favorit-btn ${isFavorit ? 'active' : ''}`}
                                            onClick={(e) => {
                                              e.stopPropagation();
                                              toggleFavorit(div.Division_Code);
                                            }}
                                            title={isFavorit ? 'Aus Favoriten entfernen' : 'Zu Favoriten hinzufgen'}
                                          >
                                            {isFavorit ? '' : ''}
                                          </button>
                                          <div className="division-info">
                                            <div className="division-code">{div.Division_Code}</div>
                                            <div className="division-name">{div.Division_Name}</div>
                                            <div className="division-details">
                                              <span> {div.Age_from || '?'}-{div.Age_to || '?'} J</span>
                                              {(div.Minimun_Weight || div.Maximum_Weight) && (
                                                <span> {div.Minimun_Weight || '?'}-{div.Maximum_Weight || '?'} kg</span>
                                              )}
                                              {div.Skill_Level && <span> {div.Skill_Level}</span>}
                                              {div.Gender && <span> {div.Gender}</span>}
                                            </div>
                                          </div>
                                        </div>
                                      );
                                    })}
                                  </div>

                                  {hasSubKategorien && filteredDivs.length === 0 && !divisionSearch && (
                                    <div className="no-filter-results">
                                      Keine Divisionen in dieser Kategorie
                                    </div>
                                  )}
                                </div>
                              );
                            })}
                          </div>
                        )}

                        {ausgewaehlte.length === 0 && passendeDivisionen.length > 0 && (
                          <div className="selection-warning">
                             Bitte mindestens eine Division fr diesen Wettkmpfer auswhlen
                          </div>
                        )}
                      </div>
                    );
                  })()}
                  </div>
                </div>
              )}
            </div>
          )}
        </div>

        {/* Zusammenfassungs-Modal */}
        {showSummary && (
          <div className="summary-overlay" onClick={() => setShowSummary(false)}>
            <div className="summary-modal" onClick={(e) => e.stopPropagation()}>
              <div className="summary-header">
                <h3>Anmeldungs-bersicht</h3>
                <button className="close-summary" onClick={() => setShowSummary(false)}></button>
              </div>
              <div className="summary-content">
                {getZusammenfassung().map(({ wettkampfer: w, divisionen }) => (
                  <div key={w?.wettkaempfer_id} className="summary-wettkampfer">
                    <div className="summary-wettkampfer-header">
                      <h4>{w?.vorname} {w?.nachname}</h4>
                      <span className="summary-badge">
                        {divisionen.length} Division{divisionen.length !== 1 ? 'en' : ''}
                      </span>
                    </div>
                    {divisionen.length === 0 ? (
                      <p className="summary-warning">Keine Divisionen ausgewhlt</p>
                    ) : (
                      <ul className="summary-divisions">
                        {divisionen.map(div => (
                          <li key={div.Division_Code}>
                            <span className="summary-code">{div.Division_Code}</span>
                            <span className="summary-name">{div.Division_Name}</span>
                          </li>
                        ))}
                      </ul>
                    )}
                  </div>
                ))}
              </div>
              <div className="summary-footer">
                <div className="summary-total">
                  Gesamt: {selectedWettkampfer.length} Wettkmpfer, {' '}
                  {getZusammenfassung().reduce((sum, item) => sum + item.divisionen.length, 0)} Divisions-Anmeldungen
                </div>
                <button className="btn-primary" onClick={() => setShowSummary(false)}>
                  Schlieen
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Modal Footer */}
        <div className="modal-footer">
          <button className="btn-secondary" onClick={onClose}>
            Abbrechen
          </button>

          {currentStep === 1 && userRole === 'admin' && (
            <button
              className="btn-primary"
              disabled={!selectedVerein}
            >
              Verein auswhlen 
            </button>
          )}

          {currentStep === 2 && (
            <>
              {userRole === 'admin' && (
                <button
                  className="btn-secondary"
                  onClick={() => setCurrentStep(1)}
                >
                   Zurck
                </button>
              )}
              <button
                className="btn-primary"
                onClick={handleWeiterZuDivisionen}
                disabled={selectedWettkampfer.length === 0}
              >
                Weiter zu Divisionen 
              </button>
            </>
          )}

          {currentStep === 3 && (
            <>
              <button
                className="btn-secondary"
                onClick={() => setCurrentStep(2)}
              >
                 Zurck
              </button>
              <button
                className="btn-primary"
                onClick={submitAnmeldung}
                disabled={!alleHabenDivisionen() || submitting}
              >
                {submitting ? (
                  <> Anmeldung luft...</>
                ) : (
                  <> {selectedWettkampfer.length} Wettkmpfer anmelden</>
                )}
              </button>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default WettkampferAuswahlModal;